import React, { useState, useEffect, useCallback, useRef } from 'react';
import { searchStocks, getStockBySymbol, getPopularStocks } from '../data/stockMasterDB';
import kisAPI from '../services/kisAPI';
import freeUSStockAPI from '../services/freeUSStockAPI';

const EnhancedStockSearch = ({ 
  darkMode = false, 
  onStockSelect,
  onWatchlistAdd,
  selectedStock 
}) => {
  const [searchTerm, setSearchTerm] = useState('');
  const [searchResults, setSearchResults] = useState([]);
  const [recentSearches, setRecentSearches] = useState([]);
  const [popularStocks, setPopularStocks] = useState([]);
  const [showDropdown, setShowDropdown] = useState(false);
  const [loading, setLoading] = useState(false);
  const [selectedCountry, setSelectedCountry] = useState('ALL');
  const [selectedSector, setSelectedSector] = useState('');
  
  const searchInputRef = useRef(null);
  const debounceTimer = useRef(null);

  const theme = {
    bg: darkMode ? '#1a1a1a' : '#ffffff',
    cardBg: darkMode ? '#2d2d2d' : '#f8f9fa',
    text: darkMode ? '#ffffff' : '#333333',
    subtext: darkMode ? '#cccccc' : '#666666',
    border: darkMode ? '#404040' : '#e0e0e0',
    accent: '#007bff',
    positive: '#00c851',
    negative: '#ff4444',
    warning: '#ffbb33'
  };

  //   
  useEffect(() => {
    //   
    const popular = getPopularStocks('ALL', 8);
    setPopularStocks(popular);

    //    
    const recent = JSON.parse(localStorage.getItem('recent_stock_searches') || '[]');
    setRecentSearches(recent);
  }, []);

  //  
  const debouncedSearch = useCallback((query, country, sector) => {
    if (debounceTimer.current) {
      clearTimeout(debounceTimer.current);
    }

    debounceTimer.current = setTimeout(() => {
      if (query.length < 1) {
        setSearchResults([]);
        return;
      }

      setLoading(true);
      
      try {
        const results = searchStocks(query, {
          country: country,
          sector: sector || null,
          limit: 10,
          includeScore: true
        });
        
        console.log(` : "${query}"  ${results.length} `);
        setSearchResults(results);
      } catch (error) {
        console.error(' :', error);
        setSearchResults([]);
      } finally {
        setLoading(false);
      }
    }, 300);
  }, []);

  //   
  const handleSearchChange = (value) => {
    setSearchTerm(value);
    setShowDropdown(true);
    debouncedSearch(value, selectedCountry, selectedSector);
  };

  //   
  const handleStockSelect = async (stock) => {
    try {
      console.log(`  : ${stock.symbol} (${stock.name})`);
      
      //   
      const updatedRecent = [
        stock,
        ...recentSearches.filter(item => item.symbol !== stock.symbol)
      ].slice(0, 10);
      
      setRecentSearches(updatedRecent);
      localStorage.setItem('recent_stock_searches', JSON.stringify(updatedRecent));

      //   
      let realTimeData = null;
      try {
        if (stock.country === 'KOR') {
          realTimeData = await kisAPI.getCurrentPrice(stock.symbol, 'domestic');
        } else {
          realTimeData = await freeUSStockAPI.getUSStockData(stock.symbol);
        }
        
        console.log(`    :`, realTimeData);
      } catch (error) {
        console.warn('   ,   :', error);
      }

      //   
      const combinedData = {
        ...stock,
        ...realTimeData,
        basicInfo: stock, //   
        hasRealTimeData: !!realTimeData
      };

      //  
      if (onStockSelect) {
        onStockSelect(combinedData);
      }

      // UI 
      setSearchTerm(stock.symbol + ' - ' + stock.name);
      setShowDropdown(false);
      
    } catch (error) {
      console.error('   :', error);
    }
  };

  //  
  const handleAddToWatchlist = (stock, event) => {
    event.stopPropagation();
    if (onWatchlistAdd) {
      onWatchlistAdd(stock);
    }
    console.log(`  : ${stock.symbol}`);
  };

  //   
  const handleCountryChange = (country) => {
    setSelectedCountry(country);
    if (searchTerm) {
      debouncedSearch(searchTerm, country, selectedSector);
    }
  };

  //   
  const handleSectorChange = (sector) => {
    setSelectedSector(sector);
    if (searchTerm) {
      debouncedSearch(searchTerm, selectedCountry, sector);
    }
  };

  //  
  const sectors = [
    { value: '', label: ' ' },
    { value: 'Technology', label: '' },
    { value: 'Healthcare', label: '' },
    { value: 'Financial Services', label: '' },
    { value: 'Consumer Discretionary', label: '' },
    { value: 'Consumer Staples', label: '' },
    { value: 'Aerospace & Defense', label: '/' },
    { value: '', label: '' },
    { value: '', label: '' },
    { value: '', label: '' },
    { value: '', label: '' }
  ];

  return (
    <div style={{
      background: theme.bg,
      borderRadius: '8px',
      border: `1px solid ${theme.border}`,
      marginBottom: '20px',
      position: 'relative'
    }}>
      {/*  */}
      <div style={{
        background: theme.cardBg,
        padding: '15px 20px',
        borderBottom: `1px solid ${theme.border}`,
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center'
      }}>
        <div style={{ display: 'flex', alignItems: 'center', gap: '10px' }}>
          <h3 style={{ margin: 0, color: theme.text, fontSize: '18px' }}>
               
          </h3>
          <span style={{
            background: theme.positive,
            color: 'white',
            padding: '2px 8px',
            borderRadius: '12px',
            fontSize: '12px'
          }}>
            DB 
          </span>
        </div>
      </div>

      {/*   */}
      <div style={{
        padding: '15px 20px',
        borderBottom: `1px solid ${theme.border}`,
        display: 'flex',
        gap: '10px',
        flexWrap: 'wrap',
        alignItems: 'center'
      }}>
        {/*   */}
        <select
          value={selectedCountry}
          onChange={(e) => handleCountryChange(e.target.value)}
          style={{
            background: theme.bg,
            border: `1px solid ${theme.border}`,
            color: theme.text,
            padding: '6px 10px',
            borderRadius: '6px',
            fontSize: '12px'
          }}
        >
          <option value="ALL"> </option>
          <option value="KOR"> </option>
          <option value="USA"> </option>
        </select>

        {/*   */}
        <select
          value={selectedSector}
          onChange={(e) => handleSectorChange(e.target.value)}
          style={{
            background: theme.bg,
            border: `1px solid ${theme.border}`,
            color: theme.text,
            padding: '6px 10px',
            borderRadius: '6px',
            fontSize: '12px'
          }}
        >
          {sectors.map(sector => (
            <option key={sector.value} value={sector.value}>
              {sector.label}
            </option>
          ))}
        </select>

        <div style={{ color: theme.subtext, fontSize: '12px', marginLeft: 'auto' }}>
           :  {Object.keys(getPopularStocks('KOR', 1000)).length}, 
           {Object.keys(getPopularStocks('USA', 1000)).length}
        </div>
      </div>

      {/*   */}
      <div style={{ padding: '15px 20px', position: 'relative' }}>
        <input
          ref={searchInputRef}
          type="text"
          placeholder=", ,   (: , UAVS, , AAPL)"
          value={searchTerm}
          onChange={(e) => handleSearchChange(e.target.value)}
          onFocus={() => setShowDropdown(true)}
          onBlur={() => setTimeout(() => setShowDropdown(false), 200)}
          style={{
            width: '100%',
            padding: '12px 15px',
            border: `2px solid ${theme.border}`,
            borderRadius: '8px',
            background: theme.bg,
            color: theme.text,
            fontSize: '14px',
            outline: 'none',
            transition: 'border-color 0.2s'
          }}
          onFocus={(e) => e.target.style.borderColor = theme.accent}
          onBlur={(e) => e.target.style.borderColor = theme.border}
        />

        {loading && (
          <div style={{
            position: 'absolute',
            right: '30px',
            top: '50%',
            transform: 'translateY(-50%)',
            color: theme.accent
          }}>
            
          </div>
        )}

        {/*    */}
        {showDropdown && (
          <div style={{
            position: 'absolute',
            top: '100%',
            left: '20px',
            right: '20px',
            background: theme.bg,
            border: `1px solid ${theme.border}`,
            borderRadius: '8px',
            boxShadow: darkMode ? '0 4px 20px rgba(0,0,0,0.5)' : '0 4px 20px rgba(0,0,0,0.1)',
            maxHeight: '400px',
            overflowY: 'auto',
            zIndex: 1000
          }}>
            {/*   */}
            {searchTerm && searchResults.length > 0 && (
              <div>
                <div style={{
                  padding: '10px 15px',
                  borderBottom: `1px solid ${theme.border}`,
                  color: theme.subtext,
                  fontSize: '12px',
                  fontWeight: '600'
                }}>
                     ({searchResults.length})
                </div>
                {searchResults.map((stock, index) => (
                  <div
                    key={stock.symbol}
                    onClick={() => handleStockSelect(stock)}
                    style={{
                      padding: '12px 15px',
                      borderBottom: index === searchResults.length - 1 ? 'none' : `1px solid ${theme.border}`,
                      cursor: 'pointer',
                      display: 'flex',
                      justifyContent: 'space-between',
                      alignItems: 'center',
                      transition: 'background-color 0.2s'
                    }}
                    onMouseEnter={(e) => e.target.style.background = theme.cardBg}
                    onMouseLeave={(e) => e.target.style.background = 'transparent'}
                  >
                    <div style={{ flex: 1 }}>
                      <div style={{
                        display: 'flex',
                        alignItems: 'center',
                        gap: '8px',
                        marginBottom: '4px'
                      }}>
                        <span style={{
                          color: stock.symbol === 'UAVS' ? theme.warning : theme.accent,
                          fontWeight: '600',
                          fontSize: '14px'
                        }}>
                          {stock.symbol}
                          {stock.symbol === 'UAVS' && ' '}
                        </span>
                        <span style={{
                          background: stock.country === 'KOR' ? theme.positive : theme.accent,
                          color: 'white',
                          padding: '1px 6px',
                          borderRadius: '8px',
                          fontSize: '10px'
                        }}>
                          {stock.country === 'KOR' ? '' : ''}
                        </span>
                        <span style={{
                          background: theme.border,
                          color: theme.subtext,
                          padding: '1px 6px',
                          borderRadius: '8px',
                          fontSize: '10px'
                        }}>
                          {stock.sector}
                        </span>
                      </div>
                      
                      <div style={{ color: theme.text, fontSize: '13px', marginBottom: '2px' }}>
                        {stock.name}
                        {stock.nameKor && stock.country === 'USA' && (
                          <span style={{ color: theme.subtext }}> ({stock.nameKor})</span>
                        )}
                      </div>
                      
                      <div style={{ color: theme.subtext, fontSize: '11px' }}>
                        {stock.market}  {stock.industry}
                        {stock.score && (
                          <span style={{ color: theme.warning }}>   {stock.score}</span>
                        )}
                      </div>
                    </div>
                    
                    <button
                      onClick={(e) => handleAddToWatchlist(stock, e)}
                      style={{
                        background: theme.positive,
                        color: 'white',
                        border: 'none',
                        padding: '4px 8px',
                        borderRadius: '4px',
                        cursor: 'pointer',
                        fontSize: '11px'
                      }}
                      title=" "
                    >
                      
                    </button>
                  </div>
                ))}
              </div>
            )}

            {/*    */}
            {searchTerm && searchResults.length === 0 && !loading && (
              <div style={{
                padding: '20px 15px',
                textAlign: 'center',
                color: theme.subtext,
                fontSize: '14px'
              }}>
                "{searchTerm}"    .
                <br />
                <small>, ,   .</small>
              </div>
            )}

            {/*   &   */}
            {!searchTerm && (
              <div>
                {/*   */}
                {recentSearches.length > 0 && (
                  <div>
                    <div style={{
                      padding: '10px 15px',
                      borderBottom: `1px solid ${theme.border}`,
                      color: theme.subtext,
                      fontSize: '12px',
                      fontWeight: '600'
                    }}>
                        
                    </div>
                    {recentSearches.slice(0, 5).map((stock, index) => (
                      <div
                        key={stock.symbol}
                        onClick={() => handleStockSelect(stock)}
                        style={{
                          padding: '10px 15px',
                          borderBottom: `1px solid ${theme.border}`,
                          cursor: 'pointer',
                          display: 'flex',
                          justifyContent: 'space-between',
                          alignItems: 'center'
                        }}
                        onMouseEnter={(e) => e.target.style.background = theme.cardBg}
                        onMouseLeave={(e) => e.target.style.background = 'transparent'}
                      >
                        <div>
                          <span style={{ color: theme.accent, fontWeight: '600', fontSize: '13px' }}>
                            {stock.symbol}
                          </span>
                          <span style={{ color: theme.text, marginLeft: '8px', fontSize: '12px' }}>
                            {stock.name}
                          </span>
                        </div>
                        <span style={{
                          background: stock.country === 'KOR' ? theme.positive : theme.accent,
                          color: 'white',
                          padding: '1px 6px',
                          borderRadius: '8px',
                          fontSize: '10px'
                        }}>
                          {stock.country === 'KOR' ? '' : ''}
                        </span>
                      </div>
                    ))}
                  </div>
                )}

                {/*   */}
                <div>
                  <div style={{
                    padding: '10px 15px',
                    borderBottom: `1px solid ${theme.border}`,
                    color: theme.subtext,
                    fontSize: '12px',
                    fontWeight: '600'
                  }}>
                       ( )
                  </div>
                  {popularStocks.slice(0, 6).map((stock, index) => (
                    <div
                      key={stock.symbol}
                      onClick={() => handleStockSelect(stock)}
                      style={{
                        padding: '10px 15px',
                        borderBottom: index === 5 ? 'none' : `1px solid ${theme.border}`,
                        cursor: 'pointer',
                        display: 'flex',
                        justifyContent: 'space-between',
                        alignItems: 'center'
                      }}
                      onMouseEnter={(e) => e.target.style.background = theme.cardBg}
                      onMouseLeave={(e) => e.target.style.background = 'transparent'}
                    >
                      <div>
                        <span style={{ color: theme.accent, fontWeight: '600', fontSize: '13px' }}>
                          {stock.symbol}
                          {stock.symbol === 'UAVS' && ' '}
                        </span>
                        <span style={{ color: theme.text, marginLeft: '8px', fontSize: '12px' }}>
                          {stock.name}
                        </span>
                      </div>
                      <div style={{ display: 'flex', alignItems: 'center', gap: '5px' }}>
                        <span style={{ color: theme.subtext, fontSize: '10px' }}>
                          {stock.marketCap ? 
                            (stock.marketCap >= 1e12 ? 
                              `$${(stock.marketCap/1e12).toFixed(1)}T` : 
                              `$${(stock.marketCap/1e9).toFixed(0)}B`
                            ) : 
                            stock.currency === 'KRW' ? 
                              `${(stock.marketCap/1e12).toFixed(0)}` : 
                              'N/A'
                          }
                        </span>
                        <span style={{
                          background: stock.country === 'KOR' ? theme.positive : theme.accent,
                          color: 'white',
                          padding: '1px 6px',
                          borderRadius: '8px',
                          fontSize: '10px'
                        }}>
                          {stock.country === 'KOR' ? '' : ''}
                        </span>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            )}
          </div>
        )}
      </div>

      {/*   */}
      <div style={{
        background: theme.cardBg,
        padding: '10px 20px',
        borderTop: `1px solid ${theme.border}`,
        fontSize: '12px',
        color: theme.subtext,
        display: 'flex',
        justifyContent: 'space-between'
      }}>
        <div>      </div>
        <div>   (UAVS)  </div>
      </div>
    </div>
  );
};

export default EnhancedStockSearch; 