import React, { useState } from 'react';
import axios from 'axios';

function summarizeHistory(history) {
  //  20     
  return history.map(h => {
    if (h.type === 'ai') {
      return `[AI] ${h.stock} : ${h.question} : ${h.answer}`;
    } else {
      return `[${h.stock}] : ${h.emotion} : ${h.chartType}`;
    }
  }).join('\n');
}

function AIProfileReport({ history }) {
  const [apiKey, setApiKey] = useState(localStorage.getItem('openai_api_key') || '');
  const [report, setReport] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  const handleAnalyze = async () => {
    setLoading(true);
    setError(null);
    setReport('');
    const summary = summarizeHistory(history);
    const prompt = `   20   //AI  .     (: /// ),  , ,   5  .\n\n${summary}`;
    try {
      const res = await axios.post(
        'https://api.openai.com/v1/chat/completions',
        {
          model: 'gpt-3.5-turbo',
          messages: [
            { role: 'system', content: '    .' },
            { role: 'user', content: prompt },
          ],
          max_tokens: 500,
        },
        {
          headers: {
            'Authorization': `Bearer ${apiKey}`,
            'Content-Type': 'application/json',
          },
        }
      );
      setReport(res.data.choices[0].message.content);
    } catch (e) {
      setError('AI  : API , ,   .');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div style={{ margin: '1.5rem 0', padding: '1rem', border: '1px solid #eee', borderRadius: '8px' }}>
      <h3>AI   </h3>
      <div style={{ marginBottom: 8 }}>
        <input
          type="password"
          placeholder="OpenAI API   (sk-...)"
          value={apiKey}
          onChange={e => setApiKey(e.target.value)}
          style={{ width: 220, marginRight: 8 }}
        />
        <button onClick={() => localStorage.setItem('openai_api_key', apiKey)}>API  </button>
        <button onClick={handleAnalyze} disabled={loading || !apiKey || history.length === 0} style={{marginLeft:8}}>
          {loading ? ' ...' : '  '}</button>
      </div>
      {error && <div style={{ color: 'red', marginBottom: 8 }}>{error}</div>}
      {report && <div style={{ background:'#fafafa', padding:12, borderRadius:6, color:'#333', whiteSpace:'pre-line' }}>{report}</div>}
      {!report && !loading && <div style={{ color:'#888', fontSize:'0.97rem' }}> //AI      .</div>}
    </div>
  );
}

export default AIProfileReport; 