import React, { useState } from 'react';
import axios from 'axios';

function summarizeNews(news) {
  //  +   
  return news.map(n => `: ${n.title} : ${n.summary}`).join('\n');
}

function NewsTrendSummary({ news }) {
  const [apiKey, setApiKey] = useState(localStorage.getItem('openai_api_key') || '');
  const [trend, setTrend] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  const handleAnalyze = async () => {
    setLoading(true);
    setError(null);
    setTrend('');
    const summary = summarizeNews(news);
    const prompt = `      .   , , /  5  .\n\n${summary}`;
    try {
      const res = await axios.post(
        'https://api.openai.com/v1/chat/completions',
        {
          model: 'gpt-3.5-turbo',
          messages: [
            { role: 'system', content: '    .' },
            { role: 'user', content: prompt },
          ],
          max_tokens: 500,
        },
        {
          headers: {
            'Authorization': `Bearer ${apiKey}`,
            'Content-Type': 'application/json',
          },
        }
      );
      setTrend(res.data.choices[0].message.content);
    } catch (e) {
      setError('AI    : API , ,   .');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div style={{ margin: '1.5rem 0', padding: '1rem', border: '1px solid #eee', borderRadius: '8px' }}>
      <h3>AI   </h3>
      <div style={{ marginBottom: 8 }}>
        <input
          type="password"
          placeholder="OpenAI API   (sk-...)"
          value={apiKey}
          onChange={e => setApiKey(e.target.value)}
          style={{ width: 220, marginRight: 8 }}
        />
        <button onClick={() => localStorage.setItem('openai_api_key', apiKey)}>API  </button>
        <button onClick={handleAnalyze} disabled={loading || !apiKey || news.length === 0} style={{marginLeft:8}}>
          {loading ? ' ...' : '  '}</button>
      </div>
      {error && <div style={{ color: 'red', marginBottom: 8 }}>{error}</div>}
      {trend && <div style={{ background:'#fafafa', padding:12, borderRadius:6, color:'#333', whiteSpace:'pre-line' }}>{trend}</div>}
      {!trend && !loading && <div style={{ color:'#888', fontSize:'0.97rem' }}>  /   , ,   .</div>}
    </div>
  );
}

export default NewsTrendSummary; 