import React from 'react';

const aiData = {
  '': {
    action: '',
    score: 8,
    reason: '  +   ',
  },
  '': {
    action: '',
    score: 6,
    reason: '  ,  ',
  },
  '': {
    action: '',
    score: 7,
    reason: '    ',
  },
};

function AIJudgement({ stock = '' }) {
  const data = aiData[stock] || aiData[''];
  return (
    <div style={{ margin: '1.5rem 0', padding: '1rem', border: '1px solid #eee', borderRadius: '8px' }}>
      <h3>AI  </h3>
      <div>: <strong>{data.action}</strong></div>
      <div>: <strong>{data.score}/10</strong></div>
      <div>: {data.reason}</div>
    </div>
  );
}

export default AIJudgement; 