import React, { useState, useCallback, useMemo } from 'react';
import { useLocalStorage } from '../hooks/useLocalStorage';
import { useTheme } from '../hooks/useTheme';
import ChaessaemEmoji from './ChaessaemEmoji';

// AI      
const AI_PROMPTS = [
    {
      id: 'industry',
      question: '    ?',
      options: [
        { value: 'ai', label: 'AI/', keywords: ['AI', '', '', 'ChatGPT', ''] },
        { value: 'defense', label: '/', keywords: ['', '', '', '', ''] },
        { value: 'space', label: '/', keywords: ['', '', '', '', 'X'] },
        { value: 'biotech', label: '/', keywords: ['', '', '', '', ''] },
        { value: 'energy', label: '/', keywords: ['', '', '', '', ''] },
        { value: 'fintech', label: '/', keywords: ['', '', '', '', 'DeFi'] },
        { value: 'gaming', label: '/', keywords: ['', '', 'VR', 'AR', 'NFT'] },
        { value: 'mobility', label: '/', keywords: ['', '', '', '', ''] }
      ]
    },
    {
      id: 'social_issues',
      question: '        ?',
      options: [
        { value: 'climate', label: '', keywords: ['', '', '', 'ESG', ''] },
        { value: 'aging', label: '', keywords: ['', '', '', '', ''] },
        { value: 'urbanization', label: '', keywords: ['', '', '', '', ''] },
        { value: 'digital', label: ' ', keywords: ['', 'DX', '', '', ''] },
        { value: 'food_security', label: '', keywords: ['', '', '', '', ''] },
        { value: 'geopolitics', label: ' ', keywords: ['', '', '', '', ''] }
      ]
    },
    {
      id: 'trends',
      question: '      ?',
      options: [
        { value: 'ukraine', label: '/', keywords: ['', '', '', '', ''] },
        { value: 'inflation', label: '/', keywords: ['', '', '', '', ''] },
        { value: 'supply_chain', label: ' ', keywords: ['', '', '', '', ''] },
        { value: 'china', label: '/', keywords: ['', '', '', '', ''] },
        { value: 'pandemic', label: ' ', keywords: ['', '', '', ''] },
        { value: 'meme', label: '/', keywords: ['', '', '', '', 'YOLO'] }
      ]
    },
    {
      id: 'personal',
      question: '    ?',
      options: [
        { value: 'penny', label: '/', keywords: ['', '', '', '', ''] },
        { value: 'dividend', label: '/', keywords: ['', '', 'REIT', '', ''] },
        { value: 'growth', label: '/', keywords: ['', '', '', '', 'IPO'] },
        { value: 'value', label: '/', keywords: ['', '', '', '', 'PER'] },
        { value: 'momentum', label: '/', keywords: ['', '', '', '', ''] },
        { value: 'contrarian', label: '/', keywords: ['', '', '', '', ''] }
      ]
    }
  ];

const KeywordBrainstormingSystem = ({ darkMode = false, onKeywordsGenerated }) => {
  const [currentKeywords, setCurrentKeywords] = useState([]);
  const [inputKeyword, setInputKeyword] = useState('');
  const [savedSessions, setSavedSessions] = useLocalStorage('keyword_sessions', []);
  const [currentSessionName, setCurrentSessionName] = useState('');
  const [showAIPrompts, setShowAIPrompts] = useState(false);
  const [selectedPromptAnswers, setSelectedPromptAnswers] = useState({});

  const theme = useTheme(darkMode);

  //  
  const addKeyword = useCallback((keyword) => {
    if (keyword && !currentKeywords.includes(keyword) && currentKeywords.length < 10) {
      setCurrentKeywords(prev => [...prev, keyword]);
      setInputKeyword('');
    }
  }, [currentKeywords]);

  //  
  const removeKeyword = useCallback((keyword) => {
    setCurrentKeywords(prev => prev.filter(k => k !== keyword));
  }, []);

  // AI   
  const handlePromptAnswer = useCallback((promptId, optionValue, keywords) => {
    setSelectedPromptAnswers(prev => ({
      ...prev,
      [promptId]: optionValue
    }));
    
    //   
    keywords.forEach(keyword => {
      if (!currentKeywords.includes(keyword) && currentKeywords.length < 10) {
        setCurrentKeywords(prev => [...prev, keyword]);
      }
    });
  }, [currentKeywords]);

  //  
  const saveSession = useCallback(() => {
    if (currentSessionName && currentKeywords.length > 0) {
      const newSession = {
        id: Date.now().toString(),
        name: currentSessionName,
        keywords: currentKeywords,
        promptAnswers: selectedPromptAnswers,
        timestamp: new Date().toISOString()
      };
      
      setSavedSessions(prev => [newSession, ...prev.slice(0, 9)]); //  10  
      setCurrentSessionName('');
      console.log('   :', newSession);
    }
  }, [currentSessionName, currentKeywords, selectedPromptAnswers, setSavedSessions]);

  //  
  const loadSession = useCallback((session) => {
    setCurrentKeywords(session.keywords);
    setSelectedPromptAnswers(session.promptAnswers);
    setCurrentSessionName(session.name);
  }, []);

  //   
  const startAnalysis = useCallback(() => {
    if (currentKeywords.length > 0 && onKeywordsGenerated) {
      onKeywordsGenerated(currentKeywords);
    }
  }, [currentKeywords, onKeywordsGenerated]);

  //     
  const keywordGroups = useMemo(() => {
    const groups = {};
    
    currentKeywords.forEach(keyword => {
      //    
      let category = '';
      
      if (['AI', '', '', 'ChatGPT', ''].some(tech => 
        keyword.includes(tech) || tech.includes(keyword))) {
        category = 'AI/Tech';
      } else if (['', '', '', '', ''].some(def => 
        keyword.includes(def) || def.includes(keyword))) {
        category = '/';
      } else if (['', '', '', '', ''].some(energy => 
        keyword.includes(energy) || energy.includes(keyword))) {
        category = '';
      } else if (['', '', '', ''].some(bio => 
        keyword.includes(bio) || bio.includes(keyword))) {
        category = '/';
      } else if (['', 'VR', 'AR', ''].some(game => 
        keyword.includes(game) || game.includes(keyword))) {
        category = '/';
      }
      
      if (!groups[category]) {
        groups[category] = [];
      }
      groups[category].push(keyword);
    });
    
    return groups;
  }, [currentKeywords]);

  return (
    <div style={{
      background: theme.bg,
      borderRadius: '12px',
      border: `2px solid ${theme.border}`,
      overflow: 'hidden',
      marginBottom: '20px'
    }}>
      {/*  */}
      <div style={{
        background: `linear-gradient(135deg, ${theme.orange}, ${theme.purple})`,
        padding: '20px',
        color: 'white'
      }}>
        <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
          <div>
            <h2 style={{ margin: 0, fontSize: '20px', fontWeight: '700' }}>
                
            </h2>
            <p style={{ margin: '8px 0 0 0', opacity: 0.9, fontSize: '14px' }}>
                    
            </p>
          </div>
          <div style={{ display: 'flex', gap: '10px', alignItems: 'center' }}>
            <span style={{
              background: 'rgba(255,255,255,0.2)',
              padding: '6px 12px',
              borderRadius: '20px',
              fontSize: '12px',
              fontWeight: '600'
            }}>
              {currentKeywords.length}/10 
            </span>
            <button
              onClick={() => setShowAIPrompts(!showAIPrompts)}
              style={{
                background: 'rgba(255,255,255,0.2)',
                border: '2px solid rgba(255,255,255,0.3)',
                color: 'white',
                padding: '8px 16px',
                borderRadius: '8px',
                cursor: 'pointer',
                fontSize: '14px',
                fontWeight: '600'
              }}
            >
              {showAIPrompts ? '  ' : ' AI '}
            </button>
          </div>
        </div>
      </div>

      <div style={{ padding: '25px' }}>
        {/* AI   */}
        {showAIPrompts && (
          <div style={{ marginBottom: '25px' }}>
            <div style={{ display: 'flex', alignItems: 'center', gap: '12px', marginBottom: '20px' }}>
              <ChaessaemEmoji
                type="emotions"
                emotion="thinking"
                size="normal"
                showMessage={false}
                autoAnimation={true}
                darkMode={darkMode}
              />
              <h3 style={{ color: theme.text, fontSize: '18px', margin: 0 }}>
                  
              </h3>
            </div>
            <div style={{ display: 'flex', flexDirection: 'column', gap: '20px' }}>
              {AI_PROMPTS.map(prompt => (
                <div
                  key={prompt.id}
                  style={{
                    background: theme.cardBg,
                    borderRadius: '12px',
                    padding: '20px',
                    border: `1px solid ${theme.border}`
                  }}
                >
                  <h4 style={{ color: theme.text, fontSize: '16px', marginBottom: '15px' }}>
                    {prompt.question}
                  </h4>
                  <div style={{
                    display: 'grid',
                    gridTemplateColumns: 'repeat(auto-fit, minmax(200px, 1fr))',
                    gap: '10px'
                  }}>
                    {prompt.options.map(option => (
                      <button
                        key={option.value}
                        onClick={() => handlePromptAnswer(prompt.id, option.value, option.keywords)}
                        style={{
                          background: selectedPromptAnswers[prompt.id] === option.value ? theme.accent : 'transparent',
                          color: selectedPromptAnswers[prompt.id] === option.value ? 'white' : theme.text,
                          border: `2px solid ${theme.accent}`,
                          padding: '12px 16px',
                          borderRadius: '8px',
                          cursor: 'pointer',
                          fontSize: '14px',
                          fontWeight: '600',
                          textAlign: 'left',
                          transition: 'all 0.2s'
                        }}
                      >
                        <div style={{ fontSize: '16px', marginBottom: '4px' }}>
                          {option.label}
                        </div>
                        <div style={{ fontSize: '11px', opacity: 0.8 }}>
                          {option.keywords.slice(0, 3).join(', ')}...
                        </div>
                      </button>
                    ))}
                  </div>
                </div>
              ))}
            </div>
          </div>
        )}

        {/*    */}
        {!showAIPrompts && (
          <div style={{ marginBottom: '25px' }}>
            <h3 style={{ color: theme.text, fontSize: '18px', marginBottom: '15px' }}>
                 
            </h3>
            <div style={{ display: 'flex', gap: '10px', marginBottom: '15px' }}>
              <input
                type="text"
                value={inputKeyword}
                onChange={(e) => setInputKeyword(e.target.value)}
                onKeyPress={(e) => e.key === 'Enter' && addKeyword(inputKeyword)}
                placeholder="    (: AI, , )"
                style={{
                  flex: 1,
                  padding: '12px 16px',
                  border: `2px solid ${theme.border}`,
                  borderRadius: '8px',
                  background: theme.bg,
                  color: theme.text,
                  fontSize: '14px'
                }}
              />
              <button
                onClick={() => addKeyword(inputKeyword)}
                disabled={!inputKeyword || currentKeywords.length >= 10}
                style={{
                  background: theme.positive,
                  border: 'none',
                  color: 'white',
                  padding: '12px 20px',
                  borderRadius: '8px',
                  cursor: inputKeyword && currentKeywords.length < 10 ? 'pointer' : 'not-allowed',
                  fontSize: '14px',
                  fontWeight: '600',
                  opacity: inputKeyword && currentKeywords.length < 10 ? 1 : 0.6
                }}
              >
                
              </button>
            </div>

            {/*   */}
            <div style={{ marginBottom: '15px' }}>
              <div style={{ color: theme.subtext, fontSize: '14px', marginBottom: '10px' }}>
                   ( ):
              </div>
              <div style={{ display: 'flex', flexWrap: 'wrap', gap: '8px' }}>
                {['AI', '', '', '', '', '', '', '', '', '', '', ''].map(keyword => (
                  <button
                    key={keyword}
                    onClick={() => addKeyword(keyword)}
                    disabled={currentKeywords.includes(keyword) || currentKeywords.length >= 10}
                    style={{
                      background: currentKeywords.includes(keyword) ? theme.subtext : 'transparent',
                      border: `1px solid ${theme.border}`,
                      color: currentKeywords.includes(keyword) ? 'white' : theme.text,
                      padding: '6px 12px',
                      borderRadius: '20px',
                      cursor: !currentKeywords.includes(keyword) && currentKeywords.length < 10 ? 'pointer' : 'not-allowed',
                      fontSize: '12px',
                      fontWeight: '600',
                      opacity: currentKeywords.includes(keyword) || currentKeywords.length >= 10 ? 0.6 : 1
                    }}
                  >
                    {keyword}
                  </button>
                ))}
              </div>
            </div>
          </div>
        )}

        {/*    */}
        {currentKeywords.length > 0 && (
          <div style={{ marginBottom: '25px' }}>
            <h3 style={{ color: theme.text, fontSize: '18px', marginBottom: '15px' }}>
                 ({currentKeywords.length})
            </h3>
            <div style={{ display: 'flex', flexWrap: 'wrap', gap: '10px', marginBottom: '15px' }}>
              {currentKeywords.map(keyword => (
                <div
                  key={keyword}
                  style={{
                    background: theme.accent,
                    color: 'white',
                    padding: '8px 16px',
                    borderRadius: '20px',
                    fontSize: '14px',
                    fontWeight: '600',
                    display: 'flex',
                    alignItems: 'center',
                    gap: '8px'
                  }}
                >
                  {keyword}
                  <button
                    onClick={() => removeKeyword(keyword)}
                    style={{
                      background: 'none',
                      border: 'none',
                      color: 'white',
                      cursor: 'pointer',
                      fontSize: '16px',
                      padding: '0',
                      lineHeight: 1
                    }}
                  >
                    
                  </button>
                </div>
              ))}
            </div>

            {/*   */}
            {Object.keys(keywordGroups).length > 1 && (
              <div style={{
                background: theme.cardBg,
                padding: '15px',
                borderRadius: '8px',
                border: `1px solid ${theme.border}`,
                marginBottom: '15px'
              }}>
                <h4 style={{ color: theme.text, fontSize: '14px', marginBottom: '10px' }}>
                     
                </h4>
                <div style={{ display: 'flex', flexWrap: 'wrap', gap: '10px' }}>
                  {Object.entries(keywordGroups).map(([category, keywords]) => (
                    <div
                      key={category}
                      style={{
                        background: theme.bg,
                        padding: '8px 12px',
                        borderRadius: '6px',
                        border: `1px solid ${theme.border}`
                      }}
                    >
                      <div style={{ color: theme.text, fontSize: '12px', fontWeight: '600', marginBottom: '4px' }}>
                        {category}
                      </div>
                      <div style={{ color: theme.subtext, fontSize: '11px' }}>
                        {keywords.join(', ')}
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            )}
          </div>
        )}

        {/*   */}
        <div style={{ marginBottom: '25px' }}>
          <h3 style={{ color: theme.text, fontSize: '18px', marginBottom: '15px' }}>
              
          </h3>
          <div style={{ display: 'flex', gap: '10px', marginBottom: '15px' }}>
            <input
              type="text"
              value={currentSessionName}
              onChange={(e) => setCurrentSessionName(e.target.value)}
              placeholder="  (:  , AI  )"
              style={{
                flex: 1,
                padding: '10px 14px',
                border: `2px solid ${theme.border}`,
                borderRadius: '6px',
                background: theme.bg,
                color: theme.text,
                fontSize: '14px'
              }}
            />
            <button
              onClick={saveSession}
              disabled={!currentSessionName || currentKeywords.length === 0}
              style={{
                background: theme.warning,
                border: 'none',
                color: 'white',
                padding: '10px 18px',
                borderRadius: '6px',
                cursor: currentSessionName && currentKeywords.length > 0 ? 'pointer' : 'not-allowed',
                fontSize: '14px',
                fontWeight: '600',
                opacity: currentSessionName && currentKeywords.length > 0 ? 1 : 0.6
              }}
            >
               
            </button>
          </div>

          {/*    */}
          {savedSessions.length > 0 && (
            <div style={{
              background: theme.cardBg,
              borderRadius: '8px',
              padding: '15px',
              border: `1px solid ${theme.border}`
            }}>
              <h4 style={{ color: theme.text, fontSize: '14px', marginBottom: '10px' }}>
                   ({savedSessions.length})
              </h4>
              <div style={{ display: 'flex', flexDirection: 'column', gap: '8px' }}>
                {savedSessions.slice(0, 5).map(session => (
                  <div
                    key={session.id}
                    style={{
                      background: theme.bg,
                      padding: '10px 12px',
                      borderRadius: '6px',
                      border: `1px solid ${theme.border}`,
                      display: 'flex',
                      justifyContent: 'space-between',
                      alignItems: 'center'
                    }}
                  >
                    <div>
                      <div style={{ color: theme.text, fontSize: '14px', fontWeight: '600', marginBottom: '2px' }}>
                        {session.name}
                      </div>
                      <div style={{ color: theme.subtext, fontSize: '12px' }}>
                        {session.keywords.slice(0, 3).join(', ')}{session.keywords.length > 3 && '...'} 
                         {new Date(session.timestamp).toLocaleDateString()}
                      </div>
                    </div>
                    <button
                      onClick={() => loadSession(session)}
                      style={{
                        background: theme.accent,
                        border: 'none',
                        color: 'white',
                        padding: '6px 12px',
                        borderRadius: '4px',
                        cursor: 'pointer',
                        fontSize: '12px',
                        fontWeight: '600'
                      }}
                    >
                      
                    </button>
                  </div>
                ))}
              </div>
            </div>
          )}
        </div>

        {/*    */}
        {currentKeywords.length > 0 && (
          <div style={{ textAlign: 'center' }}>
            <button
              onClick={startAnalysis}
              style={{
                background: `linear-gradient(135deg, ${theme.positive}, ${theme.teal})`,
                border: 'none',
                color: 'white',
                padding: '16px 32px',
                borderRadius: '12px',
                cursor: 'pointer',
                fontSize: '16px',
                fontWeight: '700',
                boxShadow: '0 4px 15px rgba(0,0,0,0.2)',
                transition: 'all 0.3s'
              }}
              onMouseEnter={(e) => e.target.style.transform = 'translateY(-2px)'}
              onMouseLeave={(e) => e.target.style.transform = 'translateY(0)'}
            >
                   !
            </button>
            <div style={{ color: theme.subtext, fontSize: '12px', marginTop: '10px' }}>
                , ,   
            </div>
          </div>
        )}
      </div>

      {/*   */}
      <div style={{
        background: theme.cardBg,
        padding: '15px 20px',
        borderTop: `1px solid ${theme.border}`,
        fontSize: '12px',
        color: theme.subtext,
        display: 'flex',
        justifyContent: 'space-between',
        flexWrap: 'wrap'
      }}>
        <div>       </div>
        <div> AI      </div>
      </div>
    </div>
  );
};

export default KeywordBrainstormingSystem; 