import React, { useState, useEffect } from 'react';
import ChaessaemEmoji from './ChaessaemEmoji';

//   
const technicalIndicators = {
  upper: [
    {
      name: '',
      emoji: '',
      simple: '       ',
      detailed: ' n     ,      .     ,    .',
      beginner: '     ,       ! '
    },
    {
      name: ' ',
      emoji: '',
      simple: '       ',
      detailed: '        .    ,   .     ,     .',
      beginner: '         ! '
    },
    {
      name: '',
      emoji: '',
      simple: '      ',
      detailed: '   4      .     ,   .',
      beginner: '   ,          ! '
    }
  ],
  lower: [
    {
      name: 'RSI',
      emoji: '',
      simple: '      ',
      detailed: '  /  0~100%   . 70%  (    ), 30%  (    ) .',
      beginner: '    !  (70% )  ,  (30% )    ! '
    },
    {
      name: 'MACD',
      emoji: '',
      simple: '     ',
      detailed: '/      . MACD      ,     .',
      beginner: '        ! '
    },
    {
      name: '',
      emoji: '',
      simple: '      ',
      detailed: '     .       ,     .',
      beginner: '       ! '
    },
    {
      name: '',
      emoji: '',
      simple: '        ',
      detailed: '    -     % . 80%  , 20%    .',
      beginner: '          ! '
    }
  ]
};

function TutorialHelper({ darkMode = false, onClose }) {
  const [currentStep, setCurrentStep] = useState(0);
  const [selectedCategory, setSelectedCategory] = useState('upper');
  const [tutorialMode, setTutorialMode] = useState(() => {
    return localStorage.getItem('tutorialMode') !== 'false';
  });

  const bg = darkMode ? '#1a1a1a' : '#fff';
  const border = darkMode ? '#333' : '#e0e0e0';
  const text = darkMode ? '#e0e0e0' : '#222';
  const accent = '#8884d8';

  const tutorialSteps = [
    {
      title: '   ',
      content: '!   !        .         !',
      emoji: ''
    },
    {
      title: '  ?',
      content: '     ! ,     .           !',
      emoji: ''
    },
    {
      title: '  ?',
      content: '      ! RSI, MACD   .      ,       !',
      emoji: ''
    },
    {
      title: '  ',
      content: ' 100%  !     .    ,        !',
      emoji: ''
    }
  ];

  const currentIndicators = technicalIndicators[selectedCategory];

  useEffect(() => {
    localStorage.setItem('tutorialMode', tutorialMode);
  }, [tutorialMode]);

  if (!tutorialMode) {
    return (
      <button
        onClick={() => setTutorialMode(true)}
        style={{
          position: 'fixed',
          bottom: '20px',
          right: '20px',
          background: `linear-gradient(45deg, ${accent}, #ff6b6b)`,
          color: '#fff',
          border: 'none',
          borderRadius: '50px',
          padding: '12px 20px',
          cursor: 'pointer',
          fontSize: '14px',
          fontWeight: '700',
          boxShadow: '0 4px 15px rgba(136, 132, 216, 0.4)',
          zIndex: 1000,
          transition: 'all 0.3s'
        }}
        onMouseEnter={(e) => {
          e.target.style.transform = 'scale(1.05)';
        }}
        onMouseLeave={(e) => {
          e.target.style.transform = 'scale(1)';
        }}
      >
          
      </button>
    );
  }

  return (
    <div style={{
      background: bg,
      border: `2px solid ${border}`,
      borderRadius: '20px',
      padding: '1.5rem',
      marginBottom: '1.5rem',
      boxShadow: darkMode ? '0 8px 32px rgba(0,0,0,0.3)' : '0 8px 32px rgba(0,0,0,0.1)',
      position: 'relative'
    }}>
      {/*  */}
      <div style={{
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
        marginBottom: '1.5rem'
      }}>
        <div style={{ display: 'flex', alignItems: 'center', gap: '12px' }}>
          <ChaessaemEmoji
            type="emotions"
            emotion="confident"
            size="normal"
            showMessage={false}
            autoAnimation={true}
            darkMode={darkMode}
          />
          <h3 style={{
            margin: 0,
            color: accent,
            fontSize: '1.4rem',
            fontWeight: '800',
            background: `linear-gradient(45deg, ${accent}, #ff6b6b)`,
            backgroundClip: 'text',
            WebkitBackgroundClip: 'text',
            WebkitTextFillColor: 'transparent'
          }}>
               
          </h3>
        </div>
        <button
          onClick={() => setTutorialMode(false)}
          style={{
            background: 'transparent',
            border: `2px solid ${border}`,
            borderRadius: '50px',
            padding: '8px 16px',
            cursor: 'pointer',
            fontSize: '12px',
            color: text,
            fontWeight: '600'
          }}
        >
            
        </button>
      </div>

      {/*   */}
      {currentStep < tutorialSteps.length && (
        <div style={{
          background: darkMode ? '#2a2a2a' : '#f8f9fa',
          borderRadius: '16px',
          padding: '1.5rem',
          marginBottom: '1.5rem',
          border: `2px solid ${accent}40`
        }}>
          <div style={{
            display: 'flex',
            alignItems: 'center',
            marginBottom: '1rem'
          }}>
            <div style={{ fontSize: '2rem', marginRight: '1rem' }}>
              {tutorialSteps[currentStep].emoji}
            </div>
            <div>
              <h4 style={{
                margin: '0 0 0.5rem 0',
                color: accent,
                fontSize: '1.2rem',
                fontWeight: '700'
              }}>
                {tutorialSteps[currentStep].title}
              </h4>
              <div style={{
                fontSize: '0.9rem',
                color: text,
                lineHeight: '1.6'
              }}>
                {tutorialSteps[currentStep].content}
              </div>
            </div>
          </div>
          
          <div style={{
            display: 'flex',
            justifyContent: 'space-between',
            alignItems: 'center'
          }}>
            <div style={{
              fontSize: '0.8rem',
              color: darkMode ? '#aaa' : '#666'
            }}>
              {currentStep + 1} / {tutorialSteps.length} 
            </div>
            <div style={{ display: 'flex', gap: '0.5rem' }}>
              {currentStep > 0 && (
                <button
                  onClick={() => setCurrentStep(currentStep - 1)}
                  style={{
                    background: 'transparent',
                    border: `2px solid ${border}`,
                    borderRadius: '20px',
                    padding: '8px 16px',
                    cursor: 'pointer',
                    fontSize: '0.9rem',
                    color: text
                  }}
                >
                   
                </button>
              )}
              <button
                onClick={() => setCurrentStep(currentStep + 1)}
                style={{
                  background: `linear-gradient(45deg, ${accent}, #ff6b6b)`,
                  color: '#fff',
                  border: 'none',
                  borderRadius: '20px',
                  padding: '8px 16px',
                  cursor: 'pointer',
                  fontSize: '0.9rem',
                  fontWeight: '600'
                }}
              >
                {currentStep === tutorialSteps.length - 1 ? '!' : ' '}
              </button>
            </div>
          </div>
        </div>
      )}

      {/*    */}
      {currentStep >= tutorialSteps.length && (
        <>
          {/*   */}
          <div style={{
            display: 'flex',
            justifyContent: 'center',
            gap: '1rem',
            marginBottom: '1.5rem'
          }}>
            <button
              onClick={() => setSelectedCategory('upper')}
              style={{
                background: selectedCategory === 'upper' 
                  ? `linear-gradient(45deg, ${accent}, #ff6b6b)` 
                  : 'transparent',
                color: selectedCategory === 'upper' ? '#fff' : text,
                border: `2px solid ${selectedCategory === 'upper' ? 'transparent' : border}`,
                borderRadius: '25px',
                padding: '0.8rem 1.5rem',
                cursor: 'pointer',
                fontSize: '1rem',
                fontWeight: '700',
                transition: 'all 0.3s'
              }}
            >
                
            </button>
            <button
              onClick={() => setSelectedCategory('lower')}
              style={{
                background: selectedCategory === 'lower' 
                  ? `linear-gradient(45deg, ${accent}, #ff6b6b)` 
                  : 'transparent',
                color: selectedCategory === 'lower' ? '#fff' : text,
                border: `2px solid ${selectedCategory === 'lower' ? 'transparent' : border}`,
                borderRadius: '25px',
                padding: '0.8rem 1.5rem',
                cursor: 'pointer',
                fontSize: '1rem',
                fontWeight: '700',
                transition: 'all 0.3s'
              }}
            >
                
            </button>
          </div>

          {/*    */}
          <div style={{
            display: 'grid',
            gap: '1rem',
            gridTemplateColumns: 'repeat(auto-fit, minmax(300px, 1fr))'
          }}>
            {currentIndicators.map((indicator, index) => (
              <div
                key={index}
                style={{
                  background: darkMode ? '#2a2a2a' : '#f8f9fa',
                  borderRadius: '16px',
                  padding: '1.5rem',
                  border: `2px solid ${border}`,
                  transition: 'all 0.3s'
                }}
                onMouseEnter={(e) => {
                  e.target.style.transform = 'translateY(-2px)';
                  e.target.style.boxShadow = '0 6px 20px rgba(0,0,0,0.1)';
                }}
                onMouseLeave={(e) => {
                  e.target.style.transform = 'translateY(0)';
                  e.target.style.boxShadow = 'none';
                }}
              >
                <div style={{
                  display: 'flex',
                  alignItems: 'center',
                  marginBottom: '1rem'
                }}>
                  <div style={{ fontSize: '1.5rem', marginRight: '0.8rem' }}>
                    {indicator.emoji}
                  </div>
                  <h4 style={{
                    margin: 0,
                    color: accent,
                    fontSize: '1.1rem',
                    fontWeight: '700'
                  }}>
                    {indicator.name}
                  </h4>
                </div>
                
                <div style={{
                  marginBottom: '1rem',
                  padding: '0.8rem',
                  background: darkMode ? '#1a1a1a' : '#fff',
                  borderRadius: '12px',
                  border: `1px solid ${border}`
                }}>
                  <div style={{
                    fontSize: '0.85rem',
                    color: '#ff6b6b',
                    fontWeight: '600',
                    marginBottom: '0.5rem'
                  }}>
                       :
                  </div>
                  <div style={{
                    fontSize: '0.9rem',
                    color: text,
                    lineHeight: '1.5'
                  }}>
                    {indicator.beginner}
                  </div>
                </div>

                <div style={{
                  marginBottom: '1rem',
                  fontSize: '0.9rem',
                  color: text,
                  fontWeight: '600',
                  lineHeight: '1.4'
                }}>
                   {indicator.simple}
                </div>

                <div style={{
                  fontSize: '0.85rem',
                  color: darkMode ? '#ccc' : '#555',
                  lineHeight: '1.5'
                }}>
                   <strong> :</strong> {indicator.detailed}
                </div>
              </div>
            ))}
          </div>
        </>
      )}

      {/*  */}
      <div style={{
        marginTop: '1.5rem',
        padding: '1rem',
        background: darkMode ? '#1a2a1a' : '#f0f8ff',
        borderRadius: '12px',
        border: `2px solid ${darkMode ? '#333' : '#e3f2fd'}`,
        textAlign: 'center'
      }}>
        <div style={{
          fontSize: '0.9rem',
          color: text,
          lineHeight: '1.5'
        }}>
           <strong>:</strong>    ! 
             ,        ! 
        </div>
      </div>
    </div>
  );
}

export default TutorialHelper; 