import React, { useState, useRef } from 'react';
import { useLocalStorage } from '../hooks/useLocalStorage';

const PortfolioOCR = ({ darkMode = false, onPortfolioUpdate }) => {
  const [portfolio, setPortfolio] = useLocalStorage('user_portfolio', []);
  const [isProcessing, setIsProcessing] = useState(false);
  const [ocrResult, setOcrResult] = useState('');
  const [extractedStocks, setExtractedStocks] = useState([]);
  const fileInputRef = useRef(null);

  const theme = {
    bg: darkMode ? '#1a1a1a' : '#ffffff',
    cardBg: darkMode ? '#2d2d2d' : '#f8f9fa',
    text: darkMode ? '#ffffff' : '#333333',
    subtext: darkMode ? '#cccccc' : '#666666',
    border: darkMode ? '#404040' : '#e0e0e0',
    accent: '#007bff',
    positive: '#00c851',
    negative: '#ff4444',
    warning: '#ffbb33'
  };

  // Tesseract.js OCR  (  OCR)
  const processImageWithOCR = async (imageFile) => {
    try {
      setIsProcessing(true);
      setOcrResult('');
      setExtractedStocks([]);

      //  Canvas  
      const canvas = document.createElement('canvas');
      const ctx = canvas.getContext('2d');
      const img = new Image();
      
      return new Promise((resolve, reject) => {
        img.onload = async () => {
          canvas.width = img.width;
          canvas.height = img.height;
          ctx.drawImage(img, 0, 0);
          
          //      
          const imageData = ctx.getImageData(0, 0, canvas.width, canvas.height);
          const data = imageData.data;
          
          //     
          for (let i = 0; i < data.length; i += 4) {
            const gray = data[i] * 0.299 + data[i + 1] * 0.587 + data[i + 2] * 0.114;
            const enhanced = gray > 128 ? 255 : 0; // 
            data[i] = enhanced;
            data[i + 1] = enhanced;
            data[i + 2] = enhanced;
          }
          
          ctx.putImageData(imageData, 0, 0);
          
          // OCR   ( Tesseract.js )
          setTimeout(() => {
            const mockOCRResult = simulateOCR(imageFile.name);
            setOcrResult(mockOCRResult);
            const extracted = extractStockData(mockOCRResult);
            setExtractedStocks(extracted);
            setIsProcessing(false);
            resolve(extracted);
          }, 3000);
        };
        
        img.onerror = () => {
          setIsProcessing(false);
          reject(new Error('  '));
        };
        
        img.src = URL.createObjectURL(imageFile);
      });
    } catch (error) {
      console.error('OCR  :', error);
      setIsProcessing(false);
      alert('OCR    : ' + error.message);
    }
  };

  // OCR   (  Tesseract.js )
  const simulateOCR = (fileName) => {
    const mockResults = [
      //  
      ` 
                100    78,500
      LG     50    480,000  
      AAPL            20     $195.50
      TSLA            15     $248.50
              30     $12.50`,
      
      //  
      ` 
      005930      200
      373220 LG     25  
      NVDA              10
      MSFT              40
      EAGLE             50`,
      
      //  
      `
      :
      -  (005930): 150
      -  (AAPL): 25  
      -  (TSLA): 10
      -  (ACEL): 75`
    ];
    
    return mockResults[Math.floor(Math.random() * mockResults.length)];
  };

  // OCR    
  const extractStockData = (ocrText) => {
    const stocks = [];
    const lines = ocrText.split('\n');
    
    //   
    const koreanStockPattern = /([-]+(?:||||||||||||||||)?)[\s\(]*(\d{6})?[\s\)]*[:\s]*(\d+)/g;
    
    //   
    const usStockPattern = /([A-Z]{1,5})[\s:]*(\d+)/g;
    
    //   
    const eaglePattern = /(|ACEL|EAGLE|AEGL)[\s\(]*([A-Z]+)?[\s\)]*[:\s]*(\d+)/g;
    
    //  
    const pricePattern = /([\d,]+(?:\.\d+)?)?|\$?([\d,]+(?:\.\d+)?)/g;

    lines.forEach(line => {
      //   
      let match;
      while ((match = koreanStockPattern.exec(line)) !== null) {
        const [, name, code, quantity] = match;
        stocks.push({
          symbol: code || getKoreanStockCode(name),
          name: name,
          quantity: parseInt(quantity),
          market: 'KOR',
          type: 'detected'
        });
      }
      
      //   
      koreanStockPattern.lastIndex = 0;
      while ((match = usStockPattern.exec(line)) !== null) {
        const [, symbol, quantity] = match;
        if (!['', '', '', ''].includes(symbol)) {
          stocks.push({
            symbol: symbol,
            name: getUSStockName(symbol),
            quantity: parseInt(quantity),
            market: 'US',
            type: 'detected'
          });
        }
      }
      
      //   
      usStockPattern.lastIndex = 0;
      while ((match = eaglePattern.exec(line)) !== null) {
        const [, koreanName, usSymbol, quantity] = match;
        const symbol = usSymbol || (koreanName === '' ? 'ACEL' : koreanName);
        stocks.push({
          symbol: symbol,
          name: koreanName === '' ? 'Accel Entertainment Inc' : koreanName,
          quantity: parseInt(quantity),
          market: 'US',
          type: 'detected',
          isEagle: true
        });
      }
    });

    //  
    const uniqueStocks = stocks.filter((stock, index, self) => 
      index === self.findIndex(s => s.symbol === stock.symbol)
    );

    return uniqueStocks;
  };

  //    
  const getKoreanStockCode = (name) => {
    const codeMap = {
      '': '005930',
      'LG': '373220',
      '': '207940',
      'SDI': '006400',
      'LG': '051910',
      'SK': '000660',
      '': '005380',
      'LG': '051900'
    };
    return codeMap[name] || '000000';
  };

  //     
  const getUSStockName = (symbol) => {
    const nameMap = {
      'AAPL': 'Apple Inc',
      'TSLA': 'Tesla Inc',
      'NVDA': 'NVIDIA Corp',
      'MSFT': 'Microsoft Corp',
      'GOOGL': 'Alphabet Inc',
      'META': 'Meta Platforms Inc',
      'AMZN': 'Amazon.com Inc',
      'ACEL': 'Accel Entertainment Inc',
      'EAGLE': 'Eagle Pharmaceuticals Inc',
      'AEGL': 'Aeglea BioTherapeutics Inc'
    };
    return nameMap[symbol] || `${symbol} Corp`;
  };

  //   
  const handleFileSelect = (event) => {
    const file = event.target.files[0];
    if (file && file.type.startsWith('image/')) {
      processImageWithOCR(file);
    } else {
      alert('   .');
    }
  };

  //    
  const addToPortfolio = (stock) => {
    const existingIndex = portfolio.findIndex(p => p.symbol === stock.symbol);
    
    if (existingIndex >= 0) {
      //    
      const updatedPortfolio = [...portfolio];
      updatedPortfolio[existingIndex].quantity += stock.quantity;
      setPortfolio(updatedPortfolio);
    } else {
      //   
      setPortfolio([...portfolio, {
        ...stock,
        addedAt: new Date().toISOString(),
        source: 'OCR'
      }]);
    }
    
    if (onPortfolioUpdate) {
      onPortfolioUpdate(portfolio);
    }
  };

  //     
  const addAllToPortfolio = () => {
    extractedStocks.forEach(stock => addToPortfolio(stock));
    setExtractedStocks([]);
    alert(`${extractedStocks.length}   .`);
  };

  return (
    <div style={{
      background: theme.bg,
      borderRadius: '8px',
      border: `1px solid ${theme.border}`,
      marginBottom: '20px',
      overflow: 'hidden'
    }}>
      {/*  */}
      <div style={{
        background: theme.cardBg,
        padding: '15px 20px',
        borderBottom: `1px solid ${theme.border}`,
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center'
      }}>
        <div style={{ display: 'flex', alignItems: 'center', gap: '10px' }}>
          <h3 style={{ margin: 0, color: theme.text, fontSize: '18px' }}>
                (OCR)
          </h3>
          <span style={{
            background: theme.warning,
            color: 'white',
            padding: '2px 8px',
            borderRadius: '12px',
            fontSize: '12px'
          }}>
            Beta
          </span>
        </div>
        <button
          onClick={() => fileInputRef.current?.click()}
          disabled={isProcessing}
          style={{
            background: isProcessing ? theme.subtext : theme.accent,
            color: 'white',
            border: 'none',
            padding: '8px 15px',
            borderRadius: '6px',
            cursor: isProcessing ? 'not-allowed' : 'pointer',
            fontSize: '14px',
            fontWeight: '600'
          }}
        >
          {isProcessing ? ' ...' : '  '}
        </button>
      </div>

      {/*   () */}
      <input
        ref={fileInputRef}
        type="file"
        accept="image/*"
        onChange={handleFileSelect}
        style={{ display: 'none' }}
      />

      {/*  */}
      <div style={{
        background: theme.cardBg,
        padding: '15px 20px',
        borderBottom: `1px solid ${theme.border}`
      }}>
        <h4 style={{ margin: '0 0 10px 0', color: theme.text, fontSize: '16px' }}>
             
        </h4>
        <div style={{
          display: 'grid',
          gridTemplateColumns: 'repeat(auto-fit, minmax(200px, 1fr))',
          gap: '10px',
          fontSize: '14px'
        }}>
          <div style={{ color: theme.subtext }}>  </div>
          <div style={{ color: theme.subtext }}>  </div>
          <div style={{ color: theme.subtext }}> </div>
          <div style={{ color: theme.subtext }}>  </div>
          <div style={{ color: theme.subtext }}> NH</div>
          <div style={{ color: theme.subtext }}> KB </div>
        </div>
        <div style={{ 
          marginTop: '10px', 
          padding: '10px', 
          background: theme.warning + '20', 
          borderRadius: '6px',
          fontSize: '12px',
          color: theme.text
        }}>
           <strong>:</strong>   ,     .     !
        </div>
      </div>

      {/* OCR   */}
      {isProcessing && (
        <div style={{
          padding: '30px 20px',
          textAlign: 'center',
          background: theme.cardBg,
          borderBottom: `1px solid ${theme.border}`
        }}>
          <div style={{ fontSize: '48px', marginBottom: '15px' }}></div>
          <div style={{ color: theme.text, fontSize: '16px', marginBottom: '10px' }}>
              ...
          </div>
          <div style={{ color: theme.subtext, fontSize: '14px' }}>
                .  .
          </div>
          <div style={{
            width: '200px',
            height: '4px',
            background: theme.border,
            borderRadius: '2px',
            margin: '20px auto',
            overflow: 'hidden'
          }}>
            <div style={{
              width: '50%',
              height: '100%',
              background: theme.accent,
              borderRadius: '2px',
              animation: 'progress 2s ease-in-out infinite'
            }}></div>
          </div>
        </div>
      )}

      {/* OCR  */}
      {ocrResult && !isProcessing && (
        <div style={{
          padding: '15px 20px',
          borderBottom: `1px solid ${theme.border}`
        }}>
          <h4 style={{ margin: '0 0 10px 0', color: theme.text, fontSize: '16px' }}>
              
          </h4>
          <div style={{
            background: theme.bg,
            border: `1px solid ${theme.border}`,
            borderRadius: '6px',
            padding: '10px',
            fontSize: '12px',
            color: theme.subtext,
            maxHeight: '150px',
            overflowY: 'auto',
            fontFamily: 'monospace',
            whiteSpace: 'pre-wrap'
          }}>
            {ocrResult}
          </div>
        </div>
      )}

      {/*   */}
      {extractedStocks.length > 0 && (
        <div style={{ padding: '15px 20px' }}>
          <div style={{
            display: 'flex',
            justifyContent: 'space-between',
            alignItems: 'center',
            marginBottom: '15px'
          }}>
            <h4 style={{ margin: 0, color: theme.text, fontSize: '16px' }}>
                 ({extractedStocks.length})
            </h4>
            <button
              onClick={addAllToPortfolio}
              style={{
                background: theme.positive,
                color: 'white',
                border: 'none',
                padding: '6px 12px',
                borderRadius: '6px',
                cursor: 'pointer',
                fontSize: '12px',
                fontWeight: '600'
              }}
            >
               
            </button>
          </div>
          
          <div style={{
            display: 'grid',
            gridTemplateColumns: 'repeat(auto-fill, minmax(280px, 1fr))',
            gap: '10px'
          }}>
            {extractedStocks.map((stock, index) => (
              <div
                key={index}
                style={{
                  background: stock.isEagle ? theme.accent + '20' : theme.cardBg,
                  border: `1px solid ${stock.isEagle ? theme.accent : theme.border}`,
                  borderRadius: '6px',
                  padding: '12px',
                  transition: 'all 0.2s'
                }}
              >
                <div style={{
                  display: 'flex',
                  justifyContent: 'space-between',
                  alignItems: 'flex-start',
                  marginBottom: '8px'
                }}>
                  <div>
                    <div style={{ 
                      color: stock.isEagle ? theme.accent : theme.text, 
                      fontWeight: '600', 
                      fontSize: '14px' 
                    }}>
                      {stock.symbol} {stock.isEagle && ''}
                    </div>
                    <div style={{ color: theme.subtext, fontSize: '12px' }}>
                      {stock.name}
                    </div>
                  </div>
                  <div style={{
                    background: stock.market === 'KOR' ? theme.positive : theme.accent,
                    color: 'white',
                    padding: '2px 6px',
                    borderRadius: '8px',
                    fontSize: '10px'
                  }}>
                    {stock.market}
                  </div>
                </div>
                
                <div style={{
                  display: 'flex',
                  justifyContent: 'space-between',
                  alignItems: 'center'
                }}>
                  <div style={{ color: theme.text, fontSize: '13px' }}>
                    : <strong>{stock.quantity.toLocaleString()}</strong>
                  </div>
                  <button
                    onClick={() => addToPortfolio(stock)}
                    style={{
                      background: theme.positive,
                      color: 'white',
                      border: 'none',
                      padding: '4px 8px',
                      borderRadius: '4px',
                      cursor: 'pointer',
                      fontSize: '11px'
                    }}
                  >
                    
                  </button>
                </div>
              </div>
            ))}
          </div>
        </div>
      )}

      {/*   */}
      {portfolio.length > 0 && (
        <div style={{
          background: theme.cardBg,
          padding: '15px 20px',
          borderTop: `1px solid ${theme.border}`
        }}>
          <h4 style={{ margin: '0 0 10px 0', color: theme.text, fontSize: '16px' }}>
               ({portfolio.length} )
          </h4>
          <div style={{
            display: 'grid',
            gridTemplateColumns: 'repeat(auto-fill, minmax(200px, 1fr))',
            gap: '8px',
            fontSize: '12px'
          }}>
            {portfolio.map((stock, index) => (
              <div key={index} style={{
                display: 'flex',
                justifyContent: 'space-between',
                padding: '6px',
                background: theme.bg,
                borderRadius: '4px',
                border: `1px solid ${theme.border}`
              }}>
                <span style={{ color: theme.accent, fontWeight: '600' }}>
                  {stock.symbol}
                </span>
                <span style={{ color: theme.text }}>
                  {stock.quantity.toLocaleString()}
                </span>
              </div>
            ))}
          </div>
        </div>
      )}
    </div>
  );
};

// CSS  
const style = document.createElement('style');
style.textContent = `
  @keyframes progress {
    0% { transform: translateX(-100%); }
    100% { transform: translateX(400%); }
  }
`;
document.head.appendChild(style);

export default PortfolioOCR; 