import React, { useState, useEffect, useCallback } from 'react';
import axios from 'axios';

const EnhancedNewsAnalyzer = ({ darkMode = false, selectedStock = '' }) => {
  const [news, setNews] = useState([]);
  const [loading, setLoading] = useState(false);
  const [summaries, setSummaries] = useState({});
  const [selectedCategory, setSelectedCategory] = useState('all');
  const [searchTerm, setSearchTerm] = useState('');
  const [autoRefresh, setAutoRefresh] = useState(true);

  const theme = {
    bg: darkMode ? '#1a1a1a' : '#ffffff',
    cardBg: darkMode ? '#2d2d2d' : '#f8f9fa',
    text: darkMode ? '#ffffff' : '#333333',
    subtext: darkMode ? '#cccccc' : '#666666',
    border: darkMode ? '#404040' : '#e0e0e0',
    accent: '#007bff',
    positive: '#00c851',
    negative: '#ff4444',
    warning: '#ffbb33'
  };

  //   
  const newsSources = {
    google: {
      name: 'Google News',
      baseUrl: 'https://news.google.com/rss/search',
      enabled: true
    },
    yahoo: {
      name: 'Yahoo Finance',
      baseUrl: 'https://feeds.finance.yahoo.com',
      enabled: true
    },
    reuters: {
      name: 'Reuters Business',
      baseUrl: 'https://feeds.reuters.com/reuters/businessNews',
      enabled: true
    },
    cnbc: {
      name: 'CNBC',
      baseUrl: 'https://search.cnbc.com/rs/search/combinedcms/view.xml',
      enabled: true
    }
  };

  //   
  const fetchRealTimeNews = useCallback(async (query = selectedStock) => {
    setLoading(true);
    try {
      const newsItems = [];
      
      //     (Mock  -  RSS/API )
      const mockNewsData = generateMockNews(query);
      
      for (const item of mockNewsData) {
        // URL     
        const content = await extractNewsContent(item.url);
        const summary = await generateNewsSummary(content, item.title);
        
        newsItems.push({
          ...item,
          content,
          summary,
          sentiment: analyzeSentiment(content),
          relevanceScore: calculateRelevanceScore(content, query)
        });
      }
      
      //   
      newsItems.sort((a, b) => b.relevanceScore - a.relevanceScore);
      
      setNews(newsItems);
    } catch (error) {
      console.error('  :', error);
    } finally {
      setLoading(false);
    }
  }, [selectedStock]);

  // Mock   
  const generateMockNews = (query) => {
    const baseNews = [
      {
        id: 1,
        title: `${query}  ,  `,
        url: 'https://finance.yahoo.com/news/stock-surge-123',
        source: 'Yahoo Finance',
        publishedAt: new Date(Date.now() - 1000 * 60 * 30), // 30 
        category: 'stock',
        image: 'https://via.placeholder.com/300x200?text=Stock+News'
      },
      {
        id: 2,
        title: `  ${query}   `,
        url: 'https://www.reuters.com/business/markets/stock-rise-456',
        source: 'Reuters',
        publishedAt: new Date(Date.now() - 1000 * 60 * 60), // 1 
        category: 'market',
        image: 'https://via.placeholder.com/300x200?text=Market+News'
      },
      {
        id: 3,
        title: `${query}   ,  `,
        url: 'https://www.cnbc.com/earnings/report-789',
        source: 'CNBC',
        publishedAt: new Date(Date.now() - 1000 * 60 * 120), // 2 
        category: 'earnings',
        image: 'https://via.placeholder.com/300x200?text=Earnings+News'
      },
      {
        id: 4,
        title: `     `,
        url: 'https://news.google.com/article/new-business-abc',
        source: 'Google News',
        publishedAt: new Date(Date.now() - 1000 * 60 * 180), // 3 
        category: 'business',
        image: 'https://via.placeholder.com/300x200?text=Business+News'
      },
      {
        id: 5,
        title: ` : ${query}   `,
        url: 'https://finance.naver.com/industry/aviation-def',
        source: 'Naver Finance',
        publishedAt: new Date(Date.now() - 1000 * 60 * 240), // 4 
        category: 'industry',
        image: 'https://via.placeholder.com/300x200?text=Industry+News'
      }
    ];

    //      
    return baseNews.map(item => ({
      ...item,
      title: item.title.replace(/|ACEL|EAGLE/g, query),
      isRelevant: item.title.toLowerCase().includes(query.toLowerCase()) || 
                  item.category === 'stock' || 
                  query.toLowerCase().includes('eagle')
    }));
  };

  // URL    ()
  const extractNewsContent = async (url) => {
    //     API 
    await new Promise(resolve => setTimeout(resolve, 500)); //  
    
    const mockContents = [
      `${selectedStock}         . 
                 . 
         30%    ,       . 
            ,       .`,
      
      `  ${selectedStock}    . 
                 . 
               . 
            ,       .`,
      
      `${selectedStock}           . 
          25% ,  40%   . 
                 . 
            15%   52  .`,
      
      `          . 
        3  5        . 
             ,     . 
                .`,
      
      `     ${selectedStock}       . 
      19      ,      . 
        2-3       . 
                .`
    ];
    
    return mockContents[Math.floor(Math.random() * mockContents.length)];
  };

  // AI    (200 )
  const generateNewsSummary = async (content, title) => {
    //  OpenAI API 
    await new Promise(resolve => setTimeout(resolve, 300));
    
    //    ( GPT )
    const sentences = content.split('. ').filter(s => s.length > 10);
    const keywords = extractKeywords(content);
    
    //   
    const keySentences = sentences
      .filter(sentence => keywords.some(keyword => sentence.includes(keyword)))
      .slice(0, 2);
    
    let summary = keySentences.join('. ');
    
    // 200 
    if (summary.length > 200) {
      summary = summary.substring(0, 197) + '...';
    }
    
    //     
    if (summary.length < 50) {
      summary = `${selectedStock}   . ${sentences[0].substring(0, 150)}...`;
    }
    
    return summary;
  };

  //  
  const extractKeywords = (text) => {
    const commonWords = ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', ''];
    const words = text.split(/\s+/)
      .filter(word => word.length > 1 && !commonWords.includes(word))
      .map(word => word.replace(/[^\w-]/g, ''));
    
    //  
    const frequency = {};
    words.forEach(word => {
      frequency[word] = (frequency[word] || 0) + 1;
    });
    
    //    
    return Object.keys(frequency)
      .sort((a, b) => frequency[b] - frequency[a])
      .slice(0, 5);
  };

  //  
  const analyzeSentiment = (content) => {
    const positiveWords = ['', '', '', '', '', '', '', '', '', '', ''];
    const negativeWords = ['', '', '', '', '', '', '', '', '', ''];
    
    let positiveScore = 0;
    let negativeScore = 0;
    
    positiveWords.forEach(word => {
      positiveScore += (content.match(new RegExp(word, 'g')) || []).length;
    });
    
    negativeWords.forEach(word => {
      negativeScore += (content.match(new RegExp(word, 'g')) || []).length;
    });
    
    if (positiveScore > negativeScore) return 'positive';
    if (negativeScore > positiveScore) return 'negative';
    return 'neutral';
  };

  //   
  const calculateRelevanceScore = (content, query) => {
    const queryWords = query.toLowerCase().split(' ');
    let score = 0;
    
    queryWords.forEach(word => {
      score += (content.toLowerCase().match(new RegExp(word, 'g')) || []).length * 10;
    });
    
    //    
    const stockKeywords = ['', '', '', '', '', ''];
    stockKeywords.forEach(keyword => {
      score += (content.includes(keyword) ? 5 : 0);
    });
    
    return score;
  };

  //  
  const filteredNews = news.filter(item => {
    const matchesCategory = selectedCategory === 'all' || item.category === selectedCategory;
    const matchesSearch = !searchTerm || 
      item.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
      item.content.toLowerCase().includes(searchTerm.toLowerCase());
    
    return matchesCategory && matchesSearch;
  });

  //  
  useEffect(() => {
    if (autoRefresh) {
      const interval = setInterval(() => {
        fetchRealTimeNews();
      }, 300000); // 5
      
      return () => clearInterval(interval);
    }
  }, [autoRefresh, fetchRealTimeNews]);

  //  
  useEffect(() => {
    fetchRealTimeNews();
  }, [fetchRealTimeNews]);

  //  
  const getSentimentColor = (sentiment) => {
    switch (sentiment) {
      case 'positive': return theme.positive;
      case 'negative': return theme.negative;
      default: return theme.subtext;
    }
  };

  //  
  const getSentimentIcon = (sentiment) => {
    switch (sentiment) {
      case 'positive': return '';
      case 'negative': return '';
      default: return '';
    }
  };

  return (
    <div style={{
      background: theme.bg,
      borderRadius: '8px',
      border: `1px solid ${theme.border}`,
      marginBottom: '20px',
      overflow: 'hidden'
    }}>
      {/*  */}
      <div style={{
        background: theme.cardBg,
        padding: '15px 20px',
        borderBottom: `1px solid ${theme.border}`
      }}>
        <div style={{
          display: 'flex',
          justifyContent: 'space-between',
          alignItems: 'center',
          marginBottom: '15px'
        }}>
          <div style={{ display: 'flex', alignItems: 'center', gap: '10px' }}>
            <h3 style={{ margin: 0, color: theme.text, fontSize: '18px' }}>
                 
            </h3>
            <span style={{
              background: theme.positive,
              color: 'white',
              padding: '2px 8px',
              borderRadius: '12px',
              fontSize: '12px'
            }}>
              AI 
            </span>
          </div>
          
          <div style={{ display: 'flex', gap: '10px' }}>
            <button
              onClick={() => setAutoRefresh(!autoRefresh)}
              style={{
                background: autoRefresh ? theme.positive : theme.subtext,
                color: 'white',
                border: 'none',
                padding: '6px 12px',
                borderRadius: '6px',
                cursor: 'pointer',
                fontSize: '12px'
              }}
            >
              {autoRefresh ? ' ' : ' '}
            </button>
            
            <button
              onClick={() => fetchRealTimeNews()}
              disabled={loading}
              style={{
                background: loading ? theme.subtext : theme.accent,
                color: 'white',
                border: 'none',
                padding: '6px 12px',
                borderRadius: '6px',
                cursor: loading ? 'not-allowed' : 'pointer',
                fontSize: '12px'
              }}
            >
              {loading ? '' : ''} 
            </button>
          </div>
        </div>

        {/*    */}
        <div style={{
          display: 'flex',
          gap: '10px',
          flexWrap: 'wrap',
          alignItems: 'center'
        }}>
          <select
            value={selectedCategory}
            onChange={(e) => setSelectedCategory(e.target.value)}
            style={{
              background: theme.bg,
              border: `1px solid ${theme.border}`,
              color: theme.text,
              padding: '6px 10px',
              borderRadius: '4px',
              fontSize: '12px'
            }}
          >
            <option value="all"> </option>
            <option value="stock"></option>
            <option value="market"></option>
            <option value="earnings"></option>
            <option value="business"></option>
            <option value="industry"></option>
          </select>
          
          <input
            type="text"
            placeholder=" ..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            style={{
              background: theme.bg,
              border: `1px solid ${theme.border}`,
              color: theme.text,
              padding: '6px 10px',
              borderRadius: '4px',
              fontSize: '12px',
              minWidth: '150px'
            }}
          />
          
          <span style={{ color: theme.subtext, fontSize: '12px' }}>
            {filteredNews.length} 
          </span>
        </div>
      </div>

      {/*   */}
      <div style={{ maxHeight: '600px', overflowY: 'auto' }}>
        {loading ? (
          <div style={{
            padding: '40px',
            textAlign: 'center',
            color: theme.subtext
          }}>
            <div style={{ fontSize: '32px', marginBottom: '10px' }}></div>
               AI   ...
          </div>
        ) : filteredNews.length === 0 ? (
          <div style={{
            padding: '40px',
            textAlign: 'center',
            color: theme.subtext
          }}>
                .
          </div>
        ) : (
          filteredNews.map((item, index) => (
            <div
              key={item.id}
              style={{
                padding: '15px 20px',
                borderBottom: `1px solid ${theme.border}`,
                transition: 'background-color 0.2s'
              }}
              onMouseEnter={(e) => e.target.style.background = theme.cardBg}
              onMouseLeave={(e) => e.target.style.background = 'transparent'}
            >
              <div style={{
                display: 'flex',
                justifyContent: 'space-between',
                alignItems: 'flex-start',
                marginBottom: '10px'
              }}>
                <div style={{ flex: 1 }}>
                  <div style={{
                    display: 'flex',
                    alignItems: 'center',
                    gap: '8px',
                    marginBottom: '5px'
                  }}>
                    <span style={{
                      color: getSentimentColor(item.sentiment),
                      fontSize: '16px'
                    }}>
                      {getSentimentIcon(item.sentiment)}
                    </span>
                    <h4 style={{
                      margin: 0,
                      color: theme.text,
                      fontSize: '14px',
                      fontWeight: '600',
                      lineHeight: '1.4'
                    }}>
                      {item.title}
                    </h4>
                  </div>
                  
                  <div style={{
                    color: theme.subtext,
                    fontSize: '12px',
                    marginBottom: '8px'
                  }}>
                    {item.source}  {item.publishedAt.toLocaleTimeString()}  
                    <a 
                      href={item.url} 
                      target="_blank" 
                      rel="noopener noreferrer"
                      style={{ color: theme.accent, textDecoration: 'none', marginLeft: '5px' }}
                    >
                        
                    </a>
                  </div>
                  
                  {/* AI  */}
                  <div style={{
                    background: theme.cardBg,
                    padding: '10px',
                    borderRadius: '6px',
                    border: `1px solid ${theme.border}`,
                    marginBottom: '8px'
                  }}>
                    <div style={{
                      color: theme.accent,
                      fontSize: '11px',
                      fontWeight: '600',
                      marginBottom: '5px'
                    }}>
                       AI  (200)
                    </div>
                    <div style={{
                      color: theme.text,
                      fontSize: '13px',
                      lineHeight: '1.5'
                    }}>
                      {item.summary}
                    </div>
                  </div>
                </div>
                
                <div style={{
                  display: 'flex',
                  flexDirection: 'column',
                  alignItems: 'flex-end',
                  gap: '5px',
                  marginLeft: '15px'
                }}>
                  <span style={{
                    background: theme.accent,
                    color: 'white',
                    padding: '2px 6px',
                    borderRadius: '8px',
                    fontSize: '10px'
                  }}>
                    {item.category}
                  </span>
                  
                  <span style={{
                    background: getSentimentColor(item.sentiment) + '20',
                    color: getSentimentColor(item.sentiment),
                    padding: '2px 6px',
                    borderRadius: '8px',
                    fontSize: '10px'
                  }}>
                     {item.relevanceScore}
                  </span>
                </div>
              </div>
            </div>
          ))
        )}
      </div>

      {/*   */}
      <div style={{
        background: theme.cardBg,
        padding: '10px 20px',
        borderTop: `1px solid ${theme.border}`,
        fontSize: '12px',
        color: theme.subtext,
        display: 'flex',
        justifyContent: 'space-between'
      }}>
        <div> AI 200   </div>
        <div> {autoRefresh ? '5  ' : ' '}</div>
      </div>
    </div>
  );
};

export default EnhancedNewsAnalyzer; 