import React, { useState, useRef, useEffect } from 'react';
import { useTheme } from '../hooks/useTheme';
import { useTypography } from '../hooks/useTypography';
import EnhancedButton from './EnhancedButton';

const CategoryNavigation = ({ currentCategory, onCategoryChange, darkMode = false }) => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [focusedIndex, setFocusedIndex] = useState(-1);
  const menuRef = useRef(null);
  const theme = useTheme(darkMode);
  const typography = useTypography(darkMode);

  const categories = [
    {
      id: 'trading',
      name: ' ',
      description: ', , ',
      icon: ''
    },
    {
      id: 'analysis',
      name: ' ',
      description: 'AI, , ',
      icon: ''
    },
    {
      id: 'news',
      name: ' ',
      description: ', ',
      icon: ''
    },
    {
      id: 'portfolio',
      name: ' ',
      description: ', ',
      icon: ''
    },
    {
      id: 'psychology',
      name: '  & ',
      description: ', , ',
      icon: ''
    },
    {
      id: 'discovery',
      name: '   ',
      description: ', , , ',
      icon: ''
    },
    {
      id: 'tools',
      name: ' ',
      description: ', , ',
      icon: ''
    }
  ];

  //   
  useEffect(() => {
    const handleKeyDown = (e) => {
      if (!isMenuOpen) return;
      
      switch (e.key) {
        case 'Escape':
          setIsMenuOpen(false);
          setFocusedIndex(-1);
          break;
        case 'ArrowDown':
          e.preventDefault();
          setFocusedIndex(prev => 
            prev < categories.length - 1 ? prev + 1 : 0
          );
          break;
        case 'ArrowUp':
          e.preventDefault();
          setFocusedIndex(prev => 
            prev > 0 ? prev - 1 : categories.length - 1
          );
          break;
        case 'Enter':
          if (focusedIndex >= 0) {
            handleCategorySelect(categories[focusedIndex].id);
          }
          break;
      }
    };

    window.addEventListener('keydown', handleKeyDown);
    return () => window.removeEventListener('keydown', handleKeyDown);
  }, [isMenuOpen, focusedIndex, categories]);

  const handleCategorySelect = (categoryId) => {
    onCategoryChange(categoryId);
    setIsMenuOpen(false);
  };

  return (
    <>
      {/*    */}
      <EnhancedButton
        onClick={() => setIsMenuOpen(true)}
        variant="primary"
        size="normal"
        icon=""
        tooltip="   (: M)"
        darkMode={darkMode}
        data-guide="category-button"
        style={{
          position: 'fixed',
          top: '20px',
          left: '20px',
          zIndex: 1000,
          width: '48px',
          height: '48px',
          padding: '0',
          borderRadius: '12px'
        }}
      />

      {/*    */}
      {isMenuOpen && (
        <div
          style={{
            position: 'fixed',
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            background: 'rgba(0, 0, 0, 0.75)',
            zIndex: 1001,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            backdropFilter: 'blur(8px)',
            animation: 'fadeIn 0.3s ease'
          }}
          onClick={() => setIsMenuOpen(false)}
        >
          <div
            ref={menuRef}
            style={{
              background: theme.bg,
              borderRadius: '20px',
              padding: '32px',
              maxWidth: '640px',
              width: '90%',
              maxHeight: '85vh',
              overflowY: 'auto',
              boxShadow: theme.shadows.xl,
              border: `2px solid ${theme.border}`,
              fontFamily: typography.fontFamily.primary,
              animation: 'slideUp 0.3s ease'
            }}
            onClick={(e) => e.stopPropagation()}
          >
            {/*  */}
            <div style={{
              display: 'flex',
              justifyContent: 'space-between',
              alignItems: 'center',
              marginBottom: '28px',
              borderBottom: `2px solid ${theme.border}`,
              paddingBottom: '20px'
            }}>
              <h2 style={{
                ...typography.presets.heading.h2,
                color: typography.colors.primary,
                margin: 0,
                display: 'flex',
                alignItems: 'center',
                gap: '12px'
              }}>
                 <span> </span>
              </h2>
              <EnhancedButton
                onClick={() => setIsMenuOpen(false)}
                variant="ghost"
                size="small"
                icon=""
                tooltip=" (Esc)"
                darkMode={darkMode}
                style={{
                  width: '36px',
                  height: '36px',
                  padding: '0'
                }}
              />
            </div>

            {/*     */}
            <div style={{
              background: `${theme.accent}15`,
              border: `2px solid ${theme.accent}`,
              borderRadius: '12px',
              padding: '16px',
              marginBottom: '24px',
              textAlign: 'center'
            }}>
              <div style={{ 
                ...typography.presets.label,
                color: typography.colors.muted,
                marginBottom: '4px'
              }}>
                  
              </div>
              <div style={{ 
                ...typography.presets.body.large,
                color: theme.accent, 
                fontWeight: typography.fontWeight.bold
              }}>
                {categories.find(cat => cat.id === currentCategory)?.name || ' '}
              </div>
            </div>

            {/*   */}
            <div style={{
              display: 'grid',
              gap: '16px'
            }}>
              {categories.map((category, index) => (
                <EnhancedButton
                  key={category.id}
                  onClick={() => handleCategorySelect(category.id)}
                  variant={currentCategory === category.id ? "primary" : "secondary"}
                  size="large"
                  darkMode={darkMode}
                  data-guide={category.id === 'discovery' ? 'keyword-discovery' : undefined}
                  style={{
                    width: '100%',
                    height: 'auto',
                    padding: '20px',
                    justifyContent: 'flex-start',
                    background: focusedIndex === index ? `${theme.accent}20` : undefined,
                    border: focusedIndex === index ? `2px solid ${theme.accent}` : undefined
                  }}
                >
                  <div style={{
                    display: 'flex',
                    alignItems: 'center',
                    gap: '16px',
                    width: '100%'
                  }}>
                    <div style={{
                      fontSize: '36px',
                      minWidth: '54px',
                      textAlign: 'center'
                    }}>
                      {category.icon}
                    </div>
                    <div style={{ flex: 1 }}>
                      <h3 style={{
                        ...typography.presets.heading.h4,
                        color: currentCategory === category.id ? 'white' : typography.colors.primary,
                        margin: '0 0 6px 0'
                      }}>
                        {category.name}
                      </h3>
                      <p style={{
                        ...typography.presets.body.small,
                        color: currentCategory === category.id ? 'rgba(255,255,255,0.8)' : typography.colors.secondary,
                        margin: 0,
                        lineHeight: typography.lineHeight.normal
                      }}>
                        {category.description}
                      </p>
                    </div>
                    {currentCategory === category.id && (
                      <div style={{
                        color: 'white',
                        fontSize: '24px',
                        fontWeight: typography.fontWeight.bold
                      }}>
                        
                      </div>
                    )}
                  </div>
                </EnhancedButton>
              ))}
            </div>

            {/*   */}
            <div style={{
              marginTop: '28px',
              textAlign: 'center',
              borderTop: `1px solid ${theme.border}`,
              paddingTop: '20px'
            }}>
              <div style={{
                ...typography.presets.caption,
                color: typography.colors.muted,
                marginBottom: '8px'
              }}>
                  
              </div>
              <div style={{
                ...typography.presets.body.xs,
                color: typography.colors.muted,
                lineHeight: typography.lineHeight.relaxed
              }}>
                  | Enter  | Esc  | M 
              </div>
            </div>
          </div>
        </div>
      )}

      <style jsx>{`
        @keyframes fadeIn {
          from { opacity: 0; }
          to { opacity: 1; }
        }
        
        @keyframes slideUp {
          from { 
            opacity: 0;
            transform: translateY(30px);
          }
          to { 
            opacity: 1;
            transform: translateY(0);
          }
        }
      `}</style>
    </>
  );
};

export default CategoryNavigation; 