import React, { useState, useEffect } from 'react';
import freeUSStockAPI from '../services/freeUSStockAPI';

const SimpleTOSTable = ({ darkMode = false, watchlist = ['UAVS', 'AAPL', 'TSLA', 'NVDA', 'MSFT', 'GOOGL', 'META'] }) => {
  const [stockData, setStockData] = useState({});
  const [loading, setLoading] = useState(true);
  const [lastUpdate, setLastUpdate] = useState(new Date());
  const [selectedStocks, setSelectedStocks] = useState(new Set());

  const theme = {
    bg: darkMode ? '#1a1a1a' : '#ffffff',
    cardBg: darkMode ? '#2d2d2d' : '#f8f9fa',
    text: darkMode ? '#ffffff' : '#333333',
    subtext: darkMode ? '#cccccc' : '#666666',
    border: darkMode ? '#404040' : '#e0e0e0',
    positive: '#00c851',
    negative: '#ff4444',
    neutral: darkMode ? '#999999' : '#666666'
  };

  //   
  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);
      try {
        const promises = watchlist.map(symbol => freeUSStockAPI.getUSStockData(symbol));
        const results = await Promise.all(promises);
        
        const dataMap = {};
        results.forEach(data => {
          dataMap[data.symbol] = data;
        });
        
        setStockData(dataMap);
        setLastUpdate(new Date());
      } catch (error) {
        console.error('  :', error);
      } finally {
        setLoading(false);
      }
    };

    fetchData();
    const interval = setInterval(fetchData, 5000); // 5 

    return () => clearInterval(interval);
  }, [watchlist]);

  const formatNumber = (num, decimals = 2) => {
    if (num === null || num === undefined) return 'N/A';
    return Number(num).toLocaleString('en-US', { 
      minimumFractionDigits: decimals, 
      maximumFractionDigits: decimals 
    });
  };

  const formatVolume = (volume) => {
    if (!volume) return 'N/A';
    if (volume >= 1e6) return `${(volume / 1e6).toFixed(1)}M`;
    if (volume >= 1e3) return `${(volume / 1e3).toFixed(1)}K`;
    return volume.toLocaleString();
  };

  const getPriceColor = (change) => {
    if (change > 0) return theme.positive;
    if (change < 0) return theme.negative;
    return theme.neutral;
  };

  if (loading && Object.keys(stockData).length === 0) {
    return (
      <div style={{
        background: theme.cardBg,
        borderRadius: '8px',
        border: `1px solid ${theme.border}`,
        padding: '20px',
        textAlign: 'center'
      }}>
        <div style={{ color: theme.text, fontSize: '16px', marginBottom: '10px' }}>
           TOS    ...
        </div>
        <div style={{ color: theme.subtext, fontSize: '14px' }}>
              
        </div>
      </div>
    );
  }

  return (
    <div style={{
      background: theme.bg,
      borderRadius: '8px',
      border: `1px solid ${theme.border}`,
      overflow: 'hidden',
      marginBottom: '20px'
    }}>
      {/*  */}
      <div style={{
        background: theme.cardBg,
        padding: '15px 20px',
        borderBottom: `1px solid ${theme.border}`,
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center'
      }}>
        <div style={{ display: 'flex', alignItems: 'center', gap: '10px' }}>
          <h3 style={{ margin: 0, color: theme.text, fontSize: '18px' }}>
             TOS  
          </h3>
          <span style={{ 
            background: theme.positive, 
            color: 'white', 
            padding: '2px 8px', 
            borderRadius: '12px', 
            fontSize: '12px' 
          }}>
            
          </span>
        </div>
        <div style={{ color: theme.subtext, fontSize: '12px' }}>
           : {lastUpdate.toLocaleTimeString()}
        </div>
      </div>

      {/*  */}
      <div style={{ overflowX: 'auto' }}>
        <table style={{ width: '100%', borderCollapse: 'collapse' }}>
          <thead>
            <tr style={{ background: theme.cardBg }}>
              <th style={{ padding: '12px', textAlign: 'left', color: theme.text, fontWeight: '600', fontSize: '14px' }}></th>
              <th style={{ padding: '12px', textAlign: 'left', color: theme.text, fontWeight: '600', fontSize: '14px' }}></th>
              <th style={{ padding: '12px', textAlign: 'right', color: theme.text, fontWeight: '600', fontSize: '14px' }}></th>
              <th style={{ padding: '12px', textAlign: 'right', color: theme.text, fontWeight: '600', fontSize: '14px' }}></th>
              <th style={{ padding: '12px', textAlign: 'right', color: theme.text, fontWeight: '600', fontSize: '14px' }}></th>
              <th style={{ padding: '12px', textAlign: 'right', color: theme.text, fontWeight: '600', fontSize: '14px' }}></th>
              <th style={{ padding: '12px', textAlign: 'center', color: theme.text, fontWeight: '600', fontSize: '14px' }}></th>
            </tr>
          </thead>
          <tbody>
            {watchlist.map(symbol => {
              const stock = stockData[symbol];
              if (!stock) return null;

              return (
                <tr 
                  key={symbol} 
                  style={{ 
                    borderBottom: `1px solid ${theme.border}`,
                    transition: 'background-color 0.2s'
                  }}
                  onMouseEnter={(e) => e.target.parentElement.style.background = theme.cardBg}
                  onMouseLeave={(e) => e.target.parentElement.style.background = 'transparent'}
                >
                  <td style={{ padding: '12px' }}>
                    <div style={{ 
                      display: 'flex', 
                      alignItems: 'center', 
                      gap: '8px',
                      cursor: 'pointer'
                    }}
                    onClick={() => {
                      const newSelected = new Set(selectedStocks);
                      if (newSelected.has(stock.symbol)) {
                        newSelected.delete(stock.symbol);
                      } else {
                        newSelected.add(stock.symbol);
                      }
                      setSelectedStocks(newSelected);
                    }}>
                      <input
                        type="checkbox"
                        checked={selectedStocks.has(stock.symbol)}
                        onChange={() => {}}
                        style={{ marginRight: '4px' }}
                      />
                      <div style={{ 
                        color: '#007bff', 
                        fontWeight: '600', 
                        fontSize: '16px',
                        background: selectedStocks.has(stock.symbol) ? theme.accent + '20' : 'transparent',
                        padding: '4px 8px',
                        borderRadius: '6px',
                        border: `2px solid ${selectedStocks.has(stock.symbol) ? theme.accent : 'transparent'}`
                      }}>
                                                {stock.symbol}
                         {stock.symbol === 'UAVS' ? ' ' : stock.symbol.includes('EAGLE') || stock.symbol === 'ACEL' || stock.symbol === 'AEGL' ? ' ' : ''}
                      </div>
                    </div>
                  </td>
                  <td style={{ padding: '12px' }}>
                    <div style={{ color: theme.text, fontSize: '13px' }}>
                      {stock.name}
                    </div>
                    <div style={{ color: theme.subtext, fontSize: '11px' }}>
                      {stock.source}
                    </div>
                  </td>
                  <td style={{ padding: '12px', textAlign: 'right' }}>
                    <div style={{ color: theme.text, fontWeight: '600', fontSize: '14px' }}>
                      ${formatNumber(stock.price)}
                    </div>
                  </td>
                  <td style={{ padding: '12px', textAlign: 'right' }}>
                    <div style={{ 
                      color: getPriceColor(stock.change), 
                      fontWeight: '600', 
                      fontSize: '13px' 
                    }}>
                      {stock.change > 0 ? '+' : ''}{formatNumber(stock.change)}
                    </div>
                  </td>
                  <td style={{ padding: '12px', textAlign: 'right' }}>
                    <div style={{ 
                      color: getPriceColor(stock.changePercent), 
                      fontWeight: '600', 
                      fontSize: '13px' 
                    }}>
                      {stock.changePercent > 0 ? '+' : ''}{formatNumber(stock.changePercent, 2)}%
                    </div>
                  </td>
                  <td style={{ padding: '12px', textAlign: 'right' }}>
                    <div style={{ color: theme.text, fontSize: '13px' }}>
                      {formatVolume(stock.volume)}
                    </div>
                  </td>
                  <td style={{ padding: '12px', textAlign: 'center' }}>
                    <span style={{
                      display: 'inline-block',
                      width: '8px',
                      height: '8px',
                      borderRadius: '50%',
                      background: stock.isRealTime ? theme.positive : '#ffbb33'
                    }}></span>
                  </td>
                </tr>
              );
            })}
          </tbody>
        </table>
      </div>

      {/*   */}
      <div style={{
        background: theme.cardBg,
        padding: '10px 20px',
        borderTop: `1px solid ${theme.border}`,
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
        fontSize: '12px',
        color: theme.subtext
      }}>
        <div>
           <strong>  (UAVS)</strong>    
          {selectedStocks.size > 0 && (
            <span style={{ 
              marginLeft: '10px', 
              background: theme.accent, 
              color: 'white', 
              padding: '2px 6px', 
              borderRadius: '8px' 
            }}>
              {selectedStocks.size} 
            </span>
          )}
        </div>
        <div>
           5      TOS 
        </div>
      </div>
    </div>
  );
};

export default SimpleTOSTable; 