import React, { useState, useEffect, useRef, useCallback, useMemo } from 'react';
import { useTheme } from '../hooks/useTheme';
import { useTypography } from '../hooks/useTypography';
import { useLocalStorage } from '../hooks/useLocalStorage';

//   
const GUIDE_STEPS = {
  onboarding: [
    {
      id: 'welcome',
      title: '     !',
      content: '    AI    .',
      target: null,
      position: 'center'
    },
    {
      id: 'categories',
      title: '  ',
      content: '         .',
      target: '[data-guide="category-button"]',
      position: 'bottom-left'
    },
    {
      id: 'keyword-discovery',
      title: '   ',
      content: '      .',
      target: '[data-guide="keyword-discovery"]',
      position: 'bottom'
    },
    {
      id: 'ai-mentor',
      title: ' AI  ',
      content: '  AI    .',
      target: '[data-guide="ai-mentor"]',
      position: 'top-left'
    }
  ],

  keywordFlow: [
    {
      id: 'brainstorming-start',
      title: '  ',
      content: 'AI         .',
      target: '[data-guide="ai-prompts-toggle"]',
      position: 'bottom'
    },
    {
      id: 'keyword-input',
      title: '  ',
      content: '     .',
      target: '[data-guide="keyword-input"]',
      position: 'top'
    },
    {
      id: 'analysis-start',
      title: '  ',
      content: '     .',
      target: '[data-guide="start-analysis"]',
      position: 'top'
    }
  ],

  discoveryFlow: [
    {
      id: 'filters',
      title: '  ',
      content: ', ,       .',
      target: '[data-guide="filter-options"]',
      position: 'right'
    },
    {
      id: 'sorting',
      title: '  ',
      content: ' ,       .',
      target: '[data-guide="sort-options"]',
      position: 'bottom'
    },
    {
      id: 'stock-tracking',
      title: '  ',
      content: '       .',
      target: '[data-guide="track-button"]',
      position: 'left'
    }
  ]
};

//  
const TOOLTIPS = {
  memeScore: {
    title: '  ?',
    content: ' ,  ,    .     .',
    warning: '    !'
  },
  quantScore: {
    title: '  ?',
    content: 'PER, PBR, ROE    .    .',
    tip: '    .'
  },
  pennyStock: {
    title: ' ?',
    content: '   .         .',
    warning: '   10%  !'
  },
  volumeSpike: {
    title: '  ',
    content: '   .      .',
    tip: '      .'
  }
};

const InteractiveGuide = ({ 
  darkMode = false, 
  currentSection = 'general',
  showOnboarding = false,
  onComplete 
}) => {
  const theme = useTheme(darkMode);
  const typography = useTypography(darkMode);
  
  const [currentStep, setCurrentStep] = useState(0);
  const [activeTooltip, setActiveTooltip] = useState(null);
  const [tooltipPosition, setTooltipPosition] = useState({ x: 0, y: 0 });
  const [isGuideActive, setIsGuideActive] = useState(false);
  const [completedGuides, setCompletedGuides] = useLocalStorage('completed_guides', []);
  const [showHints, setShowHints] = useLocalStorage('show_hints', true);
  const [keyboardShortcuts, setKeyboardShortcuts] = useState(false);
  
  const overlayRef = useRef(null);
  const tooltipRef = useRef(null);

  //   
  const currentGuide = useMemo(() => {
    if (showOnboarding && !completedGuides.includes('onboarding')) {
      return GUIDE_STEPS.onboarding;
    }
    
    switch (currentSection) {
      case 'brainstorming':
        return GUIDE_STEPS.keywordFlow;
      case 'discovery':
        return GUIDE_STEPS.discoveryFlow;
      default:
        return [];
    }
  }, [showOnboarding, currentSection, completedGuides]);

  //  
  const startGuide = useCallback(() => {
    if (currentGuide.length > 0) {
      setCurrentStep(0);
      setIsGuideActive(true);
    }
  }, [currentGuide]);

  //  
  const nextStep = useCallback(() => {
    if (currentStep < currentGuide.length - 1) {
      setCurrentStep(prev => prev + 1);
    } else {
      completeGuide();
    }
  }, [currentStep, currentGuide.length]);

  //  
  const prevStep = useCallback(() => {
    if (currentStep > 0) {
      setCurrentStep(prev => prev - 1);
    }
  }, [currentStep]);

  //  
  const completeGuide = useCallback(() => {
    setIsGuideActive(false);
    setCurrentStep(0);
    
    const guideType = showOnboarding ? 'onboarding' : currentSection;
    if (!completedGuides.includes(guideType)) {
      setCompletedGuides(prev => [...prev, guideType]);
    }
    
    onComplete?.();
  }, [showOnboarding, currentSection, completedGuides, setCompletedGuides, onComplete]);

  //  
  const skipGuide = useCallback(() => {
    completeGuide();
  }, [completeGuide]);

  //   
  const highlightTarget = useCallback((targetSelector) => {
    if (!targetSelector) return null;
    
    const element = document.querySelector(targetSelector);
    if (!element) return null;
    
    const rect = element.getBoundingClientRect();
    return {
      top: rect.top,
      left: rect.left,
      width: rect.width,
      height: rect.height
    };
  }, []);

  //  
  const showTooltip = useCallback((tooltipId, event) => {
    const tooltip = TOOLTIPS[tooltipId];
    if (!tooltip) return;
    
    setActiveTooltip({ ...tooltip, id: tooltipId });
    setTooltipPosition({
      x: event.clientX,
      y: event.clientY
    });
  }, []);

  //  
  const hideTooltip = useCallback(() => {
    setActiveTooltip(null);
  }, []);

  //   
  useEffect(() => {
    const handleKeyDown = (event) => {
      if (!isGuideActive) return;
      
      switch (event.key) {
        case 'ArrowRight':
        case 'Space':
          event.preventDefault();
          nextStep();
          break;
        case 'ArrowLeft':
          event.preventDefault();
          prevStep();
          break;
        case 'Escape':
          event.preventDefault();
          skipGuide();
          break;
        case '?':
          event.preventDefault();
          setKeyboardShortcuts(!keyboardShortcuts);
          break;
      }
    };

    window.addEventListener('keydown', handleKeyDown);
    return () => window.removeEventListener('keydown', handleKeyDown);
  }, [isGuideActive, nextStep, prevStep, skipGuide, keyboardShortcuts]);

  //    ( )
  useEffect(() => {
    if (showOnboarding && !completedGuides.includes('onboarding')) {
      setTimeout(() => startGuide(), 1000);
    }
  }, [showOnboarding, completedGuides, startGuide]);

  const currentStepData = currentGuide[currentStep];
  const targetHighlight = currentStepData?.target ? highlightTarget(currentStepData.target) : null;

  return (
    <>
      {/*   */}
      {isGuideActive && (
        <div
          ref={overlayRef}
          style={{
            position: 'fixed',
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            background: 'rgba(0, 0, 0, 0.7)',
            zIndex: 9999,
            pointerEvents: 'auto'
          }}
          onClick={(e) => {
            if (e.target === overlayRef.current) {
              skipGuide();
            }
          }}
        >
          {/*   */}
          {targetHighlight && (
            <div
              style={{
                position: 'absolute',
                top: targetHighlight.top - 4,
                left: targetHighlight.left - 4,
                width: targetHighlight.width + 8,
                height: targetHighlight.height + 8,
                border: `3px solid ${theme.accent}`,
                borderRadius: '8px',
                boxShadow: `0 0 0 9999px rgba(0, 0, 0, 0.7)`,
                pointerEvents: 'none',
                animation: 'pulse 2s infinite'
              }}
            />
          )}

          {/*   */}
          {currentStepData && (
            <div
              style={{
                position: 'absolute',
                top: targetHighlight ? 
                  (currentStepData.position?.includes('top') ? targetHighlight.top - 280 : targetHighlight.top + targetHighlight.height + 20) :
                  '50%',
                left: targetHighlight ?
                  (currentStepData.position?.includes('left') ? targetHighlight.left - 320 : targetHighlight.left) :
                  '50%',
                transform: !targetHighlight ? 'translate(-50%, -50%)' : 'none',
                width: '300px',
                background: theme.cardBg,
                borderRadius: '12px',
                border: `2px solid ${theme.accent}`,
                boxShadow: theme.shadows.xl,
                padding: '20px',
                fontFamily: typography.fontFamily.primary
              }}
            >
              {/*  */}
              <div style={{ marginBottom: '16px' }}>
                <h3 style={{
                  ...typography.presets.heading.h4,
                  color: typography.colors.primary,
                  margin: '0 0 8px 0'
                }}>
                  {currentStepData.title}
                </h3>
                <div style={{
                  ...typography.presets.caption,
                  color: typography.colors.muted
                }}>
                   {currentStep + 1} / {currentGuide.length}
                </div>
              </div>

              {/*  */}
              <div style={{
                ...typography.presets.body.normal,
                color: typography.colors.secondary,
                lineHeight: typography.lineHeight.relaxed,
                marginBottom: '20px'
              }}>
                {currentStepData.content}
              </div>

              {/*   */}
              <div style={{
                width: '100%',
                height: '4px',
                background: theme.border,
                borderRadius: '2px',
                marginBottom: '16px',
                overflow: 'hidden'
              }}>
                <div style={{
                  width: `${((currentStep + 1) / currentGuide.length) * 100}%`,
                  height: '100%',
                  background: theme.gradients.success,
                  transition: 'width 0.3s ease'
                }} />
              </div>

              {/*  */}
              <div style={{ display: 'flex', justifyContent: 'space-between', gap: '8px' }}>
                <button
                  onClick={skipGuide}
                  style={{
                    ...typography.presets.button.small,
                    background: 'transparent',
                    color: typography.colors.muted,
                    border: `1px solid ${theme.border}`,
                    padding: '8px 16px',
                    borderRadius: '6px',
                    cursor: 'pointer'
                  }}
                >
                  
                </button>
                
                <div style={{ display: 'flex', gap: '8px' }}>
                  {currentStep > 0 && (
                    <button
                      onClick={prevStep}
                      style={{
                        ...typography.presets.button.small,
                        background: theme.accent,
                        color: 'white',
                        border: 'none',
                        padding: '8px 16px',
                        borderRadius: '6px',
                        cursor: 'pointer'
                      }}
                    >
                      
                    </button>
                  )}
                  
                  <button
                    onClick={nextStep}
                    style={{
                      ...typography.presets.button.small,
                      background: theme.positive,
                      color: 'white',
                      border: 'none',
                      padding: '8px 16px',
                      borderRadius: '6px',
                      cursor: 'pointer'
                    }}
                  >
                    {currentStep === currentGuide.length - 1 ? '' : ''}
                  </button>
                </div>
              </div>

              {/*    */}
              <div style={{
                ...typography.presets.caption,
                color: typography.colors.muted,
                textAlign: 'center',
                marginTop: '12px',
                borderTop: `1px solid ${theme.border}`,
                paddingTop: '12px'
              }}>
                 :    | Space  | Esc  | ? 
              </div>
            </div>
          )}
        </div>
      )}

      {/*  */}
      {activeTooltip && (
        <div
          ref={tooltipRef}
          style={{
            position: 'fixed',
            top: tooltipPosition.y - 10,
            left: tooltipPosition.x + 10,
            width: '280px',
            background: theme.cardBg,
            border: `1px solid ${theme.border}`,
            borderRadius: '8px',
            boxShadow: theme.shadows.md,
            padding: '12px',
            zIndex: 10000,
            fontFamily: typography.fontFamily.primary,
            pointerEvents: 'none'
          }}
        >
          <h4 style={{
            ...typography.presets.heading.h4,
            color: typography.colors.primary,
            margin: '0 0 8px 0'
          }}>
            {activeTooltip.title}
          </h4>
          
          <div style={{
            ...typography.presets.body.small,
            color: typography.colors.secondary,
            lineHeight: typography.lineHeight.normal,
            marginBottom: activeTooltip.warning || activeTooltip.tip ? '8px' : '0'
          }}>
            {activeTooltip.content}
          </div>

          {activeTooltip.warning && (
            <div style={{
              ...typography.presets.body.xs,
              color: typography.colors.error,
              background: `${theme.negative}20`,
              padding: '6px 8px',
              borderRadius: '4px',
              marginBottom: activeTooltip.tip ? '8px' : '0'
            }}>
              {activeTooltip.warning}
            </div>
          )}

          {activeTooltip.tip && (
            <div style={{
              ...typography.presets.body.xs,
              color: typography.colors.info,
              background: `${theme.accent}20`,
              padding: '6px 8px',
              borderRadius: '4px'
            }}>
              {activeTooltip.tip}
            </div>
          )}
        </div>
      )}

      {/*    () */}
      {!isGuideActive && currentGuide.length > 0 && !completedGuides.includes(currentSection) && (
        <button
          onClick={startGuide}
          style={{
            position: 'fixed',
            bottom: '100px',
            right: '20px',
            background: theme.gradients.purple,
            color: 'white',
            border: 'none',
            borderRadius: '50%',
            width: '56px',
            height: '56px',
            cursor: 'pointer',
            boxShadow: theme.shadows.lg,
            fontSize: '24px',
            zIndex: 1000,
            animation: 'bounce 2s infinite'
          }}
          title=" "
        >
          
        </button>
      )}

      {/*    */}
      <button
        onClick={() => setShowHints(!showHints)}
        style={{
          position: 'fixed',
          top: '20px',
          right: '20px',
          background: showHints ? theme.positive : theme.border,
          color: showHints ? 'white' : theme.text,
          border: 'none',
          borderRadius: '20px',
          padding: '8px 12px',
          cursor: 'pointer',
          fontSize: '12px',
          fontWeight: '600',
          zIndex: 1000
        }}
      >
        {showHints ? '  ON' : '  OFF'}
      </button>

      {/* CSS  */}
      <style jsx>{`
        @keyframes pulse {
          0% { opacity: 1; }
          50% { opacity: 0.5; }
          100% { opacity: 1; }
        }
        
        @keyframes bounce {
          0%, 20%, 50%, 80%, 100% { transform: translateY(0); }
          40% { transform: translateY(-10px); }
          60% { transform: translateY(-5px); }
        }
      `}</style>
    </>
  );
};

//      HOC
export const withTooltip = (WrappedComponent) => {
  return function WithTooltipComponent(props) {
    const [showTooltip, setShowTooltip] = useState(false);
    
    return (
      <div
        onMouseEnter={(e) => {
          if (props.tooltip) {
            //   
            setShowTooltip(true);
          }
        }}
        onMouseLeave={() => setShowTooltip(false)}
        data-guide={props['data-guide']}
      >
        <WrappedComponent {...props} />
      </div>
    );
  };
};

export { TOOLTIPS };
export default InteractiveGuide; 