import React from 'react';

const picks = [
  { name: '', reason: ' ,  ' },
  { name: '', reason: 'AI  ' },
  { name: '', reason: '   ' },
];

function TodayPicks() {
  return (
    <div style={{ margin: '1.5rem 0' }}>
      <h3>  </h3>
      <ul>
        {picks.map((pick, idx) => (
          <li key={idx} style={{ marginBottom: '0.5rem' }}>
            <strong>{pick.name}</strong> - {pick.reason}
          </li>
        ))}
      </ul>
    </div>
  );
}

export default TodayPicks; 