import React, { useState, useEffect, useCallback, useMemo } from 'react';
import { useTheme } from '../hooks/useTheme';
import { useTypography } from '../hooks/useTypography';
import { useLocalStorage } from '../hooks/useLocalStorage';
import ChaessaemEmoji from './ChaessaemEmoji';

//   
const MENTOR_ADVICE = {
  //   
  brainstorming: {
    beginner: [
      {
        title: "   ",
        content: "!   '?'  .       .    ,    ?",
        tip: "  :    .     ,    .",
        level: "beginner"
      },
      {
        title: "  ",
        content: "AI    !     ,     . ' '        .",
        tip: "  :     20%  .      .",
        level: "intermediate"
      }
    ],
    intermediate: [
      {
        title: "    ",
        content: "       ! ' +  + '     .",
        tip: "  :       . 2022      .",
        level: "intermediate"
      }
    ]
  },

  //   
  discovery: {
    beginner: [
      {
        title: "  ",
        content: "  !  20   .  ''   .   .",
        tip: "  :    10%  .        .",
        level: "beginner"
      },
      {
        title: "  ",
        content: "  ! 2021   ?   10 ,   90%  .",
        tip: "  :       50%  .    .",
        level: "beginner"
      }
    ],
    intermediate: [
      {
        title: "   ",
        content: "   !  . PER, PBR       .   .",
        tip: "  :    . IT PER 30 ,     .",
        level: "intermediate"
      }
    ]
  },

  //   
  emotional: {
    beginner: [
      {
        title: "   ",
        content: "     !  25   ,    .    .",
        tip: " :   '   ?' .  'NO'   .",
        level: "beginner"
      },
      {
        title: "  ",
        content: "  !  .            .",
        tip: "  : 3     .       .",
        level: "beginner"
      }
    ]
  },

  //  
  general: [
    {
      title: "   ",
      content: "25   ,     3 : 1)  2)  3)  ",
      tip: "  : '    ', '  ', '   '",
      level: "universal"
    },
    {
      title: "   ",
      content: "    . '    ,     .'        .",
      tip: "  :      ,       .",
      level: "universal"
    }
  ]
};

//   
const CONTEXTUAL_GUIDES = {
  firstVisit: {
    title: "     !",
    content: "!  25   .    .  '   '   .",
    steps: [
      "1     ",
      "2 AI    ", 
      "3    ",
      "4     "
    ]
  },
  
  keywordEmpty: {
    title: "    ?",
    content: "!   .      .",
    suggestions: [
      "  : 'AI', '', ''",
      "  : '', '', ''",
      "  : '', '', 'VR'",
      "  : '', '', ''"
    ]
  },

  memeStockWarning: {
    title: "     !",
    content: "    .     .",
    warnings: [
      "  :  50% ",
      "  :   ",
      " :   ",
      "  :   5%  "
    ]
  }
};

const AIStockMentor = ({ 
  darkMode = false, 
  currentSection = 'general',
  userLevel = 'beginner',
  keywords = [],
  selectedStock = null,
  isFirstVisit = false 
}) => {
  const theme = useTheme(darkMode);
  const typography = useTypography(darkMode);
  const [currentAdvice, setCurrentAdvice] = useState(null);
  const [showGuide, setShowGuide] = useState(false);
  const [mentorPersonality, setMentorPersonality] = useLocalStorage('mentor_personality', 'wise');
  const [userProgress, setUserProgress] = useLocalStorage('user_progress', {
    brainstorming: 0,
    discovery: 0,
    emotional: 0,
    overall: 0
  });

  //   
  const mentorStyles = {
    wise: {
      name: " ",
      avatar: "",
      tone: "  ",
      greeting: "25   "
    },
    friendly: {
      name: " ",
      avatar: "",
      tone: " ",
      greeting: "  !"
    },
    analytical: {
      name: " ",
      avatar: "",
      tone: "  ",
      greeting: " "
    }
  };

  const currentMentor = mentorStyles[mentorPersonality];

  //    
  const getContextualAdvice = useCallback(() => {
    //   
    if (isFirstVisit) {
      return CONTEXTUAL_GUIDES.firstVisit;
    }

    //   
    if (currentSection === 'brainstorming' && keywords.length === 0) {
      return CONTEXTUAL_GUIDES.keywordEmpty;
    }

    //   
    if (currentSection === 'discovery' && keywords.some(k => 
      ['', 'meme', '', ''].some(meme => k.toLowerCase().includes(meme.toLowerCase()))
    )) {
      return CONTEXTUAL_GUIDES.memeStockWarning;
    }

    //  
    const sectionAdvice = MENTOR_ADVICE[currentSection];
    if (sectionAdvice && sectionAdvice[userLevel]) {
      const adviceList = sectionAdvice[userLevel];
      return adviceList[Math.floor(Math.random() * adviceList.length)];
    }

    //  
    const generalAdvice = MENTOR_ADVICE.general;
    return generalAdvice[Math.floor(Math.random() * generalAdvice.length)];
  }, [currentSection, userLevel, keywords, isFirstVisit]);

  //  
  useEffect(() => {
    const advice = getContextualAdvice();
    setCurrentAdvice(advice);
  }, [getContextualAdvice]);

  //   
  const updateProgress = useCallback((section, increment = 10) => {
    setUserProgress(prev => ({
      ...prev,
      [section]: Math.min(100, prev[section] + increment),
      overall: Math.min(100, prev.overall + increment / 4)
    }));
  }, [setUserProgress]);

  //   
  const changeMentorPersonality = useCallback((personality) => {
    setMentorPersonality(personality);
  }, [setMentorPersonality]);

  if (!currentAdvice) return null;

  return (
    <div style={{
      position: 'fixed',
      bottom: '20px',
      right: '20px',
      width: '380px',
      maxHeight: '500px',
      background: theme.cardBg,
      borderRadius: '16px',
      border: `2px solid ${theme.accent}`,
      boxShadow: theme.shadows.xl,
      overflow: 'hidden',
      zIndex: 1000,
      fontFamily: typography.fontFamily.primary
    }}>
      {/*   */}
      <div style={{
        background: theme.gradients.ocean,
        padding: '16px',
        color: 'white',
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center'
      }}>
        <div style={{ display: 'flex', alignItems: 'center', gap: '12px' }}>
          <ChaessaemEmoji
            type="emotions"
            emotion={currentSection === 'discovery' ? 'excited' : 'confident'}
            size="normal"
            showMessage={false}
            darkMode={false}
          />
          <div>
            <div style={{ 
              ...typography.presets.body.small,
              fontWeight: typography.fontWeight.bold,
              marginBottom: '2px'
            }}>
               AI  
            </div>
            <div style={{ 
              ...typography.presets.caption,
              color: 'rgba(255,255,255,0.8)'
            }}>
              {currentMentor.greeting}
            </div>
          </div>
        </div>
        
        <div style={{ display: 'flex', gap: '8px' }}>
          {/*     */}
          <select
            value={mentorPersonality}
            onChange={(e) => changeMentorPersonality(e.target.value)}
            style={{
              background: 'rgba(255,255,255,0.2)',
              border: 'none',
              color: 'white',
              borderRadius: '6px',
              padding: '4px 8px',
              fontSize: '12px'
            }}
          >
            <option value="wise" style={{ color: '#333' }}> </option>
            <option value="friendly" style={{ color: '#333' }}> </option>
            <option value="analytical" style={{ color: '#333' }}> </option>
          </select>
          
          <button
            onClick={() => setShowGuide(!showGuide)}
            style={{
              background: 'rgba(255,255,255,0.2)',
              border: 'none',
              color: 'white',
              borderRadius: '6px',
              padding: '4px 8px',
              cursor: 'pointer',
              fontSize: '12px'
            }}
          >
            {showGuide ? '' : '?'}
          </button>
        </div>
      </div>

      {/*   */}
      <div style={{
        background: theme.bg,
        padding: '12px 16px',
        borderBottom: `1px solid ${theme.border}`
      }}>
        <div style={{ 
          display: 'flex', 
          justifyContent: 'space-between', 
          alignItems: 'center',
          marginBottom: '8px'
        }}>
          <span style={{ 
            ...typography.presets.label,
            color: typography.colors.secondary
          }}>
              
          </span>
          <span style={{ 
            ...typography.presets.body.small,
            color: typography.colors.accent,
            fontWeight: typography.fontWeight.semibold
          }}>
            {Math.round(userProgress.overall)}%
          </span>
        </div>
        <div style={{
          width: '100%',
          height: '6px',
          background: theme.border,
          borderRadius: '3px',
          overflow: 'hidden'
        }}>
          <div style={{
            width: `${userProgress.overall}%`,
            height: '100%',
            background: theme.gradients.success,
            transition: 'width 0.3s ease'
          }} />
        </div>
      </div>

      {/*   */}
      <div style={{
        padding: '20px',
        maxHeight: '300px',
        overflowY: 'auto'
      }}>
        {/*  */}
        <h3 style={{
          ...typography.presets.heading.h4,
          color: typography.colors.primary,
          margin: '0 0 12px 0'
        }}>
          {currentAdvice.title}
        </h3>

        {/*  */}
        <div style={{
          ...typography.presets.body.normal,
          color: typography.colors.secondary,
          lineHeight: typography.lineHeight.relaxed,
          marginBottom: '16px'
        }}>
          {currentAdvice.content}
        </div>

        {/*  */}
        {currentAdvice.tip && (
          <div style={{
            background: theme.gradients.warning,
            padding: '12px',
            borderRadius: '8px',
            marginBottom: '16px'
          }}>
            <div style={{
              ...typography.presets.body.small,
              color: 'white',
              lineHeight: typography.lineHeight.normal
            }}>
              {currentAdvice.tip}
            </div>
          </div>
        )}

        {/*   */}
        {currentAdvice.steps && (
          <div style={{ marginBottom: '16px' }}>
            <h4 style={{
              ...typography.presets.heading.h4,
              color: typography.colors.primary,
              marginBottom: '8px'
            }}>
                
            </h4>
            {currentAdvice.steps.map((step, index) => (
              <div
                key={index}
                style={{
                  ...typography.presets.body.small,
                  color: typography.colors.secondary,
                  marginBottom: '6px',
                  paddingLeft: '8px'
                }}
              >
                {step}
              </div>
            ))}
          </div>
        )}

        {/*  */}
        {currentAdvice.suggestions && (
          <div style={{ marginBottom: '16px' }}>
            <h4 style={{
              ...typography.presets.heading.h4,
              color: typography.colors.primary,
              marginBottom: '8px'
            }}>
                
            </h4>
            {currentAdvice.suggestions.map((suggestion, index) => (
              <div
                key={index}
                style={{
                  ...typography.presets.body.small,
                  color: typography.colors.secondary,
                  marginBottom: '4px',
                  paddingLeft: '8px'
                }}
              >
                {suggestion}
              </div>
            ))}
          </div>
        )}

        {/*  */}
        {currentAdvice.warnings && (
          <div style={{
            background: theme.gradients.danger,
            padding: '12px',
            borderRadius: '8px',
            marginBottom: '16px'
          }}>
            <h4 style={{
              ...typography.presets.heading.h4,
              color: 'white',
              marginBottom: '8px'
            }}>
               
            </h4>
            {currentAdvice.warnings.map((warning, index) => (
              <div
                key={index}
                style={{
                  ...typography.presets.body.small,
                  color: 'white',
                  marginBottom: '4px',
                  paddingLeft: '8px'
                }}
              >
                {warning}
              </div>
            ))}
          </div>
        )}

        {/*   */}
        <div style={{ display: 'flex', gap: '8px', marginTop: '16px' }}>
          <button
            onClick={() => updateProgress(currentSection, 5)}
            style={{
              ...typography.presets.button.small,
              background: theme.positive,
              color: 'white',
              border: 'none',
              padding: '8px 16px',
              borderRadius: '6px',
              cursor: 'pointer',
              flex: 1
            }}
          >
             
          </button>
          <button
            onClick={() => setCurrentAdvice(getContextualAdvice())}
            style={{
              ...typography.presets.button.small,
              background: theme.accent,
              color: 'white',
              border: 'none',
              padding: '8px 16px',
              borderRadius: '6px',
              cursor: 'pointer',
              flex: 1
            }}
          >
              
          </button>
        </div>
      </div>

      {/*   */}
      <div style={{
        background: theme.cardBg,
        padding: '12px 16px',
        borderTop: `1px solid ${theme.border}`,
        textAlign: 'center'
      }}>
        <div style={{
          ...typography.presets.caption,
          color: typography.colors.muted
        }}>
            : {currentSection} |  : {userLevel}
        </div>
      </div>
    </div>
  );
};

export default AIStockMentor; 