import React, { useState, useEffect, useRef, useCallback } from 'react';
import { useLocalStorage } from '../hooks/useLocalStorage';
import kisAPI from '../services/kisAPI';
import freeUSStockAPI from '../services/freeUSStockAPI';
import { getStockBySymbol } from '../data/stockMasterDB';

const StockAlertSystem = ({ darkMode = false, watchlist = [] }) => {
  const [alerts, setAlerts] = useLocalStorage('stock_alerts', []);
  const [realTimeData, setRealTimeData] = useState({});
  const [alertHistory, setAlertHistory] = useLocalStorage('alert_history', []);
  const [soundEnabled, setSoundEnabled] = useLocalStorage('alert_sound_enabled', true);
  const [notificationEnabled, setNotificationEnabled] = useLocalStorage('alert_notification_enabled', true);
  const [isMonitoring, setIsMonitoring] = useState(false);

  const monitoringInterval = useRef(null);
  const lastNotificationTime = useRef({});

  const theme = {
    bg: darkMode ? '#1a1a1a' : '#ffffff',
    cardBg: darkMode ? '#2d2d2d' : '#f8f9fa',
    text: darkMode ? '#ffffff' : '#333333',
    subtext: darkMode ? '#cccccc' : '#666666',
    border: darkMode ? '#404040' : '#e0e0e0',
    accent: '#007bff',
    positive: '#00c851',
    negative: '#ff4444',
    warning: '#ffbb33'
  };

  //   
  const alertTypes = [
    { value: 'price_above', label: ' ', icon: '' },
    { value: 'price_below', label: ' ', icon: '' },
    { value: 'change_above', label: ' +', icon: '' },
    { value: 'change_below', label: ' -', icon: '' },
    { value: 'volume_spike', label: ' ', icon: '' }
  ];

  //   
  const addAlert = (symbol, type, value, description = '') => {
    const stockInfo = getStockBySymbol(symbol);
    const newAlert = {
      id: Date.now().toString(),
      symbol,
      stockName: stockInfo?.name || symbol,
      type,
      value: parseFloat(value),
      description,
      isActive: true,
      createdAt: new Date().toISOString(),
      triggeredCount: 0
    };

    setAlerts(prev => [...prev, newAlert]);
    console.log(`  : ${symbol} ${type} ${value}`);
  };

  //  
  const removeAlert = (alertId) => {
    setAlerts(prev => prev.filter(alert => alert.id !== alertId));
  };

  //  /
  const toggleAlert = (alertId) => {
    setAlerts(prev => prev.map(alert => 
      alert.id === alertId 
        ? { ...alert, isActive: !alert.isActive }
        : alert
    ));
  };

  //   
  const startMonitoring = useCallback(async () => {
    if (!watchlist.length) return;

    setIsMonitoring(true);
    console.log(`   : ${watchlist.length} `);

    try {
      const promises = watchlist.map(async (symbol) => {
        try {
          const stockInfo = getStockBySymbol(symbol);
          let data;

          if (stockInfo?.country === 'KOR') {
            data = await kisAPI.getCurrentPrice(symbol, 'domestic');
          } else {
            data = await freeUSStockAPI.getUSStockData(symbol);
          }

          return { symbol, data, stockInfo };
        } catch (error) {
          console.warn(`${symbol}   :`, error);
          return { symbol, data: null, stockInfo: null };
        }
      });

      const results = await Promise.all(promises);
      const newRealTimeData = {};

      results.forEach(({ symbol, data, stockInfo }) => {
        if (data) {
          newRealTimeData[symbol] = {
            ...data,
            stockInfo,
            lastUpdated: new Date().toISOString()
          };
        }
      });

      setRealTimeData(prev => ({
        ...prev,
        ...newRealTimeData
      }));

      //  
      checkAlerts(newRealTimeData);

    } catch (error) {
      console.error(' :', error);
    } finally {
      setIsMonitoring(false);
    }
  }, [watchlist, alerts]);

  //   
  const checkAlerts = useCallback((currentData) => {
    const activeAlerts = alerts.filter(alert => alert.isActive);
    
    activeAlerts.forEach(alert => {
      const stockData = currentData[alert.symbol];
      if (!stockData) return;

      let shouldTrigger = false;
      let message = '';

      switch (alert.type) {
        case 'price_above':
          if (stockData.price >= alert.value) {
            shouldTrigger = true;
            message = `${alert.stockName}(${alert.symbol})  ${alert.value}${stockData.currency === 'USD' ? '$' : ''}  . : ${stockData.price}`;
          }
          break;

        case 'price_below':
          if (stockData.price <= alert.value) {
            shouldTrigger = true;
            message = `${alert.stockName}(${alert.symbol})  ${alert.value}${stockData.currency === 'USD' ? '$' : ''}  . : ${stockData.price}`;
          }
          break;

        case 'change_above':
          if (stockData.changePercent >= alert.value) {
            shouldTrigger = true;
            message = `${alert.stockName}(${alert.symbol})  +${alert.value}%  . : ${stockData.changePercent}%`;
          }
          break;

        case 'change_below':
          if (stockData.changePercent <= -alert.value) {
            shouldTrigger = true;
            message = `${alert.stockName}(${alert.symbol})  -${alert.value}%  . : ${stockData.changePercent}%`;
          }
          break;

        case 'volume_spike':
          //   alert.value  
          const avgVolume = stockData.volume / (alert.value || 2);
          if (stockData.volume >= avgVolume * alert.value) {
            shouldTrigger = true;
            message = `${alert.stockName}(${alert.symbol})  ${alert.value} . : ${stockData.volume?.toLocaleString()}`;
          }
          break;
      }

      if (shouldTrigger) {
        triggerAlert(alert, message, stockData);
      }
    });
  }, [alerts]);

  //  
  const triggerAlert = useCallback((alert, message, stockData) => {
    const now = Date.now();
    const lastTime = lastNotificationTime.current[alert.id] || 0;
    
    //      (5 )
    if (now - lastTime < 300000) return;

    lastNotificationTime.current[alert.id] = now;

    //   
    const alertRecord = {
      id: Date.now().toString(),
      alertId: alert.id,
      symbol: alert.symbol,
      message,
      stockData,
      timestamp: new Date().toISOString()
    };

    setAlertHistory(prev => [alertRecord, ...prev.slice(0, 99)]); //  100 

    //    
    setAlerts(prev => prev.map(a => 
      a.id === alert.id 
        ? { ...a, triggeredCount: (a.triggeredCount || 0) + 1 }
        : a
    ));

    console.log(`  : ${message}`);

    //  
    if (soundEnabled) {
      playAlertSound();
    }

    //  
    if (notificationEnabled && 'Notification' in window) {
      if (Notification.permission === 'granted') {
        new Notification(' ', {
          body: message,
          icon: '/favicon.ico',
          badge: '/favicon.ico'
        });
      } else if (Notification.permission !== 'denied') {
        Notification.requestPermission().then(permission => {
          if (permission === 'granted') {
            new Notification(' ', {
              body: message,
              icon: '/favicon.ico'
            });
          }
        });
      }
    }
  }, [soundEnabled, notificationEnabled, setAlertHistory, setAlerts]);

  //   
  const playAlertSound = () => {
    try {
      // Web Audio API   
      const audioContext = new (window.AudioContext || window.webkitAudioContext)();
      const oscillator = audioContext.createOscillator();
      const gainNode = audioContext.createGain();

      oscillator.connect(gainNode);
      gainNode.connect(audioContext.destination);

      oscillator.frequency.value = 800; // 800Hz
      oscillator.type = 'sine';

      gainNode.gain.setValueAtTime(0.3, audioContext.currentTime);
      gainNode.gain.exponentialRampToValueAtTime(0.001, audioContext.currentTime + 0.5);

      oscillator.start(audioContext.currentTime);
      oscillator.stop(audioContext.currentTime + 0.5);
    } catch (error) {
      console.warn('  :', error);
    }
  };

  //   
  useEffect(() => {
    if (watchlist.length > 0) {
      //   
      startMonitoring();

      //   (30 )
      monitoringInterval.current = setInterval(() => {
        startMonitoring();
      }, 30000);

      return () => {
        if (monitoringInterval.current) {
          clearInterval(monitoringInterval.current);
        }
      };
    }
  }, [watchlist, startMonitoring]);

  //    
  const AlertForm = ({ symbol }) => {
    const [alertType, setAlertType] = useState('price_above');
    const [alertValue, setAlertValue] = useState('');
    const [description, setDescription] = useState('');

    const handleSubmit = (e) => {
      e.preventDefault();
      if (alertValue && parseFloat(alertValue) > 0) {
        addAlert(symbol, alertType, alertValue, description);
        setAlertValue('');
        setDescription('');
      }
    };

    const stockInfo = getStockBySymbol(symbol);
    const currentData = realTimeData[symbol];

    return (
      <form onSubmit={handleSubmit} style={{
        padding: '15px',
        background: theme.cardBg,
        borderRadius: '6px',
        border: `1px solid ${theme.border}`,
        marginBottom: '10px'
      }}>
        <div style={{ marginBottom: '10px' }}>
          <strong style={{ color: theme.text }}>
            {stockInfo?.name || symbol} ({symbol})  
          </strong>
          {currentData && (
            <div style={{ color: theme.subtext, fontSize: '12px', marginTop: '4px' }}>
              : {currentData.price} {currentData.currency === 'USD' ? '$' : ''} 
              ({currentData.changePercent > 0 ? '+' : ''}{currentData.changePercent}%)
            </div>
          )}
        </div>

        <div style={{ display: 'flex', gap: '10px', marginBottom: '10px', flexWrap: 'wrap' }}>
          <select
            value={alertType}
            onChange={(e) => setAlertType(e.target.value)}
            style={{
              background: theme.bg,
              border: `1px solid ${theme.border}`,
              color: theme.text,
              padding: '6px 10px',
              borderRadius: '4px',
              fontSize: '12px'
            }}
          >
            {alertTypes.map(type => (
              <option key={type.value} value={type.value}>
                {type.icon} {type.label}
              </option>
            ))}
          </select>

          <input
            type="number"
            step="0.01"
            placeholder=" "
            value={alertValue}
            onChange={(e) => setAlertValue(e.target.value)}
            style={{
              background: theme.bg,
              border: `1px solid ${theme.border}`,
              color: theme.text,
              padding: '6px 10px',
              borderRadius: '4px',
              fontSize: '12px',
              width: '100px'
            }}
            required
          />

          <button
            type="submit"
            style={{
              background: theme.positive,
              color: 'white',
              border: 'none',
              padding: '6px 12px',
              borderRadius: '4px',
              cursor: 'pointer',
              fontSize: '12px'
            }}
          >
            
          </button>
        </div>

        <input
          type="text"
          placeholder=" ()"
          value={description}
          onChange={(e) => setDescription(e.target.value)}
          style={{
            width: '100%',
            background: theme.bg,
            border: `1px solid ${theme.border}`,
            color: theme.text,
            padding: '6px 10px',
            borderRadius: '4px',
            fontSize: '12px'
          }}
        />
      </form>
    );
  };

  return (
    <div style={{
      background: theme.bg,
      borderRadius: '8px',
      border: `1px solid ${theme.border}`,
      marginBottom: '20px'
    }}>
      {/*  */}
      <div style={{
        background: theme.cardBg,
        padding: '15px 20px',
        borderBottom: `1px solid ${theme.border}`,
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center'
      }}>
        <div style={{ display: 'flex', alignItems: 'center', gap: '10px' }}>
          <h3 style={{ margin: 0, color: theme.text, fontSize: '18px' }}>
               
          </h3>
          <span style={{
            background: isMonitoring ? theme.positive : theme.subtext,
            color: 'white',
            padding: '2px 8px',
            borderRadius: '12px',
            fontSize: '12px'
          }}>
            {isMonitoring ? ' ' : ' '}
          </span>
          <span style={{
            background: theme.accent,
            color: 'white',
            padding: '2px 8px',
            borderRadius: '12px',
            fontSize: '12px'
          }}>
            {alerts.filter(a => a.isActive).length} 
          </span>
        </div>

        <div style={{ display: 'flex', gap: '10px', alignItems: 'center' }}>
          <button
            onClick={() => setSoundEnabled(!soundEnabled)}
            style={{
              background: soundEnabled ? theme.positive : theme.subtext,
              color: 'white',
              border: 'none',
              padding: '6px 10px',
              borderRadius: '4px',
              cursor: 'pointer',
              fontSize: '12px'
            }}
            title=" "
          >
            {soundEnabled ? '' : ''}
          </button>

          <button
            onClick={() => setNotificationEnabled(!notificationEnabled)}
            style={{
              background: notificationEnabled ? theme.positive : theme.subtext,
              color: 'white',
              border: 'none',
              padding: '6px 10px',
              borderRadius: '4px',
              cursor: 'pointer',
              fontSize: '12px'
            }}
            title=" "
          >
            {notificationEnabled ? '' : ''}
          </button>
        </div>
      </div>

      {/*    */}
      <div style={{ padding: '20px' }}>
        {watchlist.length === 0 ? (
          <div style={{
            textAlign: 'center',
            color: theme.subtext,
            padding: '40px',
            fontSize: '14px'
          }}>
              .
            <br />
                  .
          </div>
        ) : (
          watchlist.map(symbol => (
            <AlertForm key={symbol} symbol={symbol} />
          ))
        )}

        {/*    */}
        {alerts.length > 0 && (
          <div style={{ marginTop: '20px' }}>
            <h4 style={{ color: theme.text, fontSize: '16px', marginBottom: '15px' }}>
                 ({alerts.length})
            </h4>
            <div style={{ maxHeight: '300px', overflowY: 'auto' }}>
              {alerts.map(alert => {
                const typeInfo = alertTypes.find(t => t.value === alert.type);
                const currentData = realTimeData[alert.symbol];
                
                return (
                  <div
                    key={alert.id}
                    style={{
                      background: theme.cardBg,
                      border: `1px solid ${alert.isActive ? theme.border : theme.subtext}`,
                      borderRadius: '6px',
                      padding: '12px',
                      marginBottom: '8px',
                      opacity: alert.isActive ? 1 : 0.6
                    }}
                  >
                    <div style={{
                      display: 'flex',
                      justifyContent: 'space-between',
                      alignItems: 'flex-start',
                      marginBottom: '8px'
                    }}>
                      <div>
                        <div style={{ color: theme.text, fontWeight: '600', fontSize: '14px' }}>
                          {typeInfo?.icon} {alert.stockName} ({alert.symbol})
                        </div>
                        <div style={{ color: theme.subtext, fontSize: '12px' }}>
                          {typeInfo?.label}: {alert.value}
                          {alert.type.includes('price') && (currentData?.currency === 'USD' ? '$' : '')}
                          {alert.type.includes('change') && '%'}
                          {alert.type === 'volume_spike' && ''}
                        </div>
                        {alert.description && (
                          <div style={{ color: theme.subtext, fontSize: '11px', marginTop: '2px' }}>
                             {alert.description}
                          </div>
                        )}
                        <div style={{ color: theme.subtext, fontSize: '11px', marginTop: '4px' }}>
                          : {new Date(alert.createdAt).toLocaleString()}  
                          : {alert.triggeredCount || 0}
                        </div>
                      </div>

                      <div style={{ display: 'flex', gap: '5px', flexShrink: 0 }}>
                        <button
                          onClick={() => toggleAlert(alert.id)}
                          style={{
                            background: alert.isActive ? theme.warning : theme.positive,
                            color: 'white',
                            border: 'none',
                            padding: '4px 8px',
                            borderRadius: '4px',
                            cursor: 'pointer',
                            fontSize: '11px'
                          }}
                        >
                          {alert.isActive ? '' : ''}
                        </button>
                        <button
                          onClick={() => removeAlert(alert.id)}
                          style={{
                            background: theme.negative,
                            color: 'white',
                            border: 'none',
                            padding: '4px 8px',
                            borderRadius: '4px',
                            cursor: 'pointer',
                            fontSize: '11px'
                          }}
                        >
                          
                        </button>
                      </div>
                    </div>

                    {/*   */}
                    {currentData && (
                      <div style={{
                        background: theme.bg,
                        padding: '8px',
                        borderRadius: '4px',
                        fontSize: '11px',
                        color: theme.subtext
                      }}>
                        : {currentData.price} ({currentData.changePercent > 0 ? '+' : ''}{currentData.changePercent}%)  
                        : {currentData.volume?.toLocaleString()}  
                        : {new Date(currentData.lastUpdated).toLocaleTimeString()}
                      </div>
                    )}
                  </div>
                );
              })}
            </div>
          </div>
        )}

        {/*    */}
        {alertHistory.length > 0 && (
          <div style={{ marginTop: '20px' }}>
            <h4 style={{ color: theme.text, fontSize: '16px', marginBottom: '15px' }}>
                  ({alertHistory.length})
            </h4>
            <div style={{ maxHeight: '200px', overflowY: 'auto' }}>
              {alertHistory.slice(0, 10).map(record => (
                <div
                  key={record.id}
                  style={{
                    background: theme.cardBg,
                    border: `1px solid ${theme.border}`,
                    borderRadius: '6px',
                    padding: '10px',
                    marginBottom: '6px'
                  }}
                >
                  <div style={{ color: theme.text, fontSize: '13px', marginBottom: '4px' }}>
                    {record.message}
                  </div>
                  <div style={{ color: theme.subtext, fontSize: '11px' }}>
                    {new Date(record.timestamp).toLocaleString()}
                  </div>
                </div>
              ))}
            </div>
          </div>
        )}
      </div>

      {/*   */}
      <div style={{
        background: theme.cardBg,
        padding: '10px 20px',
        borderTop: `1px solid ${theme.border}`,
        fontSize: '12px',
        color: theme.subtext,
        display: 'flex',
        justifyContent: 'space-between'
      }}>
        <div> 30  </div>
        <div>   +  </div>
      </div>
    </div>
  );
};

export default StockAlertSystem; 