import React, { useState, useRef, useEffect } from 'react';

// OpenAI API  
const callChatGPT = async (message, stock) => {
  //     API   
  const API_KEY = process.env.REACT_APP_OPENAI_API_KEY;
  
  if (!API_KEY) {
    // API     
    return getSampleResponse(message, stock);
  }

  try {
    const response = await fetch('https://api.openai.com/v1/chat/completions', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${API_KEY}`,
      },
      body: JSON.stringify({
        model: 'gpt-3.5-turbo',
        messages: [
          {
            role: 'system',
            content: `      AI. ${stock}      .   ,    .          .`
          },
          {
            role: 'user',
            content: message
          }
        ],
        max_tokens: 500,
        temperature: 0.7,
      }),
    });

    const data = await response.json();
    return data.choices[0].message.content;
  } catch (error) {
    console.error('ChatGPT API  :', error);
    return getSampleResponse(message, stock);
  }
};

//   
const getSampleResponse = (message, stock) => {
  //    
  if (message.includes('RSI')) {
    return `RSI  ""  ! \n\n **  RSI:**\nRSI 0~100   .   !\n\n 70   " !"  (,  )\n 30   " !"  (,  )\n 30~70  "!"  ( )\n\n **:** RSI    !    ! `;
  }
  
  if (message.includes('')) {
    return `   " "   ! \n\n ** :**\n    ,         !\n\n   ****    ! \n   ****    ! \n\n ** :**\n- 5, 20, 60   !\n-     ! `;
  }
  
  if (message.includes(' ')) {
    return `    " "  ! \n\n ** :**\n        !\n\n ****:   " !" \n ****:   " !" \n ****:   () \n\n ** :**\n-       \n-       \n\n  100%      !`;
  }
  
  if (message.includes('MACD')) {
    return `MACD  " "  ! \n\n ** :**\nMACD        !\n\n ** :**\n **MACD**:    \n ****:     \n ****:     \n\n ** :**\n- MACD  ****    ! \n- MACD  ****    ! \n\n     !  ,   !`;
  }

  const responses = [
    `!  ${stock}    ?       ! `,
    `${stock}   !       ! `,
    `      !       ! `,
    `!  !  RSI, , MACD     !        ! `,
    `${stock}   !       !        ! `
  ];
  
  return responses[Math.floor(Math.random() * responses.length)];
};

function AIChatSection({ stock, onAIAnswer, darkMode = false }) {
  const [messages, setMessages] = useState([]);
  const [input, setInput] = useState('');
  const [loading, setLoading] = useState(false);
  const messagesEndRef = useRef(null);

  const bg = darkMode ? '#23272b' : '#fff';
  const border = darkMode ? '#333' : '#eee';
  const text = darkMode ? '#e0e0e0' : '#222';
  const subtext = darkMode ? '#aaa' : '#888';
  const accent = '#8884d8';

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  const handleSend = async () => {
    if (!input.trim() || loading) return;

    const userMessage = {
      type: 'user',
      content: input,
      timestamp: new Date().toLocaleTimeString()
    };

    setMessages(prev => [...prev, userMessage]);
    setLoading(true);
    setInput('');

    try {
      const aiResponse = await callChatGPT(input, stock);
      
      const aiMessage = {
        type: 'ai',
        content: aiResponse,
        timestamp: new Date().toLocaleTimeString()
      };

      setMessages(prev => [...prev, aiMessage]);
      
      //   AI  
      if (onAIAnswer) {
        onAIAnswer({
          time: new Date().toLocaleString(),
          stock: stock,
          question: input,
          answer: aiResponse
        });
      }
    } catch (error) {
      const errorMessage = {
        type: 'ai',
        content: '!    .     !',
        timestamp: new Date().toLocaleTimeString()
      };
      setMessages(prev => [...prev, errorMessage]);
    } finally {
      setLoading(false);
    }
  };

  const handleKeyPress = (e) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSend();
    }
  };

  const quickQuestions = [
    `${stock}   ?`,
    `${stock}  `,
    `RSI  ?  `,
    `  ?`,
    `${stock}  ?`,
    `   `,
    `MACD    `,
    ` ${stock}  ?`
  ];

  return (
    <div style={{
      background: bg,
      border: `1px solid ${border}`,
      borderRadius: '20px',
      padding: '1.5rem',
      marginBottom: '1.5rem',
      boxShadow: darkMode ? '0 8px 32px rgba(0,0,0,0.3)' : '0 8px 32px rgba(0,0,0,0.1)',
      position: 'relative',
      overflow: 'hidden'
    }}>
      {/*    */}
      <div style={{
        position: 'absolute',
        top: '-30px',
        right: '-30px',
        width: '60px',
        height: '60px',
        background: 'linear-gradient(45deg, #74b9ff, #0984e3)',
        borderRadius: '50%',
        opacity: 0.1
      }} />

      {/*  */}
      <div style={{
        display: 'flex',
        alignItems: 'center',
        marginBottom: '1.5rem',
        position: 'relative',
        zIndex: 1
      }}>
        <div style={{
          fontSize: '2rem',
          marginRight: '1rem',
          animation: 'bounce 2s infinite'
        }}></div>
        <div>
          <h3 style={{
            margin: 0,
            color: accent,
            fontSize: '1.4rem',
            fontWeight: '800',
            background: `linear-gradient(45deg, ${accent}, #74b9ff)`,
            backgroundClip: 'text',
            WebkitBackgroundClip: 'text',
            WebkitTextFillColor: 'transparent'
          }}>
            AI   
          </h3>
          <p style={{
            margin: '0.25rem 0 0 0',
            color: subtext,
            fontSize: '0.9rem',
            fontStyle: 'italic'
          }}>
            {stock}    ! 
          </p>
        </div>
      </div>

      {/*    */}
      <div style={{
        height: '300px',
        overflowY: 'auto',
        marginBottom: '1rem',
        padding: '1rem',
        background: darkMode ? '#1a1a1a' : '#f8f9fa',
        borderRadius: '16px',
        border: `2px solid ${border}`
      }}>
        {messages.length === 0 ? (
          <div style={{
            textAlign: 'center',
            color: subtext,
            padding: '2rem',
            lineHeight: '1.6'
          }}>
            <div style={{ fontSize: '3rem', marginBottom: '1rem' }}></div>
            <div style={{ fontSize: '1.1rem', fontWeight: '600', marginBottom: '0.5rem' }}>
              ! ! 
            </div>
            <div style={{ fontSize: '0.9rem' }}>
              {stock}      !<br/>
                  ! 
            </div>
          </div>
        ) : (
          messages.map((message, index) => (
            <div
              key={index}
              style={{
                display: 'flex',
                justifyContent: message.type === 'user' ? 'flex-end' : 'flex-start',
                marginBottom: '1rem'
              }}
            >
              <div style={{
                maxWidth: '80%',
                padding: '0.8rem 1rem',
                borderRadius: '16px',
                background: message.type === 'user' 
                  ? `linear-gradient(135deg, ${accent}, #74b9ff)`
                  : (darkMode ? '#2a2a2a' : '#ffffff'),
                color: message.type === 'user' ? '#fff' : text,
                border: message.type === 'ai' ? `2px solid ${border}` : 'none',
                boxShadow: message.type === 'user' 
                  ? '0 4px 12px rgba(116, 185, 255, 0.3)'
                  : (darkMode ? '0 2px 8px rgba(0,0,0,0.2)' : '0 2px 8px rgba(0,0,0,0.1)'),
                position: 'relative'
              }}>
                {message.type === 'ai' && (
                  <div style={{
                    position: 'absolute',
                    top: '8px',
                    left: '-8px',
                    fontSize: '1rem'
                  }}></div>
                )}
                <div style={{ 
                  fontSize: '0.95rem', 
                  lineHeight: '1.5',
                  marginLeft: message.type === 'ai' ? '1rem' : '0'
                }}>
                  {message.content}
                </div>
                <div style={{
                  fontSize: '0.7rem',
                  opacity: 0.7,
                  marginTop: '0.5rem',
                  textAlign: message.type === 'user' ? 'right' : 'left',
                  marginLeft: message.type === 'ai' ? '1rem' : '0'
                }}>
                  {message.timestamp}
                </div>
              </div>
            </div>
          ))
        )}
        
        {loading && (
          <div style={{
            display: 'flex',
            justifyContent: 'flex-start',
            marginBottom: '1rem'
          }}>
            <div style={{
              padding: '0.8rem 1rem',
              borderRadius: '16px',
              background: darkMode ? '#2a2a2a' : '#ffffff',
              border: `2px solid ${border}`,
              position: 'relative'
            }}>
              <div style={{
                position: 'absolute',
                top: '8px',
                left: '-8px',
                fontSize: '1rem'
              }}></div>
              <div style={{ 
                marginLeft: '1rem',
                display: 'flex',
                alignItems: 'center',
                gap: '0.5rem',
                color: subtext
              }}>
                <div style={{
                  display: 'flex',
                  gap: '2px'
                }}>
                  <div style={{
                    width: '6px',
                    height: '6px',
                    borderRadius: '50%',
                    background: accent,
                    animation: 'bounce 1.4s infinite 0s'
                  }} />
                  <div style={{
                    width: '6px',
                    height: '6px',
                    borderRadius: '50%',
                    background: accent,
                    animation: 'bounce 1.4s infinite 0.2s'
                  }} />
                  <div style={{
                    width: '6px',
                    height: '6px',
                    borderRadius: '50%',
                    background: accent,
                    animation: 'bounce 1.4s infinite 0.4s'
                  }} />
                </div>
                 ...
              </div>
            </div>
          </div>
        )}
        <div ref={messagesEndRef} />
      </div>

      {/*    */}
      {messages.length === 0 && (
        <div style={{ marginBottom: '1rem' }}>
          <div style={{
            fontSize: '0.9rem',
            fontWeight: '600',
            color: text,
            marginBottom: '0.5rem',
            textAlign: 'center'
          }}>
              
          </div>
          <div style={{
            display: 'grid',
            gridTemplateColumns: 'repeat(auto-fit, minmax(200px, 1fr))',
            gap: '0.5rem'
          }}>
            {quickQuestions.map((question, index) => (
              <button
                key={index}
                onClick={() => setInput(question)}
                style={{
                  background: darkMode ? '#2a2a2a' : '#f8f9fa',
                  border: `2px solid ${border}`,
                  borderRadius: '12px',
                  padding: '0.6rem',
                  cursor: 'pointer',
                  fontSize: '0.8rem',
                  color: text,
                  transition: 'all 0.2s',
                  textAlign: 'left'
                }}
                onMouseEnter={(e) => {
                  e.target.style.borderColor = accent;
                  e.target.style.background = darkMode ? accent + '22' : accent + '11';
                }}
                onMouseLeave={(e) => {
                  e.target.style.borderColor = border;
                  e.target.style.background = darkMode ? '#2a2a2a' : '#f8f9fa';
                }}
              >
                {question}
              </button>
        ))}
      </div>
        </div>
      )}

      {/*   */}
      <div style={{
        display: 'flex',
        gap: '0.5rem',
        alignItems: 'flex-end'
      }}>
        <div style={{ flex: 1 }}>
          <textarea
          value={input}
            onChange={(e) => setInput(e.target.value)}
            onKeyPress={handleKeyPress}
            placeholder={`${stock}    ... `}
          disabled={loading}
            style={{
              width: '100%',
              minHeight: '50px',
              maxHeight: '120px',
              padding: '0.8rem',
              border: `2px solid ${border}`,
              borderRadius: '16px',
              background: darkMode ? '#2a2a2a' : '#fff',
              color: text,
              fontSize: '0.95rem',
              resize: 'vertical',
              fontFamily: 'inherit',
              transition: 'all 0.2s'
            }}
            onFocus={(e) => {
              e.target.style.borderColor = accent;
              e.target.style.boxShadow = `0 0 0 3px ${accent}22`;
            }}
            onBlur={(e) => {
              e.target.style.borderColor = border;
              e.target.style.boxShadow = 'none';
            }}
          />
        </div>
        <button
          onClick={handleSend}
          disabled={!input.trim() || loading}
          style={{
            background: input.trim() && !loading 
              ? `linear-gradient(135deg, ${accent}, #74b9ff)`
              : (darkMode ? '#333' : '#eee'),
            color: input.trim() && !loading ? '#fff' : subtext,
            border: 'none',
            borderRadius: '16px',
            padding: '0.8rem 1.2rem',
            cursor: input.trim() && !loading ? 'pointer' : 'not-allowed',
            fontSize: '1rem',
            fontWeight: '700',
            transition: 'all 0.2s',
            minHeight: '50px',
            display: 'flex',
            alignItems: 'center',
            gap: '0.5rem',
            boxShadow: input.trim() && !loading 
              ? '0 4px 12px rgba(116, 185, 255, 0.3)' 
              : 'none'
          }}
          onMouseEnter={(e) => {
            if (input.trim() && !loading) {
              e.target.style.transform = 'translateY(-1px)';
              e.target.style.boxShadow = '0 6px 16px rgba(116, 185, 255, 0.4)';
            }
          }}
          onMouseLeave={(e) => {
            e.target.style.transform = 'translateY(0)';
            e.target.style.boxShadow = input.trim() && !loading 
              ? '0 4px 12px rgba(116, 185, 255, 0.3)' 
              : 'none';
          }}
        >
          {loading ? '' : ''} {loading ? '' : ''}
        </button>
      </div>

      {/*   */}
      <div style={{
        marginTop: '1rem',
        padding: '0.8rem',
        background: darkMode ? '#1a1a1a' : '#fff5f5',
        borderRadius: '12px',
        border: `2px solid ${darkMode ? '#333' : '#ffeaa7'}`,
        textAlign: 'center'
      }}>
        <div style={{ fontSize: '0.8rem', color: subtext, lineHeight: '1.4' }}>
           <strong>:</strong> AI  ,     ! 
        </div>
      </div>

      <style>
        {`
          @keyframes bounce {
            0%, 20%, 50%, 80%, 100% { transform: translateY(0); }
            40% { transform: translateY(-8px); }
            60% { transform: translateY(-4px); }
          }
        `}
      </style>
    </div>
  );
}

export default AIChatSection; 