import React, { useState } from 'react';
import { fetchYahooRealtime } from '../utils/fetchRealtime';

function RealtimeFetchDemo() {
  const [symbol, setSymbol] = useState('005930.KS');
  const [result, setResult] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  const handleFetch = async () => {
    setLoading(true);
    setError(null);
    setResult(null);
    try {
      const data = await fetchYahooRealtime(symbol);
      setResult(data);
    } catch (e) {
      setError(e.message);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div style={{ margin: '1.5rem 0', padding: '1rem', border: '1px solid #eee', borderRadius: '8px' }}>
      <h3> /  ()</h3>
      <div style={{ display:'flex', gap:8, marginBottom:8 }}>
        <input value={symbol} onChange={e=>setSymbol(e.target.value)} placeholder=" (: 005930.KS, AAPL)" style={{width:160}} />
        <button onClick={handleFetch} disabled={loading}></button>
      </div>
      {loading && <div style={{color:'#888'}}> ...</div>}
      {error && <div style={{color:'red'}}>{error}</div>}
      {result && (
        <div style={{background:'#fafafa',padding:12,borderRadius:6}}>
          <div><b>:</b> {result.symbol}</div>
          <div><b>:</b> {result.price} {result.currency}</div>
          <div><b>:</b> {result.volume?.toLocaleString()}</div>
          <div><b>:</b> {result.exchange}</div>
          <div><b>:</b> {result.time}</div>
        </div>
      )}
      <div style={{color:'#888',fontSize:'0.97rem',marginTop:8}}>
        : (005930.KS), (035720.KS), (AAPL), (TSLA) <br/>
        : KOSPI/KOSDAQ + (: 005930.KS, 035720.KQ)<br/>
        :  (: AAPL, TSLA, MSFT)
      </div>
    </div>
  );
}

export default RealtimeFetchDemo; 