import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { fetchStockNewsMultiple, symbolMap, analyzeNewsSentiment } from '../utils/fetchRealtime';

//    
const fetchGlobalNews = async (symbol) => {
  try {
    // Alpha Vantage News API ()
    const response = await fetch(`https://www.alphavantage.co/query?function=NEWS_SENTIMENT&tickers=${symbol}&apikey=demo`);
    const data = await response.json();
    
    if (data.feed) {
      return data.feed.slice(0, 10).map(news => ({
        title: news.title,
        summary: news.summary || news.title,
        url: news.url,
        publishTime: new Date(news.time_published).toLocaleString(),
        provider: news.source,
        category: '',
        sentiment: news.overall_sentiment_label || 'Neutral'
      }));
    }
    return [];
  } catch (e) {
    console.error('   :', e);
    return [];
  }
};

//    (API   )
const getSampleNews = (stock) => [
  {
    title: `${stock}, 3   `,
    summary: `${stock}   3   .     .`,
    url: '#sample-news-1',
    publishTime: new Date().toLocaleString(),
    provider: '',
    category: '',
    sentiment: 'Positive'
  },
  {
    title: `${stock}  ,   `,
    summary: ` ${stock}     .      .`,
    url: '#sample-news-2',
    publishTime: new Date(Date.now() - 3600000).toLocaleString(),
    provider: '',
    category: '',
    sentiment: 'Positive'
  },
  {
    title: `Global Market Update: ${stock} Performance`,
    summary: `International markets are showing positive sentiment towards ${stock}. Recent developments suggest strong growth potential.`,
    url: '#sample-news-3',
    publishTime: new Date(Date.now() - 7200000).toLocaleString(),
    provider: 'Financial Times',
    category: '',
    sentiment: 'Positive'
  },
  {
    title: `${stock}  ,   `,
    summary: ` ${stock}   200%      .`,
    url: '#sample-news-4',
    publishTime: new Date(Date.now() - 10800000).toLocaleString(),
    provider: '',
    category: '',
    sentiment: 'Neutral'
  },
  {
    title: `${stock} Analyst Recommendations Updated`,
    summary: `Major investment banks have updated their recommendations for ${stock}, with most maintaining a buy rating.`,
    url: '#sample-news-5',
    publishTime: new Date(Date.now() - 14400000).toLocaleString(),
    provider: 'Bloomberg',
    category: '',
    sentiment: 'Positive'
  }
];

function NewsDetailPage() {
  const { stock } = useParams();
  const navigate = useNavigate();
  const [news, setNews] = useState([]);
  const [loading, setLoading] = useState(true);
  const [filter, setFilter] = useState('ALL'); // ALL, , 
  const [sortBy, setSortBy] = useState('TIME'); // TIME, SENTIMENT
  const [sentiment, setSentiment] = useState(null);
  const [darkMode, setDarkMode] = useState(() => {
    return localStorage.getItem('darong_darkmode') === 'true';
  });

  const bg = darkMode ? '#181a1b' : '#f8f9fa';
  const card = darkMode ? '#23272b' : '#fff';
  const text = darkMode ? '#e0e0e0' : '#222';
  const subtext = darkMode ? '#aaa' : '#888';
  const border = darkMode ? '#333' : '#eee';
  const accent = '#8884d8';

  useEffect(() => {
    loadNews();
  }, [stock]);

  const loadNews = async () => {
    setLoading(true);
    try {
      const symbol = symbolMap[stock] || '005930.KS';
      
      //      
      const [domesticNews, globalNews] = await Promise.all([
        fetchStockNewsMultiple(stock, symbol),
        fetchGlobalNews(symbol.replace('.KS', '').replace('.KQ', ''))
      ]);

      const allNews = [
        ...domesticNews.map(n => ({ ...n, category: '' })),
        ...globalNews
      ];

      //   
      const finalNews = allNews.length > 0 ? allNews : getSampleNews(stock);
      
      setNews(finalNews);
      
      //  
      const sentimentAnalysis = analyzeNewsSentiment(finalNews);
      setSentiment(sentimentAnalysis);
      
    } catch (error) {
      console.error('  :', error);
      setNews(getSampleNews(stock));
      setSentiment(analyzeNewsSentiment(getSampleNews(stock)));
    } finally {
      setLoading(false);
    }
  };

  const filteredNews = news.filter(item => {
    if (filter === 'ALL') return true;
    return item.category === filter;
  });

  const sortedNews = [...filteredNews].sort((a, b) => {
    if (sortBy === 'TIME') {
      return new Date(b.publishTime) - new Date(a.publishTime);
    }
    //  
    const sentimentOrder = { 'Positive': 3, 'Neutral': 2, 'Negative': 1 };
    return (sentimentOrder[b.sentiment] || 2) - (sentimentOrder[a.sentiment] || 2);
  });

  const getSentimentColor = (sentimentType) => {
    switch (sentimentType) {
      case 'Positive': return '#4caf50';
      case 'Negative': return '#f44336';
      default: return '#ff9800';
    }
  };

  const getSentimentLabel = (sentimentType) => {
    switch (sentimentType) {
      case 'Positive': return '';
      case 'Negative': return '';
      default: return '';
    }
  };

  return (
    <div style={{
      minHeight: '100vh',
      background: bg,
      color: text,
      padding: '1rem'
    }}>
      <div style={{
        maxWidth: '800px',
        margin: '0 auto'
      }}>
        {/*  */}
        <div style={{
          background: card,
          borderRadius: '16px',
          padding: '2rem',
          marginBottom: '2rem',
          boxShadow: darkMode ? '0 4px 20px rgba(0,0,0,0.3)' : '0 4px 20px rgba(0,0,0,0.1)',
          border: `1px solid ${border}`
        }}>
          <div style={{ display: 'flex', alignItems: 'center', marginBottom: '1rem' }}>
            <button
              onClick={() => navigate('/')}
              style={{
                background: 'none',
                border: `2px solid ${accent}`,
                borderRadius: '50%',
                width: '40px',
                height: '40px',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                cursor: 'pointer',
                marginRight: '1rem',
                color: accent,
                fontSize: '1.2rem',
                fontWeight: 'bold'
              }}
            >
              
            </button>
            <div>
              <h1 style={{ 
                margin: 0, 
                fontSize: '2rem', 
                color: accent,
                fontWeight: '700'
              }}>
                {stock}  
              </h1>
              <p style={{ 
                margin: '0.5rem 0 0 0', 
                color: subtext,
                fontSize: '1.1rem'
              }}>
                     
              </p>
            </div>
          </div>

          {/*    */}
          {sentiment && (
            <div style={{
              background: darkMode ? '#1a1a1a' : '#f8f9fa',
              borderRadius: '12px',
              padding: '1.5rem',
              marginTop: '1rem'
            }}>
              <h3 style={{ margin: '0 0 1rem 0', color: accent }}>   </h3>
              <div style={{ 
                display: 'grid', 
                gridTemplateColumns: 'repeat(auto-fit, minmax(120px, 1fr))', 
                gap: '1rem',
                marginBottom: '1rem'
              }}>
                <div style={{ textAlign: 'center' }}>
                  <div style={{ fontSize: '2rem', fontWeight: 'bold', color: '#4caf50' }}>
                    {sentiment.positive}%
                  </div>
                  <div style={{ color: subtext, fontSize: '0.9rem' }}></div>
                </div>
                <div style={{ textAlign: 'center' }}>
                  <div style={{ fontSize: '2rem', fontWeight: 'bold', color: '#ff9800' }}>
                    {sentiment.neutral}%
                  </div>
                  <div style={{ color: subtext, fontSize: '0.9rem' }}></div>
                </div>
                <div style={{ textAlign: 'center' }}>
                  <div style={{ fontSize: '2rem', fontWeight: 'bold', color: '#f44336' }}>
                    {sentiment.negative}%
                  </div>
                  <div style={{ color: subtext, fontSize: '0.9rem' }}></div>
                </div>
              </div>
              <div style={{ 
                textAlign: 'center',
                padding: '0.8rem',
                background: darkMode ? '#2a2a2a' : '#fff',
                borderRadius: '8px',
                fontWeight: 'bold',
                color: getSentimentColor(sentiment.summary === '' ? 'Positive' : sentiment.summary === '' ? 'Negative' : 'Neutral')
              }}>
                  : {sentiment.summary}
              </div>
            </div>
          )}
        </div>

        {/*    */}
        <div style={{
          background: card,
          borderRadius: '12px',
          padding: '1.5rem',
          marginBottom: '2rem',
          border: `1px solid ${border}`
        }}>
          <div style={{ 
            display: 'flex', 
            gap: '1rem', 
            flexWrap: 'wrap',
            alignItems: 'center',
            justifyContent: 'space-between'
          }}>
            <div style={{ display: 'flex', gap: '0.5rem', flexWrap: 'wrap' }}>
              {['ALL', '', ''].map(category => (
                <button
                  key={category}
                  onClick={() => setFilter(category)}
                  style={{
                    background: filter === category ? accent : 'transparent',
                    color: filter === category ? '#fff' : text,
                    border: `2px solid ${filter === category ? accent : border}`,
                    borderRadius: '20px',
                    padding: '0.5rem 1rem',
                    cursor: 'pointer',
                    fontWeight: '600',
                    transition: 'all 0.2s'
                  }}
                >
                  {category === 'ALL' ? '' : category}
                </button>
              ))}
            </div>
            
            <div style={{ display: 'flex', gap: '0.5rem', alignItems: 'center' }}>
              <span style={{ color: subtext, fontSize: '0.9rem' }}>:</span>
              <select
                value={sortBy}
                onChange={(e) => setSortBy(e.target.value)}
                style={{
                  background: darkMode ? '#2a2a2a' : '#fff',
                  color: text,
                  border: `1px solid ${border}`,
                  borderRadius: '8px',
                  padding: '0.5rem',
                  fontSize: '0.9rem'
                }}
              >
                <option value="TIME"></option>
                <option value="SENTIMENT"></option>
              </select>
            </div>
          </div>
          
          <div style={{ 
            marginTop: '1rem',
            fontSize: '0.9rem',
            color: subtext,
            display: 'flex',
            justifyContent: 'space-between'
          }}>
            <span> {sortedNews.length} </span>
            <button
              onClick={loadNews}
              disabled={loading}
              style={{
                background: 'none',
                border: 'none',
                color: accent,
                cursor: loading ? 'not-allowed' : 'pointer',
                fontSize: '0.9rem',
                fontWeight: '600'
              }}
            >
              {loading ? ' ...' : ' '}
            </button>
          </div>
        </div>

        {/*   */}
        {loading ? (
          <div style={{
            background: card,
            borderRadius: '12px',
            padding: '3rem',
            textAlign: 'center',
            color: subtext,
            border: `1px solid ${border}`
          }}>
            <div style={{ fontSize: '2rem', marginBottom: '1rem' }}></div>
              ...
          </div>
        ) : (
          <div style={{ display: 'flex', flexDirection: 'column', gap: '1rem' }}>
            {sortedNews.map((article, index) => (
              <div
                key={index}
                style={{
                  background: card,
                  borderRadius: '12px',
                  padding: '1.5rem',
                  border: `1px solid ${border}`,
                  cursor: 'pointer',
                  transition: 'all 0.2s',
                  boxShadow: darkMode ? '0 2px 10px rgba(0,0,0,0.2)' : '0 2px 10px rgba(0,0,0,0.05)'
                }}
                onClick={() => {
                  if (article.url && article.url !== '#') {
                    window.open(article.url, '_blank');
                  }
                }}
                onMouseEnter={(e) => {
                  e.currentTarget.style.transform = 'translateY(-2px)';
                  e.currentTarget.style.boxShadow = darkMode ? '0 4px 20px rgba(0,0,0,0.3)' : '0 4px 20px rgba(0,0,0,0.1)';
                }}
                onMouseLeave={(e) => {
                  e.currentTarget.style.transform = 'translateY(0)';
                  e.currentTarget.style.boxShadow = darkMode ? '0 2px 10px rgba(0,0,0,0.2)' : '0 2px 10px rgba(0,0,0,0.05)';
                }}
              >
                <div style={{ 
                  display: 'flex', 
                  justifyContent: 'space-between', 
                  alignItems: 'flex-start',
                  marginBottom: '1rem'
                }}>
                  <div style={{ display: 'flex', gap: '0.5rem', flexWrap: 'wrap' }}>
                    <span style={{
                      background: article.category === '' ? '#2196f3' : '#4caf50',
                      color: '#fff',
                      padding: '0.25rem 0.75rem',
                      borderRadius: '12px',
                      fontSize: '0.8rem',
                      fontWeight: '600'
                    }}>
                      {article.category}
                    </span>
                    <span style={{
                      background: getSentimentColor(article.sentiment) + '22',
                      color: getSentimentColor(article.sentiment),
                      padding: '0.25rem 0.75rem',
                      borderRadius: '12px',
                      fontSize: '0.8rem',
                      fontWeight: '600',
                      border: `1px solid ${getSentimentColor(article.sentiment)}44`
                    }}>
                      {getSentimentLabel(article.sentiment)}
                    </span>
                  </div>
                  <span style={{ 
                    color: subtext, 
                    fontSize: '0.8rem',
                    whiteSpace: 'nowrap'
                  }}>
                    {article.publishTime}
                  </span>
                </div>

                <h3 style={{
                  margin: '0 0 0.75rem 0',
                  fontSize: '1.2rem',
                  fontWeight: '700',
                  lineHeight: '1.4',
                  color: text
                }}>
                  {article.title}
                </h3>

                <p style={{
                  margin: '0 0 1rem 0',
                  lineHeight: '1.6',
                  color: subtext,
                  fontSize: '0.95rem'
                }}>
                  {article.summary}
                </p>

                <div style={{ 
                  display: 'flex', 
                  justifyContent: 'space-between', 
                  alignItems: 'center',
                  paddingTop: '0.75rem',
                  borderTop: `1px solid ${border}`
                }}>
                  <span style={{ 
                    color: subtext, 
                    fontSize: '0.9rem',
                    fontWeight: '500'
                  }}>
                     {article.provider}
                  </span>
                  <span style={{
                    color: accent,
                    fontSize: '0.9rem',
                    fontWeight: '600'
                  }}>
                      
                  </span>
                </div>
              </div>
            ))}
          </div>
        )}

        {/*  */}
        <div style={{
          textAlign: 'center',
          padding: '2rem',
          color: subtext,
          fontSize: '0.9rem'
        }}>
          <p>     </p>
          <p>      </p>
        </div>
      </div>
    </div>
  );
}

export default NewsDetailPage; 