import React, { useState, useCallback } from 'react';
import KeywordBrainstormingSystem from './KeywordBrainstormingSystem';
import StockDiscoveryEngine from './StockDiscoveryEngine';

const KeywordToStockWorkflow = ({ darkMode = false, onStockSelect, onKeywordsGenerated }) => {
  const [currentStep, setCurrentStep] = useState(1); // 1: , 2:  
  const [generatedKeywords, setGeneratedKeywords] = useState([]);
  const [discoveredStocks, setDiscoveredStocks] = useState([]);

  const theme = {
    bg: darkMode ? '#1a1a1a' : '#ffffff',
    cardBg: darkMode ? '#2d2d2d' : '#f8f9fa',
    text: darkMode ? '#ffffff' : '#333333',
    subtext: darkMode ? '#cccccc' : '#666666',
    border: darkMode ? '#404040' : '#e0e0e0',
    accent: '#007bff',
    positive: '#00c851',
    negative: '#ff4444',
    warning: '#ffbb33',
    purple: '#9c27b0',
    teal: '#20c997',
    orange: '#ff6b35'
  };

  //    
  const handleKeywordsGenerated = useCallback((keywords) => {
    setGeneratedKeywords(keywords);
    setCurrentStep(2);
    onKeywordsGenerated?.(keywords); //    
    console.log('   :', keywords);
  }, [onKeywordsGenerated]);

  //    
  const handleStockTrack = useCallback((stock) => {
    if (onStockSelect) {
      onStockSelect(stock);
    }
    
    //    
    setDiscoveredStocks(prev => {
      const exists = prev.find(s => s.symbol === stock.symbol);
      if (!exists) {
        return [stock, ...prev.slice(0, 9)]; //  10 
      }
      return prev;
    });
    
    console.log('   :', stock);
  }, [onStockSelect]);

  //  
  const resetWorkflow = useCallback(() => {
    setCurrentStep(1);
    setGeneratedKeywords([]);
  }, []);

  //  
  const goToPreviousStep = useCallback(() => {
    if (currentStep > 1) {
      setCurrentStep(currentStep - 1);
    }
  }, [currentStep]);

  return (
    <div style={{
      background: theme.bg,
      borderRadius: '12px',
      border: `2px solid ${theme.border}`,
      overflow: 'hidden',
      marginBottom: '20px'
    }}>
      {/*    */}
      <div style={{
        background: `linear-gradient(135deg, ${theme.purple}, ${theme.orange})`,
        padding: '20px',
        color: 'white'
      }}>
        <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
          <div>
            <h2 style={{ margin: 0, fontSize: '20px', fontWeight: '700' }}>
                   
            </h2>
            <p style={{ margin: '8px 0 0 0', opacity: 0.9, fontSize: '14px' }}>
                    
            </p>
          </div>
          <div style={{ display: 'flex', gap: '10px', alignItems: 'center' }}>
            {currentStep > 1 && (
              <button
                onClick={goToPreviousStep}
                style={{
                  background: 'rgba(255,255,255,0.2)',
                  border: '2px solid rgba(255,255,255,0.3)',
                  color: 'white',
                  padding: '6px 12px',
                  borderRadius: '6px',
                  cursor: 'pointer',
                  fontSize: '12px',
                  fontWeight: '600'
                }}
              >
                  
              </button>
            )}
            <button
              onClick={resetWorkflow}
              style={{
                background: 'rgba(255,255,255,0.2)',
                border: '2px solid rgba(255,255,255,0.3)',
                color: 'white',
                padding: '6px 12px',
                borderRadius: '6px',
                cursor: 'pointer',
                fontSize: '12px',
                fontWeight: '600'
              }}
            >
               
            </button>
          </div>
        </div>

        {/*    */}
        <div style={{ marginTop: '15px' }}>
          <div style={{ display: 'flex', alignItems: 'center', gap: '15px' }}>
            {[
              { step: 1, label: ' ', desc: ' ' },
              { step: 2, label: '  ', desc: '  ' },
              { step: 3, label: '  ', desc: ' ' }
            ].map((item, index) => (
              <React.Fragment key={item.step}>
                <div style={{
                  display: 'flex',
                  flexDirection: 'column',
                  alignItems: 'center',
                  opacity: currentStep >= item.step ? 1 : 0.5
                }}>
                  <div style={{
                    width: '40px',
                    height: '40px',
                    borderRadius: '50%',
                    background: currentStep >= item.step ? 'rgba(255,255,255,0.9)' : 'rgba(255,255,255,0.3)',
                    color: currentStep >= item.step ? theme.purple : 'white',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    fontSize: '18px',
                    fontWeight: '700',
                    marginBottom: '5px'
                  }}>
                    {currentStep > item.step ? '' : item.step}
                  </div>
                  <div style={{ fontSize: '12px', textAlign: 'center' }}>
                    <div style={{ fontWeight: '600' }}>{item.label}</div>
                    <div style={{ opacity: 0.8 }}>{item.desc}</div>
                  </div>
                </div>
                {index < 2 && (
                  <div style={{
                    width: '30px',
                    height: '2px',
                    background: currentStep > item.step ? 'rgba(255,255,255,0.9)' : 'rgba(255,255,255,0.3)',
                    marginTop: '-15px'
                  }} />
                )}
              </React.Fragment>
            ))}
          </div>
        </div>
      </div>

      {/*    */}
      <div style={{ padding: '0' }}>
        {currentStep === 1 && (
          <KeywordBrainstormingSystem
            darkMode={darkMode}
            onKeywordsGenerated={handleKeywordsGenerated}
          />
        )}

        {currentStep === 2 && (
          <StockDiscoveryEngine
            darkMode={darkMode}
            keywords={generatedKeywords}
            onStockTrack={handleStockTrack}
          />
        )}
      </div>

      {/*   */}
      {currentStep === 2 && generatedKeywords.length > 0 && (
        <div style={{
          background: theme.cardBg,
          padding: '20px',
          borderTop: `1px solid ${theme.border}`
        }}>
          <h3 style={{ color: theme.text, fontSize: '16px', marginBottom: '15px' }}>
              
          </h3>
          
          <div style={{ display: 'grid', gridTemplateColumns: 'repeat(auto-fit, minmax(250px, 1fr))', gap: '15px' }}>
            {/*   */}
            <div style={{
              background: theme.bg,
              padding: '15px',
              borderRadius: '8px',
              border: `1px solid ${theme.border}`
            }}>
              <h4 style={{ color: theme.text, fontSize: '14px', marginBottom: '10px' }}>
                   ({generatedKeywords.length})
              </h4>
              <div style={{ display: 'flex', flexWrap: 'wrap', gap: '6px' }}>
                {generatedKeywords.map(keyword => (
                  <span
                    key={keyword}
                    style={{
                      background: theme.accent,
                      color: 'white',
                      padding: '4px 8px',
                      borderRadius: '12px',
                      fontSize: '11px',
                      fontWeight: '600'
                    }}
                  >
                    {keyword}
                  </span>
                ))}
              </div>
            </div>

            {/*    */}
            <div style={{
              background: theme.bg,
              padding: '15px',
              borderRadius: '8px',
              border: `1px solid ${theme.border}`
            }}>
              <h4 style={{ color: theme.text, fontSize: '14px', marginBottom: '10px' }}>
                  
              </h4>
              <div style={{ fontSize: '12px', color: theme.subtext, lineHeight: '1.4' }}>
                <div>   :   </div>
                <div>  : {discoveredStocks.length}</div>
                <div>  :  </div>
                <div>  : //</div>
              </div>
            </div>

            {/*   */}
            <div style={{
              background: theme.bg,
              padding: '15px',
              borderRadius: '8px',
              border: `1px solid ${theme.positive}`
            }}>
              <h4 style={{ color: theme.text, fontSize: '14px', marginBottom: '10px' }}>
                   
              </h4>
              <div style={{ fontSize: '12px', color: theme.subtext, lineHeight: '1.4' }}>
                <div>    </div>
                <div>   </div>
                <div>   </div>
                <div>    </div>
              </div>
            </div>
          </div>
        </div>
      )}

      {/*     */}
      {discoveredStocks.length > 0 && (
        <div style={{
          background: theme.cardBg,
          padding: '20px',
          borderTop: `1px solid ${theme.border}`
        }}>
          <h3 style={{ color: theme.text, fontSize: '16px', marginBottom: '15px' }}>
                ({discoveredStocks.length})
          </h3>
          
          <div style={{ display: 'grid', gridTemplateColumns: 'repeat(auto-fit, minmax(200px, 1fr))', gap: '10px' }}>
            {discoveredStocks.map(stock => (
              <div
                key={stock.symbol}
                style={{
                  background: theme.bg,
                  padding: '12px',
                  borderRadius: '8px',
                  border: `1px solid ${theme.border}`,
                  cursor: 'pointer'
                }}
                onClick={() => onStockSelect && onStockSelect(stock)}
              >
                <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                  <div>
                    <div style={{ color: theme.text, fontSize: '14px', fontWeight: '600' }}>
                      {stock.symbol}
                      {stock.symbol === 'UAVS' && ' '}
                      {stock.isPennyStock && ' '}
                    </div>
                    <div style={{ color: theme.subtext, fontSize: '11px' }}>
                      {stock.name}
                    </div>
                  </div>
                  <div style={{ textAlign: 'right' }}>
                    <div style={{ color: theme.text, fontSize: '12px', fontWeight: '600' }}>
                      {stock.currency === 'USD' ? '$' : ''}{stock.price?.toFixed(2)}
                      {stock.currency === 'KRW' ? '' : ''}
                    </div>
                    <div style={{ fontSize: '10px', color: theme.subtext }}>
                      :{stock.memeScore} :{stock.quantScore}
                    </div>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      )}

      {/*   */}
      <div style={{
        background: theme.cardBg,
        padding: '15px 20px',
        borderTop: `1px solid ${theme.border}`,
        fontSize: '12px',
        color: theme.subtext,
        display: 'flex',
        justifyContent: 'space-between',
        flexWrap: 'wrap'
      }}>
        <div>      </div>
        <div>           </div>
      </div>
    </div>
  );
};

export default KeywordToStockWorkflow; 