import React, { useState } from 'react';
import ChaessaemEmoji from './ChaessaemEmoji';

const emotions = [
  //  32
  { key: 'happy', emoji: '', label: '', color: '#27ae60' },
  { key: 'excited', emoji: '', label: '', color: '#f39c12' },
  { key: 'love', emoji: '', label: '', color: '#e91e63' },
  { key: 'laughing', emoji: '', label: '', color: '#ff9800' },
  { key: 'wink', emoji: '', label: '', color: '#9c27b0' },
  { key: 'kiss', emoji: '', label: '', color: '#e74c3c' },
  { key: 'cool', emoji: '', label: '', color: '#3498db' },
  { key: 'angel', emoji: '', label: '', color: '#f1c40f' },
  
  { key: 'neutral', emoji: '', label: '', color: '#95a5a6' },
  { key: 'thinking', emoji: '', label: '', color: '#34495e' },
  { key: 'confused', emoji: '', label: '', color: '#7f8c8d' },
  { key: 'surprised', emoji: '', label: '', color: '#f39c12' },
  { key: 'shock', emoji: '', label: '', color: '#e74c3c' },
  { key: 'dizzy', emoji: '', label: '', color: '#9b59b6' },
  { key: 'sleepy', emoji: '', label: '', color: '#3498db' },
  { key: 'yawn', emoji: '', label: '', color: '#95a5a6' },
  
  { key: 'worried', emoji: '', label: '', color: '#e74c3c' },
  { key: 'sad', emoji: '', label: '', color: '#3498db' },
  { key: 'crying', emoji: '', label: '', color: '#2980b9' },
  { key: 'disappointed', emoji: '', label: '', color: '#8e44ad' },
  { key: 'tired', emoji: '', label: '', color: '#7f8c8d' },
  { key: 'sick', emoji: '', label: '', color: '#e67e22' },
  { key: 'nervous', emoji: '', label: '', color: '#f39c12' },
  { key: 'sweating', emoji: '', label: '', color: '#3498db' },
  
  { key: 'angry', emoji: '', label: '', color: '#e74c3c' },
  { key: 'rage', emoji: '', label: '', color: '#c0392b' },
  { key: 'annoyed', emoji: '', label: '', color: '#e67e22' },
  { key: 'grumpy', emoji: '', label: '', color: '#95a5a6' },
  { key: 'money_face', emoji: '', label: '', color: '#27ae60' },
  { key: 'party', emoji: '', label: '', color: '#9c27b0' },
  { key: 'rocket', emoji: '', label: '', color: '#3498db' },
  { key: 'fire', emoji: '', label: '', color: '#e74c3c' },

  //   32
  { key: 'melting', emoji: '', label: '', color: '#ff6b6b' },
  { key: 'nerd', emoji: '', label: '', color: '#74b9ff' },
  { key: 'sunglasses_cool', emoji: '', label: '', color: '#2d3436' },
  { key: 'glasses', emoji: '', label: '', color: '#6c5ce7' },
  { key: 'rose', emoji: '', label: '', color: '#fd79a8' },
  { key: 'gift', emoji: '', label: '', color: '#e84393' },
  { key: 'gift_box', emoji: '', label: '', color: '#a29bfe' },
  { key: 'computer', emoji: '', label: '', color: '#00b894' },
  
  { key: 'chart_up', emoji: '', label: '', color: '#00b894' },
  { key: 'chart_down', emoji: '', label: '', color: '#e17055' },
  { key: 'chart', emoji: '', label: '', color: '#0984e3' },
  { key: 'money_bag', emoji: '', label: '', color: '#00b894' },
  { key: 'dollar', emoji: '', label: '', color: '#00b894' },
  { key: 'yen', emoji: '', label: '', color: '#fdcb6e' },
  { key: 'euro', emoji: '', label: '', color: '#6c5ce7' },
  { key: 'pound', emoji: '', label: '', color: '#fd79a8' },
  
  { key: 'diamond', emoji: '', label: '', color: '#74b9ff' },
  { key: 'gem', emoji: '', label: '', color: '#fd79a8' },
  { key: 'crown', emoji: '', label: '', color: '#fdcb6e' },
  { key: 'trophy', emoji: '', label: '', color: '#fdcb6e' },
  { key: 'medal', emoji: '', label: '', color: '#fdcb6e' },
  { key: 'target', emoji: '', label: '', color: '#e17055' },
  { key: 'bullseye', emoji: '', label: '', color: '#e84393' },
  { key: 'lightning', emoji: '', label: '', color: '#fdcb6e' },
  
  { key: 'star', emoji: '', label: '', color: '#fdcb6e' },
  { key: 'sparkle', emoji: '', label: '', color: '#ffeaa7' },
  { key: 'rainbow', emoji: '', label: '', color: '#fd79a8' },
  { key: 'unicorn', emoji: '', label: '', color: '#fd79a8' },
  { key: 'phoenix', emoji: '', label: '', color: '#e17055' },
  { key: 'butterfly', emoji: '', label: '', color: '#a29bfe' },
  { key: 'flower', emoji: '', label: '', color: '#fd79a8' },
  { key: 'four_leaf', emoji: '', label: '', color: '#00b894' }
];

function EmotionButtons({ onSelect, darkMode = false }) {
  const [showAll, setShowAll] = useState(false);
  const [selectedCategory, setSelectedCategory] = useState('ALL');
  
  const bg = darkMode ? '#23272b' : '#fff';
  const border = darkMode ? '#333' : '#eee';
  const text = darkMode ? '#e0e0e0' : '#222';
  const accent = '#8884d8';

  const categories = {
    'ALL': '',
    'POSITIVE': '',
    'NEUTRAL': '', 
    'NEGATIVE': '',
    'MONEY': '/',
    'SPECIAL': ''
  };

  const getEmotionCategory = (emotion) => {
    const positive = ['happy', 'excited', 'love', 'laughing', 'wink', 'kiss', 'cool', 'angel', 'party', 'rocket', 'fire', 'nerd', 'sunglasses_cool', 'rose', 'gift', 'gift_box', 'chart_up', 'diamond', 'gem', 'crown', 'trophy', 'medal', 'target', 'lightning', 'star', 'sparkle', 'rainbow', 'unicorn', 'phoenix', 'butterfly', 'flower', 'four_leaf'];
    const neutral = ['neutral', 'thinking', 'confused', 'surprised', 'sleepy', 'yawn', 'glasses', 'computer'];
    const negative = ['worried', 'sad', 'crying', 'disappointed', 'tired', 'sick', 'nervous', 'sweating', 'angry', 'rage', 'annoyed', 'grumpy', 'melting', 'chart_down'];
    const money = ['money_face', 'money_bag', 'dollar', 'yen', 'euro', 'pound', 'chart_up', 'chart_down', 'chart'];
    const special = ['shock', 'dizzy', 'bullseye'];
    
    if (positive.includes(emotion.key)) return 'POSITIVE';
    if (neutral.includes(emotion.key)) return 'NEUTRAL';
    if (negative.includes(emotion.key)) return 'NEGATIVE';
    if (money.includes(emotion.key)) return 'MONEY';
    return 'SPECIAL';
  };

  const filteredEmotions = emotions.filter(emotion => {
    if (selectedCategory === 'ALL') return true;
    return getEmotionCategory(emotion) === selectedCategory;
  });

  const displayEmotions = showAll ? filteredEmotions : filteredEmotions.slice(0, 12);

  return (
    <div style={{
      background: bg,
      border: `1px solid ${border}`,
      borderRadius: '24px',
      padding: '2rem',
      marginBottom: '2rem',
      boxShadow: darkMode ? '0 12px 40px rgba(0,0,0,0.4)' : '0 12px 40px rgba(0,0,0,0.08)',
      position: 'relative',
      overflow: 'hidden'
    }}>
      {/*     */}
      <div style={{
        position: 'absolute',
        top: '-25px',
        right: '-25px',
        width: '50px',
        height: '50px',
        background: 'linear-gradient(45deg, #ff9ff3, #f368e0)',
        borderRadius: '50%',
        opacity: 0.15,
        animation: 'float 3s ease-in-out infinite'
      }} />
      <div style={{
        position: 'absolute',
        bottom: '-20px',
        left: '-20px',
        width: '40px',
        height: '40px',
        background: 'linear-gradient(45deg, #74b9ff, #0984e3)',
        borderRadius: '50%',
        opacity: 0.12,
        animation: 'float 4s ease-in-out infinite reverse'
      }} />
      <div style={{
        position: 'absolute',
        top: '50%',
        left: '-15px',
        width: '30px',
        height: '30px',
        background: 'linear-gradient(45deg, #fd79a8, #fdcb6e)',
        borderRadius: '50%',
        opacity: 0.1,
        animation: 'float 5s ease-in-out infinite'
      }} />

      <div style={{
        textAlign: 'center',
        marginBottom: '2rem'
      }}>
        <h3 style={{
          margin: '0 0 0.8rem 0',
          color: accent,
          fontSize: '1.6rem',
          fontWeight: '900',
          background: `linear-gradient(45deg, ${accent}, #ff6b6b, #4ecdc4)`,
          backgroundClip: 'text',
          WebkitBackgroundClip: 'text',
          WebkitTextFillColor: 'transparent',
          backgroundSize: '200% 200%',
          animation: 'gradientMove 3s ease infinite'
        }}>
             !
        </h3>
        <p style={{
          margin: 0,
          color: darkMode ? '#bbb' : '#555',
          fontSize: '1rem',
          fontStyle: 'italic',
          fontWeight: '500'
        }}>
           <span style={{ color: accent, fontWeight: 'bold' }}>64</span>   ! 
        </p>
      </div>

      {/*   */}
      <div style={{
        display: 'flex',
        justifyContent: 'center',
        gap: '0.8rem',
        marginBottom: '1.5rem',
        flexWrap: 'wrap'
      }}>
        {Object.entries(categories).map(([key, label]) => (
          <button
            key={key}
            onClick={() => setSelectedCategory(key)}
            style={{
              background: selectedCategory === key 
                ? `linear-gradient(45deg, ${accent}, #ff6b6b)` 
                : 'transparent',
              color: selectedCategory === key ? '#fff' : text,
              border: selectedCategory === key 
                ? 'none' 
                : `2px solid ${border}`,
              borderRadius: '25px',
              padding: '0.6rem 1.2rem',
              cursor: 'pointer',
              fontSize: '0.9rem',
              fontWeight: '700',
              transition: 'all 0.3s cubic-bezier(0.4, 0, 0.2, 1)',
              boxShadow: selectedCategory === key 
                ? '0 6px 20px rgba(136, 132, 216, 0.4)' 
                : 'none',
              transform: selectedCategory === key ? 'translateY(-2px)' : 'translateY(0)'
            }}
          >
            {label}
          </button>
        ))}
      </div>
      
      <div style={{
        display: 'grid',
        gridTemplateColumns: 'repeat(auto-fit, minmax(85px, 1fr))',
        gap: '1rem',
        marginBottom: '1.5rem'
      }}>
        {displayEmotions.map(emotion => (
          <button
            key={emotion.key}
            onClick={() => onSelect(emotion.key)}
            style={{
              background: `linear-gradient(135deg, ${darkMode ? '#2a2a2a' : '#ffffff'}, ${darkMode ? '#3a3a3a' : '#f8f9fa'})`,
              border: `3px solid ${border}`,
              borderRadius: '20px',
              padding: '1.2rem 0.8rem',
              cursor: 'pointer',
              transition: 'all 0.4s cubic-bezier(0.4, 0, 0.2, 1)',
              display: 'flex',
              flexDirection: 'column',
              alignItems: 'center',
              gap: '0.5rem',
              minHeight: '100px',
              position: 'relative',
              overflow: 'hidden'
            }}
            onMouseEnter={(e) => {
              e.target.style.transform = 'translateY(-8px) scale(1.08)';
              e.target.style.borderColor = emotion.color;
              e.target.style.background = `linear-gradient(135deg, ${emotion.color}20, ${emotion.color}35)`;
              e.target.style.boxShadow = `0 12px 30px ${emotion.color}50`;
            }}
            onMouseLeave={(e) => {
              e.target.style.transform = 'translateY(0) scale(1)';
              e.target.style.borderColor = border;
              e.target.style.background = `linear-gradient(135deg, ${darkMode ? '#2a2a2a' : '#ffffff'}, ${darkMode ? '#3a3a3a' : '#f8f9fa'})`;
              e.target.style.boxShadow = 'none';
            }}
            onMouseDown={(e) => {
              e.target.style.transform = 'translateY(0) scale(0.95)';
            }}
            onMouseUp={(e) => {
              e.target.style.transform = 'translateY(-8px) scale(1.08)';
            }}
          >
            {/*   */}
            <div style={{
              position: 'absolute',
              top: '8px',
              right: '8px',
              fontSize: '0.7rem',
              opacity: 0.8,
              animation: 'twinkle 2s infinite'
            }}></div>
            
            <div style={{
              marginBottom: '0.3rem',
              filter: 'drop-shadow(0 3px 6px rgba(0,0,0,0.15))',
              transition: 'all 0.3s'
            }}>
              <ChaessaemEmoji
                type="emotions"
                emotion={emotion.key === 'excited' ? 'excited' : 
                         emotion.key === 'happy' ? 'happy' :
                         emotion.key === 'confident' ? 'confident' :
                         emotion.key === 'worried' ? 'worried' :
                         emotion.key === 'surprised' ? 'surprised' :
                         emotion.key === 'love' ? 'love' : 'thinking'}
                size="normal"
                showMessage={false}
                autoAnimation={true}
                darkMode={darkMode}
              />
            </div>
            <div style={{
              fontSize: '0.8rem',
              fontWeight: '800',
              color: text,
              textAlign: 'center',
              lineHeight: '1.2',
              letterSpacing: '-0.02em'
            }}>
              {emotion.label}
            </div>
          </button>
        ))}
      </div>

      {/* /  */}
      <div style={{ textAlign: 'center', marginBottom: '1.5rem' }}>
        <button
          onClick={() => setShowAll(!showAll)}
          style={{
            background: `linear-gradient(45deg, ${accent}, #ff6b6b, #4ecdc4)`,
            color: '#fff',
            border: 'none',
            borderRadius: '30px',
            padding: '1rem 2rem',
            cursor: 'pointer',
            fontSize: '1rem',
            fontWeight: '800',
            transition: 'all 0.4s cubic-bezier(0.4, 0, 0.2, 1)',
            boxShadow: '0 6px 20px rgba(136, 132, 216, 0.4)',
            display: 'flex',
            alignItems: 'center',
            gap: '0.8rem',
            margin: '0 auto',
            backgroundSize: '200% 200%',
            animation: 'gradientMove 3s ease infinite'
          }}
          onMouseEnter={(e) => {
            e.target.style.transform = 'translateY(-3px) scale(1.05)';
            e.target.style.boxShadow = '0 8px 25px rgba(136, 132, 216, 0.5)';
          }}
          onMouseLeave={(e) => {
            e.target.style.transform = 'translateY(0) scale(1)';
            e.target.style.boxShadow = '0 6px 20px rgba(136, 132, 216, 0.4)';
          }}
        >
          {showAll ? (
            <>  </>
          ) : (
            <>    ({emotions.length - 12} )</>
          )}
        </button>
      </div>

      {/*     */}
      <div style={{
        background: `linear-gradient(135deg, ${darkMode ? '#2a1a2a' : '#fff0f5'}, ${darkMode ? '#1a2a3a' : '#f0f8ff'})`,
        borderRadius: '20px',
        padding: '1.5rem',
        border: `3px solid ${darkMode ? '#444' : '#ffd6e7'}`,
        textAlign: 'center',
        position: 'relative'
      }}>
        <div style={{
          fontSize: '1.5rem',
          marginBottom: '0.8rem'
        }}></div>
        <div style={{
          fontSize: '1rem',
          color: text,
          fontWeight: '700',
          marginBottom: '0.6rem'
        }}>
              
        </div>
        <div style={{
          fontSize: '0.9rem',
          color: darkMode ? '#bbb' : '#666',
          lineHeight: '1.6',
          fontWeight: '500'
        }}>
            <br/>
                !<br/>
          <span style={{ color: accent, fontWeight: '700' }}> 64 </span>  ! 
        </div>
        
        {/*   */}
        <div style={{
          position: 'absolute',
          top: '15px',
          right: '20px',
          fontSize: '1.2rem',
          animation: 'heartbeat 2s infinite'
        }}></div>
        <div style={{
          position: 'absolute',
          bottom: '15px',
          left: '20px',
          fontSize: '1rem',
          animation: 'twinkle 3s infinite'
        }}></div>
      </div>

      <style>
        {`
          @keyframes heartbeat {
            0%, 100% { transform: scale(1); }
            50% { transform: scale(1.15); }
          }
          @keyframes twinkle {
            0%, 100% { opacity: 0.7; transform: scale(1); }
            50% { opacity: 1; transform: scale(1.2); }
          }
          @keyframes float {
            0%, 100% { transform: translateY(0px); }
            50% { transform: translateY(-10px); }
          }
          @keyframes gradientMove {
            0% { background-position: 0% 50%; }
            50% { background-position: 100% 50%; }
            100% { background-position: 0% 50%; }
          }
        `}
      </style>
    </div>
  );
}

export default EmotionButtons; 