import React, { useState, useEffect, useMemo } from 'react';
import { useTheme } from '../hooks/useTheme';
import { useTypography } from '../hooks/useTypography';

//    (64     )
const CHAESSAEM_EMOJIS = {
  //   
  emotions: {
    happy: {
      face: "",
      description: "",
      message: "  !",
      animation: "bounce"
    },
    excited: {
      face: "",
      description: "",
      message: "!   ?",
      animation: "wobble"
    },
    thinking: {
      face: "",
      description: "",
      message: "...  .",
      animation: "pulse"
    },
    worried: {
      face: "",
      description: "",
      message: "     .",
      animation: "shake"
    },
    surprised: {
      face: "",
      description: "",
      message: "?  !",
      animation: "jump"
    },
    confident: {
      face: "",
      description: "",
      message: "   !",
      animation: "slide"
    },
    sleepy: {
      face: "",
      description: "",
      message: "  ...",
      animation: "sway"
    },
    love: {
      face: "",
      description: "",
      message: "   !",
      animation: "heartbeat"
    }
  },

  //    
  trading: {
    analyzing: {
      face: "",
      description: "",
      message: "   !",
      animation: "rotate"
    },
    celebrating: {
      face: "",
      description: "",
      message: " ! !",
      animation: "confetti"
    },
    warning: {
      face: "",
      description: "",
      message: "  !",
      animation: "alert"
    },
    thumbsup: {
      face: "",
      description: "",
      message: "  !",
      animation: "approve"
    },
    money: {
      face: "",
      description: "",
      message: "  !",
      animation: "coin"
    },
    rocket: {
      face: "",
      description: "",
      message: "  !",
      animation: "launch"
    }
  },

  //   (3 )
  loading: {
    working: {
      face: "",
      description: "  ",
      message: "  !  .",
      animation: "work"
    },
    tip: {
      face: "",
      description: " ",
      message: "    !",
      animation: "tip"
    },
    running: {
      face: "",
      description: "  ",
      message: "    !",
      animation: "run"
    }
  }
};

//    
const LOADING_TIPS = [
  "  !     !",
  "   !    !",
  "  !   !",
  "   !   !",
  "   !   !",
  "    !   !",
  "    !  !",
  "   !     !"
];

const ChaessaemEmoji = ({ 
  type = 'emotions', 
  emotion = 'happy', 
  size = 'normal', 
  showMessage = false,
  autoAnimation = true,
  onClick,
  darkMode = false,
  style = {},
  className = '',
  loadingTips = false,
  children
}) => {
  const theme = useTheme(darkMode);
  const typography = useTypography(darkMode);
  
  const [currentTip, setCurrentTip] = useState(0);
  const [animationState, setAnimationState] = useState('idle');
  const [isAnimating, setIsAnimating] = useState(false);

  //  
  const sizes = {
    small: { width: '32px', height: '32px', fontSize: '16px' },
    normal: { width: '48px', height: '48px', fontSize: '24px' },
    large: { width: '64px', height: '64px', fontSize: '32px' },
    huge: { width: '96px', height: '96px', fontSize: '48px' }
  };

  const sizeStyle = sizes[size] || sizes.normal;
  
  //   
  const emojiData = useMemo(() => {
    return CHAESSAEM_EMOJIS[type]?.[emotion] || CHAESSAEM_EMOJIS.emotions.happy;
  }, [type, emotion]);

  //   
  useEffect(() => {
    if (loadingTips && type === 'loading') {
      const interval = setInterval(() => {
        setCurrentTip(prev => (prev + 1) % LOADING_TIPS.length);
      }, 3000);
      return () => clearInterval(interval);
    }
  }, [loadingTips, type]);

  //  
  useEffect(() => {
    if (autoAnimation) {
      setIsAnimating(true);
      const timeout = setTimeout(() => setIsAnimating(false), 2000);
      return () => clearTimeout(timeout);
    }
  }, [autoAnimation, emotion]);

  //   SVG ( )
  const ChaessaemFace = ({ expression = 'happy' }) => (
    <div style={{
      position: 'relative',
      width: '100%',
      height: '100%',
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
      justifyContent: 'center'
    }}>
      {/*   */}
      <div style={{
        width: '80%',
        height: '25%',
        background: 'linear-gradient(135deg, #c44536 0%, #8b2e1f 100%)',
        borderRadius: '50% 50% 0 0',
        position: 'relative',
        border: '1px solid #6b1f14'
      }}>
        {/*   */}
        <div style={{
          position: 'absolute',
          top: '2px',
          left: '50%',
          transform: 'translateX(-50%)',
          width: '6px',
          height: '4px',
          background: '#333',
          borderRadius: '2px'
        }} />
      </div>

      {/*   */}
      <div style={{
        width: '70%',
        height: '50%',
        background: 'linear-gradient(135deg, #fdbcb4 0%, #f4a395 100%)',
        borderRadius: '50%',
        position: 'relative',
        border: '1px solid #e8967d'
      }}>
        {/*  */}
        <div style={{
          position: 'absolute',
          top: '30%',
          left: '20%',
          width: '8px',
          height: '8px',
          background: '#000',
          borderRadius: '50%'
        }}>
          {/*   */}
          <div style={{
            position: 'absolute',
            top: '2px',
            left: '2px',
            width: '3px',
            height: '3px',
            background: '#fff',
            borderRadius: '50%'
          }} />
        </div>
        <div style={{
          position: 'absolute',
          top: '30%',
          right: '20%',
          width: '8px',
          height: '8px',
          background: '#000',
          borderRadius: '50%'
        }}>
          <div style={{
            position: 'absolute',
            top: '2px',
            left: '2px',
            width: '3px',
            height: '3px',
            background: '#fff',
            borderRadius: '50%'
          }} />
        </div>

        {/*  (  ) */}
        <div style={{
          position: 'absolute',
          bottom: '25%',
          left: '50%',
          transform: 'translateX(-50%)',
          width: '12px',
          height: expression === 'happy' ? '6px' : '4px',
          borderRadius: expression === 'happy' ? '0 0 12px 12px' : '12px',
          background: expression === 'worried' ? '#333' : '#ff6b9d',
          border: '1px solid #e55a88'
        }} />

        {/*   */}
        {expression === 'happy' && (
          <>
            <div style={{
              position: 'absolute',
              top: '45%',
              left: '10%',
              width: '6px',
              height: '4px',
              background: '#ff9eb5',
              borderRadius: '50%',
              opacity: 0.7
            }} />
            <div style={{
              position: 'absolute',
              top: '45%',
              right: '10%',
              width: '6px',
              height: '4px',
              background: '#ff9eb5',
              borderRadius: '50%',
              opacity: 0.7
            }} />
          </>
        )}
      </div>

      {/*   */}
      <div style={{
        position: 'absolute',
        top: '15%',
        right: '-5%',
        width: '15px',
        height: '20px',
        background: 'linear-gradient(135deg, #d4af37 0%, #b8941f 100%)',
        borderRadius: '50% 0 80% 20%',
        border: '1px solid #9d7e1a'
      }} />

      {/*   */}
      <div style={{
        position: 'absolute',
        top: '-10%',
        right: '-10%',
        fontSize: '60%',
        filter: 'drop-shadow(1px 1px 2px rgba(0,0,0,0.3))'
      }}>
        {emojiData.face}
      </div>
    </div>
  );

  //  
  const getAnimationStyle = () => {
    if (!isAnimating && !autoAnimation) return {};
    
    const animations = {
      bounce: 'bounce 0.6s ease-in-out',
      wobble: 'wobble 0.8s ease-in-out',
      pulse: 'pulse 1s ease-in-out infinite',
      shake: 'shake 0.5s ease-in-out',
      jump: 'jump 0.4s ease-out',
      slide: 'slideIn 0.6s ease-out',
      sway: 'sway 2s ease-in-out infinite',
      heartbeat: 'heartbeat 1s ease-in-out infinite',
      rotate: 'rotate 2s linear infinite',
      confetti: 'confetti 1s ease-out',
      alert: 'alert 0.3s ease-in-out infinite',
      approve: 'approve 0.8s ease-out',
      coin: 'coinFlip 1s ease-in-out',
      launch: 'launch 1.2s ease-out',
      work: 'work 1.5s ease-in-out infinite',
      tip: 'tip 0.8s ease-in-out',
      run: 'run 1s ease-in-out infinite'
    };

    return {
      animation: animations[emojiData.animation] || animations.bounce
    };
  };

  return (
    <div
      className={className}
      onClick={onClick}
      style={{
        display: 'inline-flex',
        flexDirection: 'column',
        alignItems: 'center',
        gap: '8px',
        cursor: onClick ? 'pointer' : 'default',
        ...style
      }}
    >
      {/*   */}
      <div
        style={{
          ...sizeStyle,
          position: 'relative',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          background: theme.gradients.light,
          borderRadius: '50%',
          border: `2px solid ${theme.accent}`,
          boxShadow: theme.shadows.md,
          overflow: 'hidden',
          ...getAnimationStyle()
        }}
      >
        <ChaessaemFace expression={emotion} />
      </div>

      {/*  */}
      {showMessage && (
        <div style={{
          background: theme.cardBg,
          border: `1px solid ${theme.border}`,
          borderRadius: '12px',
          padding: '8px 12px',
          maxWidth: '200px',
          textAlign: 'center',
          position: 'relative'
        }}>
          {/*   */}
          <div style={{
            position: 'absolute',
            top: '-6px',
            left: '50%',
            transform: 'translateX(-50%)',
            width: '0',
            height: '0',
            borderLeft: '6px solid transparent',
            borderRight: '6px solid transparent',
            borderBottom: `6px solid ${theme.cardBg}`
          }} />
          
          <div style={{
            ...typography.presets.body.small,
            color: typography.colors.primary,
            fontWeight: typography.fontWeight.medium
          }}>
            {loadingTips && type === 'loading' ? 
              LOADING_TIPS[currentTip] : 
              emojiData.message
            }
          </div>
        </div>
      )}

      {/*   */}
      {children}

      {/* CSS   */}
      <style jsx>{`
        @keyframes bounce {
          0%, 100% { transform: translateY(0); }
          50% { transform: translateY(-10px); }
        }
        
        @keyframes wobble {
          0% { transform: rotate(0deg); }
          25% { transform: rotate(5deg); }
          75% { transform: rotate(-5deg); }
          100% { transform: rotate(0deg); }
        }
        
        @keyframes pulse {
          0%, 100% { transform: scale(1); }
          50% { transform: scale(1.1); }
        }
        
        @keyframes shake {
          0%, 100% { transform: translateX(0); }
          25% { transform: translateX(-3px); }
          75% { transform: translateX(3px); }
        }
        
        @keyframes jump {
          0% { transform: translateY(0) scale(1); }
          50% { transform: translateY(-15px) scale(1.1); }
          100% { transform: translateY(0) scale(1); }
        }
        
        @keyframes slideIn {
          0% { transform: translateX(-20px) opacity(0); }
          100% { transform: translateX(0) opacity(1); }
        }
        
        @keyframes sway {
          0%, 100% { transform: rotate(0deg); }
          50% { transform: rotate(3deg); }
        }
        
        @keyframes heartbeat {
          0%, 100% { transform: scale(1); }
          25%, 75% { transform: scale(1.1); }
          50% { transform: scale(1.2); }
        }
        
        @keyframes rotate {
          0% { transform: rotate(0deg); }
          100% { transform: rotate(360deg); }
        }
        
        @keyframes confetti {
          0% { transform: scale(1) rotate(0deg); }
          50% { transform: scale(1.3) rotate(180deg); }
          100% { transform: scale(1) rotate(360deg); }
        }
        
        @keyframes alert {
          0%, 100% { transform: scale(1); }
          50% { transform: scale(1.15); }
        }
        
        @keyframes approve {
          0% { transform: scale(1) rotateY(0deg); }
          50% { transform: scale(1.2) rotateY(180deg); }
          100% { transform: scale(1) rotateY(360deg); }
        }
        
        @keyframes coinFlip {
          0% { transform: rotateY(0deg); }
          50% { transform: rotateY(180deg) scale(1.1); }
          100% { transform: rotateY(360deg); }
        }
        
        @keyframes launch {
          0% { transform: translateY(0) scale(1); }
          30% { transform: translateY(-20px) scale(1.2); }
          100% { transform: translateY(-40px) scale(0.8) opacity(0.3); }
        }
        
        @keyframes work {
          0%, 100% { transform: rotate(0deg); }
          25% { transform: rotate(-10deg); }
          75% { transform: rotate(10deg); }
        }
        
        @keyframes tip {
          0% { transform: scale(1); }
          50% { transform: scale(1.1) rotateZ(5deg); }
          100% { transform: scale(1); }
        }
        
        @keyframes run {
          0%, 100% { transform: translateX(0) scale(1); }
          50% { transform: translateX(3px) scale(1.05); }
        }
      `}</style>
    </div>
  );
};

//    
export const ChaessaemLoader = ({ 
  type = 'working', 
  message,
  darkMode = false,
  showTips = true 
}) => {
  const [currentType, setCurrentType] = useState(0);
  const loadingTypes = ['working', 'tip', 'running'];

  useEffect(() => {
    const interval = setInterval(() => {
      setCurrentType(prev => (prev + 1) % loadingTypes.length);
    }, 4000);
    return () => clearInterval(interval);
  }, []);

  return (
    <div style={{
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
      gap: '16px',
      padding: '20px'
    }}>
      <ChaessaemEmoji
        type="loading"
        emotion={loadingTypes[currentType]}
        size="large"
        showMessage={showTips}
        autoAnimation={true}
        darkMode={darkMode}
        loadingTips={showTips}
      />
      
      {message && (
        <div style={{
          textAlign: 'center',
          color: darkMode ? '#e0e0e0' : '#666',
          fontSize: '14px',
          maxWidth: '300px'
        }}>
          {message}
        </div>
      )}
    </div>
  );
};

export default ChaessaemEmoji; 